<template>
    <div id="app">
        <router-view class="view"></router-view>
    </div>
</template>
<script>
export default {
  name: 'app',
  data () {
    return {}
  },
  methods: {
    readAuth (callback) {
      const that = this
      that.$ajax({
        method: 'get',
        url: 'admin/PowerList/getUserPowerList'
      }).then((res) => {
        const _data = res.data
        const auth = _data.data
        that.$store.commit('setAuth', auth)
        that.$store.commit('getAuth', that.$store.state.auth)
        that.$store.commit('getsubAuth', that.$store.state.auth)
        callback()
      }, (res) => {
        this.$message.warning('网络似乎出了点问题')
      })
    }
  },
  mounted () {
    // 判断登录状态
    let that = this
    if (document.cookie.includes('token=1')) {
      that.$message.warning('未登录或登录已过期')
      that.$router.push('/Login')
    } else if (!document.cookie.includes('token')) {
      that.$router.push('/Login')
    }
  }
}
</script>
<style>
    html,body,#app{margin:0;padding:0;height:100%;color:#333;}
    #app {
        margin: 0 auto;
        font-family: "微软雅黑",Arial,sans-serif;
        background:#eff2f7;
    }
    p{
        margin:0;
    }
</style>



