<template>
    <main id="transfer" class="container">
        <section>
            <header class="head-title">系统内转账</header>
            <p>
                钱包余额：
                <span class="money">{{amount | showNum(amount)}}</span>
                元
            </p>
            <p v-if="bindState">
                开户人：{{person}}
            </p>
            <p v-if="bindState">
                银行卡号：{{bank | hide(bank)}}
            </p>
            <p v-if="bindState">
                预留手机：{{mobile | hide(mobile)}}
            </p>
        </section>
        <section>
            <el-form :label-position="'top'"
                     label-width="80px"
                     :model="cashForm"
                     ref="cashForm">
                <el-form-item label="转账机构" class="item">
                    <el-select v-model="cashForm.orgId"
                               placeholder="请选择转账机构"
                               filterable>
                        <el-option-group v-for="group in orgList"
                                         :key="group.label"
                                         :label="group.label">
                            <el-option v-for="item in group.options"
                                       :label="item.label"
                                       :value="item.value"
                                       :key="item.value"></el-option>
                        </el-option-group>
                    </el-select>
                </el-form-item>
                <!--<el-form-item label="转账机构">-->
                    <!--<el-cascader  @keyup.enter="submitCash('cashForm')" :show-all-levels="false" :options="orgList" class="item"></el-cascader>-->
                <!--</el-form-item>-->
                <el-form-item label="转账金额">
                    <el-input-number @keyup.enter="submitCash('cashForm')"
                                     :min="1"
                                     :max="parseInt(amount)"
                                     v-model="cashForm.cashAmount"
                                     class="item"></el-input-number>
                    <p style="margin:0;">账户可转账金额：<span class="money">{{(amount-0).toFixed(0) | showNum(amount)}}</span>元</p>
                </el-form-item>
                <!--<el-form-item label="验证码">-->
                    <!--<el-input @keyup.enter="submitCash('cashForm')" :maxlength="6" v-model="cashForm.code" class="item">-->
                        <!--<el-button :style="{cursor:(codeBtn.isAjax?'not-allowed':'pointer')}" slot="append" @click="getCode()" :disabled="codeBtn.isAjax">{{codeBtn.text}}</el-button>-->
                    <!--</el-input>-->
                <!--</el-form-item>-->
                <el-button style="margin-bottom:20px;"
                           type="primary"
                           @click="submitCash('cashForm')">转 账</el-button>
            </el-form>
            <!--<div v-if="!bindState">-->
                <!--<p> <el-button @click="toBind">前往绑定银行卡</el-button></p>-->
                <!--<p class="tip">提示：绑定银行卡后可使用提现、转账功能</p>-->
            <!--</div>-->
        </section>
        <section class="tips">
            <header>转账须知</header>
            <p class="subtitle">申请转账何时到账？有哪些需要注意的？</p>
            <p>1、转账由棋协管理系统自动完成，可随时进行转账；</p>
            <p>2、转账将转入对方机构的系统钱包中。</p>
            <p class="subtitle">可以转账到银行卡上吗？</p>
            <p>不可以。为保障棋协管理系统中的交易安全，只允许机构使用提现功能将资金转出到指定的银行卡上。</p>
            <p class="subtitle">转账金额和次数有限制吗？</p>
            <p>转账金额和次数均无限制。</p>
        </section>
    </main>
</template>
<script>
export default{
  data () {
    return {
      bindState: false,
      amount: 0,
      person: '淘气猫',
      bank: '',
      mobile: '',
      cashForm: {
        orgId: '',
        cashAmount: '',
        code: '000000'
      },
      codeBtn: {
        text: '获取验证码',
        isAjax: false
      },
      submitBtn: false,
      orgList: []
    }
  },
  filters: {
    showNum (value) {
      return value.toString().split('').reverse().join('').replace(/(\d{3})/g, '$1,').replace(/,$/, '').split('').reverse().join('')
    },
    hide (value) {
      return value.substring(0, 3) + '******' + value.substring(value.length - 2, value.length)
    }
  },
  methods: {
    toBind () {
      this.$parent.addTab('绑定/修改')
      this.$router.push('/Main/WCreate')
    },
    submitCash (formName) {
      const that = this
      that.submitBtn = !that.submitBtn
      const __url = that.$store.state.total.wallet.child.transfer[1]
      if (__url === '') return false
      if (that.cashForm.orgId === '') {
        that.$message.warning('请选择转账机构')
        return false
      }
      if (that.cashForm.cashAmount < 0.01) {
        that.$message.warning('提现金额不能低于0.01元')
        that.submitBtn = !that.submitBtn
        return false
      }
      if (that.cashForm.code === '') {
        that.$message.warning('验证码不能为空')
        that.submitBtn = !that.submitBtn
        return false
      }
      that.$ajax({
        url: __url,
        method: 'post',
        data: {
          money: that.cashForm.cashAmount,
          transfer_to: that.cashForm.orgId,
          code: that.cashForm.code
        }
      }).then(res => {
        that.submitBtn = !that.submitBtn
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.amount = parseFloat((that.amount - that.cashForm.cashAmount).toFixed(2))
          that.submitBtn = !this.submitBtn
          that.$notify.success({
            title: '成功转账',
            message: '转账 ' + that.cashForm.cashAmount + ' 元给 ' + that.getOrgName(that.cashForm.orgId),
            offset: 80
          })
          that.cashForm = {
            orgId: '',
            cashAmount: '',
            code: '000000'
          }
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        const that = this
        that.submitBtn = !that.submitBtn
      })
    },
    getOrgName (orgId) {
      let name = ''
      this.orgList.forEach((e, i) => {
        e.options.forEach((e, i) => {
          if (e.value === orgId) {
            name = e.label
            return false
          }
        })
      })
      return name
    }
  },
  mounted () {
    const that = this
    const __url = that.$store.state.total.wallet.child.info[1]
    if (__url === '') return false
    // 获取绑定信息
    that.$ajax({
      url: __url
    }).then(res => {
      const _data = res.data
      if ((_data.errcode >> 0) === 0) {
        const dat = _data.data
        that.amount = dat.balance === null ? 0 : (dat.balance - 0).toFixed(2)
        if (dat.bank_card === null || dat.bank_name === null || dat.phone === null) {
          that.bindState = false
        } else {
          that.bindState = true
          that.bank = dat.bank_card
          that.person = dat.bank_name
          that.mobile = dat.phone
        }
      } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(_data.msg)
      }
    }, res => {
      that.$message.warning('网络似乎出了点问题')
    })
    that.orgList = [{
      label: '一级',
      options: []
    }, {
      label: '二级',
      options: []
    }, {
      label: '三级',
      options: []
    }, {
      label: '四级',
      options: []
    }]
    that.$ajax({
      url: that.$store.state.total.org.child.parentOrgList[1],
      params: {
        type: 'all',
        fields: 'id,org_name,org_level'
      }
    }).then(res => {
      const _data = res.data
      if ((_data.errcode >> 0) === 0) {
        const dat = _data.data
        dat.forEach((e, i) => {
          if (e.org_level === '1') {
            that.orgList[0].options.push({
              label: e.org_name,
              value: e.id
            })
          } else if (e.org_level === '2') {
            that.orgList[1].options.push({
              label: e.org_name,
              value: e.id
            })
          } else if (e.org_level === '3') {
            that.orgList[2].options.push({
              label: e.org_name,
              value: e.id
            })
          } else if (e.org_level === '4') {
            that.orgList[3].options.push({
              label: e.org_name,
              value: e.id
            })
          }
        })
      } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(_data.msg)
      }
    }, res => {
      that.$message.warning('网络似乎出了点问题')
    })
  }
}
</script>
<style>
    #transfer{
        overflow:auto;
    }
    #transfer section .money{
        color:#FF4949;
    }
    #transfer section{
        color:#99a9bf;
        padding:20px 20px 0 20px;
        overflow: hidden;
        margin:2px 2px 20px 2px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #transfer section p{
        text-align:left;
        margin:10px 0;
    }
    #transfer section p:last-of-type{
        margin-bottom:20px;
    }
    #transfer .code{
        cursor:pointer;
    }
    #transfer .item{
        width:300px;
    }
    #transfer .tips{
        padding-top:0;
        font-size:14px;
    }
    #transfer .tips header{
        border-left:8px solid #ff4949;
        background: #c0ccda;
        margin:0 -20px;
        padding:10px 12px;
        color:#fff;
        font-size:18px;
    }
    #transfer label,#transfer .tips .subtitle{
        color:#1F2D3D;
        font-size:16px;
    }
    #transfer .tip{
        font-size:12px;
    }
</style>



