<!-- 添加会员 -->
<template>
    <main id="addPlayer" class="container">
        <!-- upload cut img-->
        <my-upload field="file"
                   @crop-success="cropSuccess"
                   @crop-upload-success="cropUploadSuccess"
                   @crop-upload-fail="cropUploadFail"
                   v-model="uploadImgShow"
                   :width="750"
                   :height="1050"
                   :url="this.$store.state.baseURL+this.$store.state.uploadImg"
                   :headers="{token:$parent.getToken()}"
                   :no-circle="true"
                   :img-format="'jpg'"></my-upload>
        <div class="addMem">
            <header class="head-title">
                <p>
                    <span class="fa fa-edit"></span>
                    <span>注册棋手</span>
                </p>
            </header>
            <div style="margin-right:20px;text-align:right">
                <div class="refresh">
                    <i class="fa fa-refresh" @click="getData"></i>
                </div>
                <el-button type="danger"
                           :disabled="!multipleSelection.length"
                           @click="deleteData">批量删除</el-button>
                <el-button type="primary"
                           @click="toImport"
                           v-if="$store.state.total.mem.child.memAdd[1]">批量导入</el-button>
                <el-button type="primary"
                           @click="addInfo"
                           v-if="$store.state.total.mem.child.memAdd[1]">单个添加</el-button>
                <el-button type="success"
                           style="float:left"
                           :disabled="!multipleSelection.length"
                           @click="payFor"
                           v-if="$store.state.total.mem.child.memPay[1]">缴费注册</el-button>
            </div>
            <p class="money">
                总计：<span class="text-red">{{money.toFixed(2)}} </span>元 <span class="tip">（注册费：{{signFee}}元/人）</span>
            </p>
            <p class="tip">
                提示：成功缴纳注册费之后的会员方可完成注册，未完成注册的会员会一直显示在该列表中。
            </p>
            <section class="add-area">
                <el-table ref="multipleTable"
                          :data="allMemberInfo"
                          stripe :loading="isAjax"
                          @selection-change="handleSelectionChange">
                    <el-table-column type="selection"
                                     width="55"
                                     fixed></el-table-column>
                    <el-table-column prop="name"
                                     label="姓名"
                                     width="120"
                                     fixed>
                        <template slot-scope="scope">
                            {{scope.row.name}}（{{scope.row.rank}}）
                        </template>
                    </el-table-column>
                    <el-table-column prop="certNo"
                                     label="注册证号"
                                     width="100"
                                     fixed></el-table-column>
                    <el-table-column label="证件类型"
                                     width="110">
                        <template slot-scope=" scope">
                            {{getCardTypeName(scope.row.cardType)}}
                        </template>
                    </el-table-column>
                    <el-table-column label="证件号"
                                     width="170">
                        <template slot-scope=" scope">{{ scope.row.ID }}</template>
                    </el-table-column>
                    <el-table-column prop="isFee"
                                     label="费用（元/人）"
                                     min-width="120"></el-table-column>
                    <el-table-column prop="time"
                                     label="添加时间"
                                     min-width="160"></el-table-column>
                    <el-table-column label="操作"
                                     min-width="160">
                        <template slot-scope=" scope">
                            <el-button type="text"
                                       @click="watch(scope.row,scope.row.id,scope.row.time)">编辑</el-button>
                                <el-button type="text"
                                           class="text-red" @click="delShow(scope.row.id,scope.row.name)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <p class="tip">为了保证棋手信息的安全性与真实性，添加棋手后的24小时内，允许修改；超出24小时不可修改。未注册棋手可以删除后重新录入。</p>
                <el-pagination class="text-center"
                               @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[20, 100,200,300,400,500]"
                               :page-size="pageSize"
                               layout="total,sizes, prev, pager, next, jumper"
                               :total="totalNum">
                </el-pagination>
            </section>
        </div>
        <!--编辑-->
        <el-dialog class="mem-dialog"
                   title="填写信息"
                   :visible.sync="dialogFormVisible"
                   :close-on-click-modal="false"
                   @close="reset('memberInfo')">
            <el-form label-position="top"
                     :model="memberInfo"
                     ref="memberInfo"
                     :rules="checkRules"
                     class="mem-form">
                <el-form-item label="姓名"
                              prop="name"
                              label-width="120px">
                    <el-input class="form-input"
                              v-model="memberInfo.name"
                              :maxlength="30"
                              auto-complete="off"
                              placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="证件类型"
                              prop="cardType"
                              label-width="120px">
                    <el-select class="form-input"
                               :disabled="isEdit"
                               v-model="memberInfo.cardType"
                               placeholder="请选择证件类型">
                        <el-option
                                v-for="item in cardTypeOptions"
                                :key="item.value"
                                :value="item.value"
                                :label="item.label"
                                ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="证件号"
                              prop="ID"
                              label-width="120px">
                    <el-input class="form-input"
                              :disabled="isEdit"
                              :maxlength="18"
                              v-model="memberInfo.ID"
                              auto-complete="off"
                              placeholder="请输入证件号"></el-input>
                    <p v-if="otherParent"
                       style="position:absolute;top:0;left:310px">
                        是否申请转会？
                        <el-button type="text"
                                   @click="changeParent(memberInfo.ID)">
                            申请转会
                        </el-button>
                    </p>
                    <p v-if="isApply"
                       style="position:absolute;top:0;left:310px">
                        已申请转会
                        <el-button type="text"
                                   @click="watchChange">
                            去查看
                        </el-button>
                    </p>
                </el-form-item>
                <el-form-item label="段级位（无等级的选手可选择26级、27级、28级）"
                              prop="rank"
                              label-width="120px">
                    <el-select class="form-input"
                               filterable
                               v-model="memberInfo.rank"
                               placeholder="请选择棋手段级位">
                        <el-option-group v-for="group in optionGroups"
                                         :key="group.label"
                                         :label="group.label">
                            <el-option v-for="item in group.options"
                                       :label="item.label"
                                       :value="item.value"
                                       :key="item.value"></el-option>
                        </el-option-group>
                    </el-select>
                </el-form-item>
                <el-form-item label="是否需要缴纳注册费"
                              prop="isFee"
                              label-width="120px">
                    <el-radio class="radio"
                              v-model="memberInfo.isFee"
                              label="1">是</el-radio>
                    <el-radio class="radio"
                              v-model="memberInfo.isFee"
                              label="0">否</el-radio>
                </el-form-item>
                <el-form-item label="性别"
                              prop="gender"
                              label-width="120px">
                    <el-radio class="radio"
                              v-model="memberInfo.gender" label="0">男</el-radio>
                    <el-radio class="radio"
                              v-model="memberInfo.gender" label="1">女</el-radio>
                </el-form-item>
                <el-form-item label="出生日期"
                              prop="birthday"
                              label-width="120px">
                    <el-date-picker
                            v-model="memberInfo.birthday"
                            type="date"
                            :picker-options="pickerOptions"
                            placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="上传证件照（请上传白底一寸照）" prop="pic" label-width="120px">
                    <p>
                        <el-button type="success"
                                   size="small"
                                   @click="toggleShow">
                            {{memberInfo.imgDataUrl ? '点此修改一寸照' : '点此上传一寸照'}}
                        </el-button>
                    </p>
                    <img style="width:120px"
                         v-show="uploadImgSuccess || memberInfo.imgDataUrl"
                         :src="memberInfo.imgDataUrl">
                </el-form-item>
                <el-form-item>
                    <!--<el-button @click="reset('memberInfo')">重 置</el-button>-->
                    <el-button type="primary"
                               @click="confirmAdd('memberInfo')">确 认</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!--支付-->
        <el-dialog title="选择支付方式"
                   width="30%"
                   :visible.sync="payVis">
            <div class="type-area">
                <div class="ali"
                     @click="$parent.Pay('ali',orderID,'/Main/addPlayer?pay')">
                    <img src="./../../assets/img/alipay.png" alt="ali">
                    <p>支付宝</p>
                </div>
                <div class="wechat"
                     @click="$parent.Pay('wx',orderID,'/Main/addPlayer?pay')">
                    <img src="./../../assets/img/wechat.png" alt="wechat">
                    <p>微信</p>
                </div>
                <div class="wallet"
                     @click=""
                     v-if="0">
                    <img src="./../../assets/img/walletpay.png" alt="wallet">
                    <p>钱包余额</p>
                </div>
            </div>
            <p class="tip">提示：支付注册费后，即可完成会员的注册。</p>
        </el-dialog>
        <!-- 批量删除 -->
        <el-dialog title="确认删除"
                   width="30%"
                   :visible.sync="deleteDataVis">
            <p>是否确认删除选中的未注册会员？</p>
            <p class="tip" style="text-align:left">一旦删除无法恢复；需重新添加。</p>
            <div style="margin-top:10px;text-align:center">
                <el-button size="small"
                           @click="deleteDataVis = false">取 消</el-button>
                <el-button type="primary"
                           size="small"
                           @click="confirmDeleteData">确 认</el-button>
            </div>
        </el-dialog>
        <!-- 单个删除 -->
        <el-dialog title="确认删除"
                   width="30%"
                   :visible.sync="isDel">
            <p>是否删除会员 <span class="text-blue">{{delName}}</span>？</p>
            <p class="tip"  style="text-align:left">一旦删除无法恢复；需重新添加。</p>
            <div style="margin-top:10px;text-align:center">
                <el-button size="small"
                           @click="isDel = false">取 消</el-button>
                <el-button type="primary"
                           size="small"
                           @click="confirmDel">确 认</el-button>
            </div>
        </el-dialog>
    </main>
</template>
<script>
import cutImg from 'vue-image-crop-upload'
export default{
  data () {
    const that = this
    const _time = (that.$store.state.sysTime)
    let checkID = (rule, value, callback) => {
      value = value.replace(/\s/g, '')
      that.memberInfo.ID = value
      if (value === '') {
        callback(new Error('请输入证件号'))
      } else {
        let res = []
        if (that.memberInfo.cardType === 1) {
          res = this.$parent.checkID(value)
        } else {
          res[0] = true
        }
        if (!res[0]) {
          callback(new Error(res[1]))
        } else {
          const that = this
          const __url = that.$store.state.total.mem.child.memCheck[1]
          if (__url === '') return false
          let res = ''
          that.$ajax({
            url: __url,
            params: {
              idCard: value,
              type: that.memberInfo.cardType
            }
          }).then(res => {
            let _data = res.data
            if ((_data.errcode >> 0) === 0) {
              that.getAge(value)
              callback()
            } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
              that.$parent.overTime()
            } else {
              if ((_data.errcode >> 0) === 1401) {
                that.isApply = true
              }
              if ((_data.errcode >> 0) === 1402) {
                that.otherParent = true
              }
              if (!that.isEdit) {
                callback(new Error(_data.msg))
                that.getAge(value)
              } else {
                callback()
                that.getAge(value)
              }
            }
          }, res => {
            that.$message.warning('网络开小差了')
          })
          return res
        }
      }
    }
    let checkRank = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请选择段级位'))
      } else {
        callback()
      }
    }
    return {
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        }
      },
      uploadImgShow: false,
      payVis: false,
      isAjax: false,
      isEdit: false,
      tinyAjax: false,
      dialogFormVisible: false,
      otherParent: false,
      isApply: false,
      picURL: '',
      sameID: false,
      multipleSelection: [],
      totalNum: 0,
      currentPage: 1,
      pageSize: 20,
      money: 0,
      signFee: 0,
      checkRules: {
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'}
        ],
        cardType: [
          {required: true, message: '请选择证件类型', trigger: 'blur', type: 'number'}
        ],
        ID: [
          {required: true, validator: checkID, trigger: 'blur'}
        ],
        rank: [
          {required: true, validator: checkRank, trigger: 'change'}
        ],
        isFee: [
          {required: true, message: '请选择是否需要缴纳注册费', trigger: 'change'}
        ],
        gender: [
          {required: true, message: '请选择性别', trigger: 'change'}
        ],
        birthday: [
          {required: true, message: '请选择出生日期', trigger: 'change', type: 'date'}
        ]
      },
      allMemberInfo: [],
      memberInfo: {
        cardType: 1,
        name: '',
        ID: '',
        rank: 143,
        isFee: '1',
        pic: '',
        birthday: '',
        gender: '1',
        imgDataUrl: '' // // the datebase64 url of created image
      },
      cardTypeOptions: [
        {value: 1, label: '身份证'},
        {value: 2, label: '护照'},
        {value: 4, label: '台胞证'},
        {value: 8, label: '港澳通行证'}
      ],
      optionGroups: [
        {
          label: '级位',
          options: [
            {value: 143, label: '28级'},
            {value: 142, label: '27级'},
            {value: 141, label: '26级'},
            {value: 99, label: '25级'},
            {value: 103, label: '24级'},
            {value: 104, label: '23级'},
            {value: 106, label: '22级'},
            {value: 107, label: '21级'},
            {value: 108, label: '20级'},
            {value: 109, label: '19级'},
            {value: 110, label: '18级'},
            {value: 111, label: '17级'},
            {value: 112, label: '16级'},
            {value: 113, label: '15级'},
            {value: 114, label: '14级'},
            {value: 115, label: '13级'},
            {value: 116, label: '12级'},
            {value: 117, label: '11级'},
            {value: 118, label: '10级'},
            {value: 119, label: '9级'},
            {value: 120, label: '8级'},
            {value: 121, label: '7级'},
            {value: 122, label: '6级'},
            {value: 123, label: '5级'},
            {value: 124, label: '4级'},
            {value: 125, label: '3级'},
            {value: 126, label: '2级'},
            {value: 127, label: '1级'}
          ]
        },
        {
          label: '业余段位',
          options: [
            {value: 128, label: '1段'},
            {value: 129, label: '2段'},
            {value: 130, label: '3段'},
            {value: 131, label: '4段'},
            {value: 132, label: '5段'},
            {value: 133, label: '6段'}
          ]
        }
      ],
      deleteDataVis: false,
      uploadImgSuccess: false,
      isDel: false,
      delId: '',
      delName: ''
    }
  },
  components: {
    'my-upload': cutImg
  },
  methods: {
    delShow (id, name) {
      this.isDel = true
      this.delId = id
      this.delName = name
    },
    toggleShow () {
      this.uploadImgShow = !this.uploadImgShow
    },
    cropSuccess (imgDataUrl, field) {
      this.memberInfo.imgDataUrl = imgDataUrl
    },
    cropUploadSuccess (res, field) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        that.memberInfo.pic = res.data
        that.uploadImgSuccess = true
        that.uploadImgShow = false
      } else if ((res.errcode >> 0) === 1000) {
        that.$parent.overTime()
      }
    },
    cropUploadFail (status, field) {
      this.uploadImgSuccess = false
      this.$message.warning('网络似乎出了点问题')
    },
    deleteData () {
      this.deleteDataVis = true
    },
    confirmDel () {
      this.del(this.delId)
      this.isDel = false
    },
    confirmDeleteData () {
      const that = this
      that.multipleSelection.forEach((e, i) => {
        that.del(e.id, i)
      })
      that.deleteDataVis = false
    },
    changeParent (id) {
      const that = this
      that.$router.push('/Main/transferPlayer?' + id)
      that.$parent.addTab('转会申请')
    },
    watchChange () {
      const that = this
      that.$router.push('/Main/transferList?out')
      that.$parent.addTab('会员转会')
    },
    handleSizeChange (pageSize) {
      let that = this
      that.currentData = []
      that.pageSize = pageSize
      that.currentPage = 1
      that.getData()
    },
    handleCurrentChange (page) {
      let that = this
      that.currentPage = page
      that.getData()
    },
    toImport () {
      const that = this
      that.$router.push('/Main/importPlayer')
      that.$parent.addTab('批量导入棋手')
    },
    getRank (rank) {
      rank = rank >> 0
      this.optionGroups.forEach((e, i) => {
        e.options.forEach((e, i) => {
          if (e.value === rank) {
            rank = e.label
            return false
          }
        })
      })
      return rank
    },
    getAge (idCard) {
      const _time = (this.$store.state.sysTime)
      if (idCard.length === 15) {
        this.memberInfo.gender = (idCard[14] % 2 === 0) - 0 + ''
        let _year = '19' + idCard.substring(6, 8)
        this.memberInfo.birthday = new Date(_year.substring(0, 4), _year.substring(4, 6) - 1, _year.substring(6, 8))
      } else if (idCard.length === 18) {
        this.memberInfo.gender = (idCard[16] % 2 === 0) - 0 + ''
        let _year = idCard.substring(6, 14)
        this.memberInfo.birthday = new Date(_year.substring(0, 4), _year.substring(4, 6) - 1, _year.substring(6, 8))
      }
    },
    reset (formName) {
      this.$refs[formName].resetFields()
    },
    handleSelectionChange (val) {
      const that = this
      that.multipleSelection = val
      that.money = 0
      that.multipleSelection.forEach((e, i) => {
        that.money += (e.isFee - 0)
      })
    },
    savePicURL (res, file) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        that.picURL = URL.createObjectURL(file.raw)
        that.memberInfo.pic = res.data
      } else if ((res.errcode >> 0) === 1000) {
        that.$parent.overTime()
      }
    },
    addInfo () {
      this.dialogFormVisible = true
      this.isEdit = false
      this.memberInfo = {
        name: '',
        ID: '',
        rank: 143,
        isFee: '1',
        pic: '',
        birthday: '',
        gender: '0',
        cardType: 1,
        imgDataUrl: ''
      }
      this.$children[0].step = 1
    },
    removeSpace (str) {
      return str.replace(/\s/g, '')
    },
    checkFormID (id) {
      let that = this
      that.sameID = false
      that.allMemberInfo.forEach((e, i) => {
        if (e.ID === that.memberInfo.ID) {
          that.$message.warning('添加人员的身份证号重复，请检查')
          that.sameID = true
        }
      })
    },
    formatBirthDay (date, type) {
      if (typeof date !== 'string') return '--'
      if (type === 'chinese') return date.substring(0, 4) + '年' + date.substring(4, 6) + '月' + date.substring(6, 8) + '日'
      else return date.substring(0, 4) + type + date.substring(4, 6) + type + date.substring(6, 8)
    },
    watch (row, id, time) {
      const that = this
      that.isEdit = true
      const __url = that.$store.state.total.mem.child.memDetail[1]
      that.$children[0].step = 1
      that.$ajax({
        url: __url,
        params: {
          id: id
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data[0]
          that.memberInfo = {
            name: dat.idCardName.replace(/\s/g, ''),
            ID: dat.idCard.replace(/\s/g, ''),
            id: dat.id,
            rank: dat.level >> 0,
            isFee: dat.reg_pay,
            pic: dat.idcard_img,
            gender: dat.sex,
            cardType: dat.cardType >> 0,
            birthday: that.formatBirthDay(dat.birthday, '-'),
            imgDataUrl: dat.idcard_img === null ? '' : that.$store.state.imgURL + dat.idcard_img,
            row: row
          }
          that.getAge(that.memberInfo.ID)
          that.dialogFormVisible = true
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    edit () {
      const that = this
      const __url = that.$store.state.total.mem.child.memEdit[1]
      let __birthday = ''
      let __year = ''
      let __y = ''
      let __M = ''
      let __d = ''
      if (typeof that.memberInfo.birthday === 'string') {
        __birthday = that.memberInfo.birthday.replace(/[-]/g, '')
      } else {
        __year = that.memberInfo.birthday
        __y = __year.getFullYear() + ''
        __M = __year.getMonth() + 1
        __d = __year.getDate()
        __birthday = __y + (__M < 10 ? '0' + __M : __M) + (__d < 10 ? '0' + __d : __d)
      }
      that.$ajax({
        method: 'post',
        data: __url,
        params: {
          id: that.memberInfo.id,
          level: that.memberInfo.rank,
          idcard_img: that.memberInfo.pic,
          name: that.memberInfo.name,
          reg_pay: that.memberInfo.isFee,
          birthday: __birthday,
          sex: that.memberInfo.gender
        }
      }).then(res => {
        this.dialogFormVisible = false
        const _data = res.data
        if (_data.errcode === 0) {
          that.$message.success('修改成功')
          that.getData()
        } else if (_data.errcode === 1000) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        this.dialogFormVisible = false
        that.$message.warning('网络似乎出了点问题')
      })
    },
    add () {
      const that = this
      const __url = that.$store.state.total.mem.child.memAdd[1]
      const __year = that.memberInfo.birthday
      const __y = __year.getFullYear() + ''
      const __M = __year.getMonth() + 1
      const __d = __year.getDate()
      const __birthday = (__y + (__M < 10 ? '0' + __M : __M) + (__d < 10 ? '0' + __d : __d))
      if (__url === '') return false
      that.$ajax({
        url: __url,
        method: 'post',
        data: {
          idCard: that.removeSpace(that.memberInfo.ID).replace(/[（]/g, '(').replace(/[）]/g, ')').replace(/x/g, 'X'),
          type: that.memberInfo.cardType,
          sex: that.memberInfo.gender,
          name: that.removeSpace(that.memberInfo.name),
          level: that.memberInfo.rank,
          reg_pay: that.memberInfo.isFee,
          idcard_img: that.memberInfo.pic,
          birthday: __birthday
        }
      }).then(res => {
        this.dialogFormVisible = false
        let _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.$message.success('添加成功')
          that.dialogFormVisible = false
          that.getData()
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          this.$message.warning(_data.msg)
        }
      }, res => {
        this.dialogFormVisible = false
        this.$message.warning('网络开小差了')
      })
    },
    del (id, multi) {
      const that = this
      const __url = that.$store.state.total.mem.child.memDel[1]
      if (__url === '') return false
      that.tinyAjax = true
      that.$ajax({
        url: __url,
        params: {
          id: id
        }
      }).then(res => {
        that.tinyAjax = false
        const _data = res.data
        if (_data.errcode === 0) {
          // const index = that.findIndex(id);
          // that.allMemberInfo.splice(index,1);
          if (!multi || multi + 1 === that.multipleSelection.length) {
            that.$message.success('删除成功')
            that.getData()
          }
        } else if (_data.errcode === 1000) {
          that.$parent.overTime()
        } else {
          if (!multi || multi + 1 === that.multipleSelection.length) {
            that.$message.warning(_data.msg)
            that.getData()
          }
        }
      }, res => {
        that.tinyAjax = false
        that.$message.warning('网络似乎出了点问题')
      })
      return false
    },
    getData () {
      const that = this
      const __url = that.$store.state.total.mem.child.memNotReg[1]
      if (__url === '') return false
      that.isAjax = true
      that.$ajax({
        url: __url,
        params: {
          pageSize: that.pageSize,
          page: that.currentPage
        }
      }).then(res => {
        const _data = res.data
        that.isAjax = false
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data
          that.signFee = (_data.reg_price - 0).toFixed(2)
          that.allMemberInfo = []
          dat.forEach((e, i) => {
            that.allMemberInfo.push({
              id: e.id,
              ID: e.idCard,
              name: e.idCardName,
              rank: that.getRank(e.level),
              isFee: e.reg_pay === '1' ? that.signFee : 0 ,
              pic: e.idcard_img,
              time: that.formatDate(e.create_time),
              del: false,
              certNo: e.book_no === null ? '暂无' : e.book_no,
              cardType: e.card_type >> 0
            })
          })
          this.totalNum = _data.total
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          this.$message.warning(_data.msg)
        }
      }, res => {
        that.isAjax = false
        this.$message.warning('网络开小差了')
      })
    },
    findIndex (id) {
      let a = ''
      this.allMemberInfo.forEach((e, i) => {
        if (e.id === id) {
          a = i
          return false
        }
      })
      return a
    },
    formatDate (t) {
      const _t = new Date(t)
      const f = (a) => {
        return a < 10 ? '0' + a : a
      }
      return _t.getFullYear() + '-' + f(_t.getMonth() + 1) + '-' + f(_t.getDate()) + ' ' + f(_t.getHours()) + ': ' + f(_t.getMinutes())
    },
    confirmAdd (formName) {
      const that = this
      if (that.isEdit) {
        that.edit()
      } else {
        that.$refs[formName].validate((valid) => {
          if (valid) {
            that.add()
          }
        })
      }
    },
    getCardTypeName (num) {
      switch (num >> 0) {
        case 1:
          return '身份证'
        case 2:
          return '护照'
        case 4:
          return '台胞证'
        case 8:
          return '港澳通行证'
      }
    },
    payFor () {
      const that = this
      const __url = that.$store.state.total.mem.child.memPay[1]
      if (__url === '') return false
      let idArr = []
      that.multipleSelection.forEach((e, i) => {
        idArr.push(e.id)
      })
      if (idArr.join(',').length > 2000) {
        that.$notify.info({
          title: '提示',
          message: '超出每次提交人数上限，请减少所选人数'
        })
        return false
      }
      that.$ajax({
        url: __url,
        method: 'post',
        data: {
          ids: idArr.join(','),
          cost:(that.money - 0)
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          if (typeof _data.data === 'string') {
            that.orderID = _data.data
            that.payVis = true
          } else {
            const num = _data.data.num
            that.$notify.success('成功注册' + num + '人')
            that.getData()
          }
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    }
  },
  mounted () {
    const that = this
    that.getData()
    if (window.location.href.includes('?pay')) {
      that.$notify.success({
        title: '支付成功',
        message: '添加会员成功',
        offset: 80
      })
    }
  }
}
</script>
<style>
    #addPlayer{
        position:relative;
    }
    #addPlayer .text-red{
        color:#ff4949;
        margin-left:16px;
    }
    #addPlayer .text-red:hover{
        opacity:0.7;
    }
    #addPlayer .error{
        color:#ff4949;
        font-size:16px;
        cursor:pointer;
    }
    #addPlayer .error:hover{
        opacity:0.7;
    }
    #addPlayer .tip{
        color:#8492A6;
        font-size:12px;
        margin:4px 0;
        text-align:right;
    }
    #addPlayer .el-table th>.cell,#addPlayer .el-table .cell{
        padding:0 4px;
    }
    #addPlayer .upload-img{
        width:100%;
        height:100%;
    }
    #addPlayer .addMem{
        background:#fff;
        overflow: hidden;
        padding:20px;
    }
    #addPlayer .addMem .head-title p{
        margin:10px 0;
    }
    #addPlayer .add-area{
        margin-bottom:20px;
        position: relative;
    }
    #addPlayer .add-panel{
        padding:15px;
        cursor:pointer;
        border-bottom:1px solid #d3dce6;
    }
    #addPlayer .add-panel:last-of-type{
        border-bottom:0;
    }
    #addPlayer .add-panel .plus{
        display: inline-block;
        width:48px;
        height:48px;
        font-size:68px;
        line-height:38px;
        color:#fff;
        background: #999;
        margin-right:18px;
    }
    #addPlayer .add-panel .mem-info{
        position: relative;
        top:-16px;
        color:#999;
    }
    #addPlayer .money{
        position: absolute;
        font-size:18px;
    }
    #addPlayer .mem-dialog{
        min-width:500px;
    }
    #addPlayer .mem-dialog .mem-form .form-input{
        width:300px;
    }
    #addPlayer .refresh{
        display:inline-block;
        margin:10px 0;
    }
    #addPlayer .refresh .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #addPlayer .text-blue{
        color:#20a0ff;
        font-size:16px;
    }
    #addPlayer .refresh .fa-refresh:hover{
        opacity:0.7;
    }
    #addPlayer .type-area{
        text-align:center;
    }
    #addPlayer .type-area .wechat,#addPlayer .type-area .ali,#addPlayer .type-area .wallet{
        margin:0 20px 20px 20px;
        cursor:pointer;
        display:inline-block;
    }
</style>



