<!-- 棋手页面 -->
<template>
    <main id="player" class="container">
        <!-- upload cut img-->
        <my-upload field="file"
                   ref="upload"
                   @crop-success="cropSuccess"
                   @crop-upload-success="cropUploadSuccess"
                   @crop-upload-fail="cropUploadFail"
                   v-model="uploadImgShow"
                   :width="500"
                   :height="700"
                   :url="this.$store.state.baseURL+this.$store.state.uploadImg"
                   :headers="{token:$parent.getToken()}"
                   :no-circle="true"
                   :img-format="'jpg'"></my-upload>
        <section class="search-area" @keyup.enter="search">
            <el-input class="search-group"
                      v-model="input"
                      placeholder="请输入证件号或姓名或隶属机构">
                <el-select style="width: 120px;"
                           v-model="select"
                           slot="prepend"
                           placeholder="全部"
                           @change="search">
                    <el-option label="全部" value="all"></el-option>
                    <el-option label="直属棋手" value="myself"></el-option>
                </el-select>
                <el-button slot="append"
                           icon="search"
                           @click="search"
                           :disabled="isSearch"
                           :loading="isSearch">查询</el-button>
            </el-input>
            <el-button class="add-new"
                       type="success"
                       @click="addNewPlayer($event)"
                       v-if="$store.state.total.mem.child.memAdd[1]">棋手注册</el-button>
        </section>
        <section class="player-list">
            <div>
                <label>棋手总人数：
                    <label class="player-num">{{totalNum | showNum(totalNum)}}</label>
                </label>
                <span class="money" v-show="multipleSelection.length!==0">
                    <span class="text-red">{{(money).toFixed(2)}} </span>元
                    <span class="tip">（年费：{{fee}}元/人）</span>
                </span>
                <div style="float:right;display:inline-block">
                    <span class="tip">提示：禁赛的棋手将显示在列表的最后。</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <i class="fa fa-refresh" @click="getData"></i>
                    <!--<el-button type="primary"-->
                               <!--size="small"-->
                               <!--:disabled="multipleSelection.length === 0"-->
                               <!--v-if="$store.state.total.mem.child.memAllot[1]"-->
                               <!--@click="allotVis = !allotVis;">棋手分配</el-button>-->
                    <el-button type="primary"
                               size="small"
                               :disabled="multipleSelection.length === 0"
                               v-if="$store.state.total.mem.child.memYear[1]"
                               @click="yearCheck">缴纳年费</el-button>
                </div>
            </div>
            <!-- 列表 -->
            <el-table ref="multipleTable"
                      :data="currentData"
                      size="mini"
                      stripe
                      @selection-change="handleSelectionChange"
                      :loading="$store.state.tableLoad">
                <el-table-column type="selection" min-width="30"></el-table-column>
                <el-table-column label="基本信息" min-width="170">
                    <template slot-scope=" scope">
                        <!--<p>吴甲甲昊甲昊（女 24段）</p>-->
                        <p>{{scope.row.name}}（{{scope.row.gender + ' ' + scope.row.rank}}）</p>
                    </template>
                </el-table-column>
                <el-table-column label="注册证号" min-width="100">
                    <template slot-scope=" scope">
                        <p>{{scope.row.certNo}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="证件号" min-width="170">
                    <template slot-scope=" scope">
                        <p>{{scope.row.number}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="隶属机构" min-width="170">
                    <template slot-scope=" scope">
                        <p>{{scope.row.org}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="注册时间"  min-width="100">
                    <template slot-scope=" scope">
                        <p>{{scope.row.signTime}}</p>
                        <p></p>
                    </template>
                </el-table-column>
                <el-table-column label="到期时间"  min-width="100">
                    <template slot-scope=" scope">
                        <p>{{scope.row.overTime}}</p>
                        <p></p>
                    </template>
                </el-table-column>
                <el-table-column prop="isOver" label="状态"  min-width="80">
                    <template slot-scope=" scope">
                        <el-tag :type="scope.row.isOver[0]"
                                size="small">{{scope.row.isOver[1]}}</el-tag>
                        <el-tag type="danger"
                                size="small"
                                v-show="scope.row.state===1">已禁赛</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" min-width="90">
                    <template slot-scope=" scope">
                        <el-button type="text"
                                   @click="watch(scope.row,scope.row.id)"
                                   size="mini"
                                   v-if="$store.state.total.mem.child.memEdit[1]">编辑</el-button>
                        <el-button type="text"
                                   size="mini"
                                   :class="{'text-warning':scope.row.state===2,'text-success':scope.row.state===1}"
                                   @click="freeze(scope.row,scope.row.id,scope.row.state)"
                                   v-if="$store.state.total.mem.child.memFreeze[1]">{{scope.row.state===1?'恢复': '禁赛'}}</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentPage"
                           :page-sizes="[20, 50, 100,300,500]"
                           :page-size="pageSize"
                           layout="total,sizes, prev, pager, next, jumper"
                           :total="totalNum">
            </el-pagination>
        </section>
        <!--编辑-->
        <el-dialog class="mem-dialog"
                   title="填写信息"
                   :visible.sync="dialogFormVisible"
                   :close-on-click-modal="false">
            <el-form label-position="top"
                     :model="memberInfo"
                     ref="memberInfo"
                     :rules="checkRules"
                     class="mem-form">
                <el-form-item label="姓名"
                              prop="name"
                              label-width="120px">
                    <el-input class="form-input"
                              v-model="memberInfo.name"
                              :maxlength="30"
                              auto-complete="off"
                              placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="证件类型" prop="cardType" label-width="120px">
                    <el-select class="form-input"
                               disabled
                               v-model="memberInfo.cardType"
                               placeholder="请选择证件类型">
                        <el-option
                                v-for="item in cardTypeOptions"
                                :key="item.value"
                                :value="item.value"
                                :label="item.label"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="证件号"
                              prop="ID"
                              label-width="120px">
                    <el-input class="form-input"
                              :maxlength="18"
                              v-model="memberInfo.ID"
                              disabled
                              auto-complete="off"
                              placeholder="请输入证件号"></el-input>
                </el-form-item>
                <el-form-item label="段级位（无等级的选手可选择26级、27级、28级）"
                              prop="rank"
                              label-width="120px">
                    <el-select class="form-input"
                               filterable
                               v-model="memberInfo.rank"
                               placeholder="请选择棋手段级位">
                        <el-option-group v-for="group in optionGroups"
                                         :key="group.label"
                                         :label="group.label">
                            <el-option v-for="item in group.options"
                                       :label="item.label"
                                       :value="item.value"
                                       :key="item.value"></el-option>
                        </el-option-group>
                    </el-select>
                </el-form-item>
                <el-form-item label="性别"
                              prop="gender"
                              label-width="120px">
                    <el-radio class="radio"
                              v-model="memberInfo.gender"
                              disabled
                              label="0">男</el-radio>
                    <el-radio class="radio"
                              v-model="memberInfo.gender"
                              disabled
                              label="1">女</el-radio>
                </el-form-item>
                <el-form-item label="出生日期"
                              prop="birthday"
                              label-width="120px">
                    <el-date-picker
                            v-model="memberInfo.birthday"
                            disabled
                            type="date"
                            placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="上传证件照（请上传白底一寸照）" prop="pic" label-width="120px">
                    <p>
                        <el-button type="success"
                                   size="small"
                                   @click="toggleShow">
                            {{memberInfo.imgDataUrl ? '点此修改一寸照' : '点此上传一寸照'}}
                        </el-button>
                    </p>
                    <img style="width: 120px"
                         v-show="uploadImgSuccess || memberInfo.imgDataUrl"
                         :src="memberInfo.imgDataUrl">
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"
                               @click="confirmEdit('memberInfo')">确认修改</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!-- 禁赛提示 -->
        <el-dialog width="30%"
                   class="mem-dialog"
                   title="禁赛/恢复"
                   :visible.sync="freezeVisible"
                   :close-on-click-modal="true">
            <p style="margin-bottom: 10px">禁赛后，会员将无法报名以及正常使用，可再次恢复。</p>
            <div class="text-center">
                <el-button @click="freezeVisible=false" size="small">取 消</el-button>
                <el-button @click="doFreeze(currentFreeze.id,currentFreeze.state)" size="small" type="primary">{{currentFreeze.state===2?'禁赛': '恢复'}}</el-button>
            </div>
        </el-dialog>
        <!--支付-->
        <el-dialog  width="30%"
                    title="选择支付方式"
                    :visible.sync="payVis">
            <div class="type-area">
                <div class="ali" @click="$parent.Pay('ali',orderID,'/Main/player?pay')">
                    <img src="./../../assets/img/alipay.png" alt="ali">
                    <p>支付宝</p>
                </div>
                <div class="wechat" @click="$parent.Pay('wx',orderID,'/Main/player?pay')">
                    <img src="./../../assets/img/wechat.png" alt="wechat">
                    <p>微信</p>
                </div>
                <div class="wallet"
                     v-if="0">
                    <img src="./../../assets/img/walletpay.png" alt="wallet">
                    <p>钱包余额</p>
                </div>
            </div>
            <p class="tip">提示：支付注册费后，即可完成会员的注册。</p>
        </el-dialog>
        <!-- 24小时后查看信息界面 -->
        <el-dialog class="mem-dialog"
                   title="查看个人信息"
                   :visible.sync="dialogOutOfOneDay"
                   :close-on-click-modal="false">
            <div class="one-day-info">
                <p class="one-day-content">姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名：{{memberDetailInfo.name}}</p>
                <p class="one-day-content">性&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;别：{{memberDetailInfo.gender}}</p>
                <p class="one-day-content">出生日期：{{memberDetailInfo.birthday}}</p>
                <p class="one-day-content">证件类型：{{memberDetailInfo.cardType}}</p>
                <p class="one-day-content">证件号码：{{memberDetailInfo.idCard}}</p>
                <p class="one-day-content">隶属机构：{{memberDetailInfo.org}}</p>
                <p class="one-day-content">证书编号：{{memberDetailInfo.certNo}}</p>
                <div class="one-day-img-container">
                    <div class="one-day-img">
                        <img :src="$store.state.imgURL + memberDetailInfo.img" v-show="memberDetailInfo.img" alt="会员一寸照">
                        <img v-show="!memberDetailInfo.img"
                             src="./../../assets/img/no_head.png" alt="暂未上传一寸照">
                    </div>
                    <el-button type="success"
                               style="width: 120px"
                               size="small"
                               @click="toggleDetailShow">点此{{ memberDetailInfo.img=== '' ? '上传' : '修改' }}一寸照</el-button>
                </div>
            </div>
            <p v-show="!memberDetailInfo.certHistory.length" class="no-cert">暂无证书获奖纪录</p>
            <div class="one-day-cert">
                <div v-show="memberDetailInfo.certHistory.length"
                     class="year-card"
                     :key="yearsData"
                     v-for="yearsData in memberDetailInfo.certHistory">
                    <span class="year-title">{{yearsData.year}}</span>
                    <div class="each-card"
                         :key="details.datte"
                         v-for="details in yearsData.data">
                        <div class="date-title-out">
                            <div class="date-title">
                                <div class="date-title-in">
                                    <p class="month">{{details.date.substring(5,7)}}</p>
                                    <p class="date">{{details.date.substring(8,10)}}</p>
                                </div>
                            </div>
                        </div>
                        <div class="each-card-content">
                            <p>
                                在 <span class="text-blue">{{details.game}}</span> 中，取得 <span class="text-black">{{details.grade}}</span> 成绩，授予 <span class="text-black">{{details.reward}}</span> 称号
                            </p>
                            <p>
                                承办方：{{details.org}}
                            </p>
                            <p>
                                发证日期：{{details.date.substring(0,4)}}年{{details.date.substring(5,7)}}月{{details.date.substring(8,10)}}日
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </el-dialog>
        <!-- 棋手分配 -->
        <el-dialog title="棋手分配"
                   width="30%"
                   :visible.sync="allotVis">
            <p class="allot-item">将
                <span class="selected-name"
                      v-for="(item,index) in multipleSelection"
                      :key="item.id"
                      v-if="index < 5">{{item.name + (index >= 4 || index + 1 === multipleSelection.length ? '' : '、')}}</span>
                <span v-if="multipleSelection.length > 5">等</span>
                <span v-if="multipleSelection.length <= 5">，共</span>
                <span class="selected-name">{{multipleSelection.length}}</span>
                名棋手分配至机构：
            </p>
            <el-select class="text-center"
                       v-model="allotOrgId"
                       placeholder="请选择机构"
                       filterable>
                <el-option-group v-for="group in orgList"
                                 :key="group.label"
                                 :label="group.label">
                    <el-option v-for="item in group.options"
                               :label="item.label"
                               :value="item.value"
                               :key="item.value"></el-option>
                </el-option-group>
            </el-select>
            <p class="tip text-left">提示：可输入机构名称查询；请确认名单后再操作。</p>
            <p class="allot-item text-center">
                <el-button size="small" @click="allotVis = !allotVis">取消</el-button>
                <el-button size="small" type="primary" @click="confirmAllot">确认</el-button>
            </p>
        </el-dialog>
    </main>
</template>
<script>
import cutImg from 'vue-image-crop-upload'
export default{
  data () {
    const that = this
    let hei = 0
    let win = window.outerWidth
    if (win > 1366) {
      hei = 640
    } else if (win <= 1366 && win >= 1024) {
      hei = 450
    } else {
      hei = 300
    }
    let checkID = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入身份证号'))
      } else {
        let res = []
        if (that.memberInfo.cardType === 1) {
          res = that.$parent.checkID(value)
        } else {
          res[0] = true
        }
        if (!res[0]) {
          callback(new Error(res[1]))
        } else {
          that.getAge(that.memberInfo.ID)
          callback()
        }
      }
    }
    return {
      allotOrgId: '',
      orgList: [],
      cardTypeOptions: [
        {value: 1, label: '身份证'},
        {value: 2, label: '护照'},
        {value: 4, label: '台胞证'},
        {value: 8, label: '港澳通行证'}
      ],
      dialogOutOfOneDay: false,
      isSearch: false,
      dialogFormVisible: false,
      freezeVisible: false,
      input: '',
      select: 'all',
      currentPage: 1,
      pageSize: that.$parent.getCookie('playerPageSize') !== '' ? (that.$parent.getCookie('playerPageSize') >> 0) : 20,
      totalNum: 0,
      money: 0,
      fee: 0,
      orderID: '',
      allotVis: false,
      payVis: false,
      multipleSelection: [],
      systemTime: '',
      currentFreeze: {},
      currentData: [],
      tableHei: hei,
      memberInfo: {
        name: '',
        ID: '',
        rank: '',
        pic: '',
        birthday: '',
        gender: 1,
        imgDataUrl: '', // // the datebase64 url of created image
        cardType: 1
      },
      memberDetailInfo: {
        id: '',
        name: '张三三',
        gender: '男',
        birthday: '2017-10-30',
        cardType: '身份证',
        idCard: '330802201710304635',
        org: '浙江省围棋协会',
        certNo: '5710001002',
        img: '',
        certHistory: []
      },
      gotAge: '',
      gender: 1,
      checkRules: {
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'}
        ],
        ID: [
          {required: true, validator: checkID, trigger: 'blur'}
        ]
      },
      optionGroups: [
        {
          label: '级位',
          options: [
            {value: 143, label: '28级'},
            {value: 142, label: '27级'},
            {value: 141, label: '26级'},
            {value: 99, label: '25级'},
            {value: 103, label: '24级'},
            {value: 104, label: '23级'},
            {value: 106, label: '22级'},
            {value: 107, label: '21级'},
            {value: 108, label: '20级'},
            {value: 109, label: '19级'},
            {value: 110, label: '18级'},
            {value: 111, label: '17级'},
            {value: 112, label: '16级'},
            {value: 113, label: '15级'},
            {value: 114, label: '14级'},
            {value: 115, label: '13级'},
            {value: 116, label: '12级'},
            {value: 117, label: '11级'},
            {value: 118, label: '10级'},
            {value: 119, label: '9级'},
            {value: 120, label: '8级'},
            {value: 121, label: '7级'},
            {value: 122, label: '6级'},
            {value: 123, label: '5级'},
            {value: 124, label: '4级'},
            {value: 125, label: '3级'},
            {value: 126, label: '2级'},
            {value: 127, label: '1级'}
          ]
        },
        {
          label: '业余段位',
          options: [
            {value: 128, label: '1段'},
            {value: 129, label: '2段'},
            {value: 130, label: '3段'},
            {value: 131, label: '4段'},
            {value: 132, label: '5段'},
            {value: 133, label: '6段'}
          ]
        }
      ],
      outOfOneDay: false,
      uploadImgSuccess: false,
      uploadImgShow: false,
      uploadImgDetailShow: false
    }
  },
  filters: {
    showNum (value) {
      return value.toString().split('').reverse().join('').replace(/(\d{3})/g, '$1,').replace(/,$/, '').split('').reverse().join('')
    }
  },
  components: {
    'my-upload': cutImg
  },
  methods: {
    getOrgName (orgId) {
      let name = ''
      this.orgList.forEach((e, i) => {
        e.options.forEach((e, i) => {
          if (e.value === orgId) {
            name = e.label
            return false
          }
        })
      })
      return name
    },
    toggleShow () {
      this.uploadImgShow = !this.uploadImgShow
      this.uploadImgDetailShow = false
    },
    toggleDetailShow () {
      this.uploadImgShow = !this.uploadImgShow
      this.uploadImgDetailShow = true
    },
    cropSuccess (imgDataUrl, field) {
      if (this.uploadImgShow) this.memberInfo.imgDataUrl = imgDataUrl
    },
    cropUploadSuccess (res, field) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        if (that.uploadImgShow && that.uploadImgDetailShow) {
          that.onlyEditImg(res.data)
        }
        that.memberInfo.pic = res.data
        that.uploadImgSuccess = true
        that.uploadImgShow = false
      } else if ((res.errcode >> 0) === 1000) {
        that.$parent.overTime()
      } else {
        that.$message.warning(res.msg)
        that.uploadImgShow = false
        that.$refs.upload.step = 1
      }
    },
    onlyEditImg (imgSrc) {
      const that = this
      const __url = that.$store.state.total.mem.child.memEdit[1]
      if (__url === '') return false
      that.$ajax({
        method: 'post',
        url: __url,
        data: {
          id: that.memberDetailInfo.id,
          idcard_img: imgSrc
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.getData()
          that.$message.success('修改成功')
          that.memberDetailInfo.img = imgSrc
          that.$refs.upload.step = 1
          that.dialogFormVisible = false
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    cropUploadFail (status, field) {
      this.uploadImgSuccess = false
      this.$message.warning('网络似乎出了点问题')
    },
    handleSelectionChange (val) {
      const that = this
      that.multipleSelection = val
      that.money = 0
      that.multipleSelection.forEach((e, i) => {
        that.money += that.fee
      })
    },
    yearCheck () {
      const that = this
      const __url = that.$store.state.total.mem.child.memYear[1]
      if (__url === '') return false
      let idArr = []
      that.multipleSelection.forEach((e, i) => {
        idArr.push(e.id)
      })
      that.$ajax({
        url: __url,
        method: 'post',
        data: {
          ids: idArr.join(','),
          cost: that.money - 0
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          if (typeof _data.data === 'string') {
            that.orderID = _data.data
            if ((_data.state >> 0) === 0) {
              that.payVis = true
            } else {
              that.$notify.success({
                title: '会员续费成功',
                message: '本次续费为免费',
                offset: 80
              })
              that.getData()
            }
          } else {}
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    getAge (idCard) {
      if (idCard.length === 15) {
        this.gender = (idCard[14] % 2 === 0) - 0 + ''
        let _year = '19' + idCard.substring(6, 8)
        this.gotAge = ((new Date() - new Date(_year.substring(0, 4), _year.substring(4, 6) - 1, _year.substring(6, 8))) / 1000 / 60 / 60 / 24 / 365) >> 0
      } else if (idCard.length === 18) {
        this.gender = (idCard[16] % 2 === 0) - 0 + ''
        let _year = idCard.substring(6, 14)
        this.gotAge = ((new Date() - new Date(_year.substring(0, 4), _year.substring(4, 6) - 1, _year.substring(6, 8))) / 1000 / 60 / 60 / 24 / 365) >> 0
      }
    },
    getRank (rank) {
      rank = rank >> 0
      if (rank >= 141) {
        rank = '无等级'
      }
      this.optionGroups.forEach((e, i) => {
        e.options.forEach((e, i) => {
          if (e.value === rank) {
            rank = e.label
            return false
          }
        })
      })
      return rank
    },
    savePicURL (res, file) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        that.memberInfo.imgDataUrl = URL.createObjectURL(file.raw)
        that.memberInfo.pic = res.data
      } else if ((res.errcode >> 0) === 1000) {
        that.$parent.overTime()
      }
    },
    watch (row, id) {
      const that = this
      // 机构等级判断是临时处理
      if (that.$store.state.orgLevel === 1) {
        that.watchInOneDay(id)
        return false
      }
      that.checkOneDay(row.signTime)
      that.$refs.upload.step = 1
      if (!that.outOfOneDay) {
        that.watchInOneDay(id)
      } else {
        that.watchOutOfOneDay(id) // test id 7778
      }
    },
    checkOneDay (_time2) {
      const that = this
      const _time1 = that.$store.state.sysTime
      const y1 = _time1.substring(0, 4)
      const M1 = _time1.substring(5, 7) - 1
      const d1 = _time1.substring(8, 10)
      const h1 = _time1.substring(11, 13)
      const m1 = _time1.substring(14, 16)
      const s1 = _time1.substring(17, 19)
      const y2 = _time2.substring(0, 4)
      const M2 = _time2.substring(5, 7) - 1
      const d2 = _time2.substring(8, 10)
      const h2 = _time2.substring(11, 13)
      const m2 = _time2.substring(14, 16)
      const s2 = _time2.substring(17, 19)
      that.outOfOneDay = new Date(y1, M1, d1, h1, m1, s1) - new Date(y2, M2, d2, h2, m2, s2) > 864e5
    },
    watchInOneDay (id) {
      const that = this
      const __url = that.$store.state.total.mem.child.memDetail[1]
      if (__url === '') return false
      that.$ajax({
        url: __url,
        params: {
          id: id
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data[0]
          that.memberInfo = {
            name: dat.idCardName.replace(/\s/g, ''),
            ID: dat.idCard.replace(/\s/g, ''),
            id: dat.id,
            rank: dat.level >> 0,
            pic: dat.idcard_img === null ? '' : dat.idcard_img,
            cardType: dat.cardType >> 0,
            gender: dat.sex,
            birthday: that.formatBirthDay(dat.birthday, '-')
          }
          if (that.memberInfo.pic) {
            that.memberInfo.imgDataUrl = that.$store.state.imgURL + that.memberInfo.pic
          }
          if (that.outOfOneDay) {
            that.dialogOutOfOneDay = true
          } else {
            that.dialogFormVisible = true
          }
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    formatBirthDay (date, type) {
      if (typeof date !== 'string') return '--'
      if (type === 'chinese') return date.substring(0, 4) + '年' + date.substring(4, 6) + '月' + date.substring(6, 8) + '日'
      else return date.substring(0, 4) + type + date.substring(4, 6) + type + date.substring(6, 8)
    },
    watchOutOfOneDay (id) {
      const that = this
      const __url = that.$store.state.total.mem.child.memDetailAfter[1]
      if (__url === '') return false
      that.$ajax({
        url: __url,
        params: {
          id: id
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data
          that.memberDetailInfo = {
            name: dat.p_info.idCardName.replace(/\s/g, ''),
            idCard: dat.p_info.idCard.replace(/\s/g, ''),
            cardType: that.getCardTypeName(dat.p_info.cardType),
            birthday: that.formatBirthDay(dat.p_info.birthday, 'chinese'),
            org: dat.p_info.org_name,
            id: id,
            gender: dat.p_info.sex >> 0 === 0 ? '男' : '女',
            certNo: dat.p_info.book_no === null ? '暂无' : dat.p_info.book_no,
            img: dat.p_info.idcard_img === null ? '' : dat.p_info.idcard_img,
            certHistory: []
          }
          that.getCertData(dat.cert_list)
          if (that.outOfOneDay) {
            that.dialogOutOfOneDay = true
          } else {
            that.dialogFormVisible = true
          }
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    getCertData (list) {
      if (list.length === 0) return false
      const that = this
      // sort desc
      function desc (value1, value2) {
        if (value1 < value2) {
          return 1
        } else if (value1 > value2) {
          return -1
        } else {
          return 0
        }
      }
      // remove repeat years
      Array.prototype.unique = function () {
        this.sort()
        let res = [this[0]]
        for (let i = 1; i < this.length; i++) {
          if (this[i] !== res[res.length - 1]) {
            res.push(this[i])
          }
        }
        return res
      }
      let _arr = []
      list.forEach((e, i) => {
        _arr.push(e.create_time.substring(0, 4))
        _arr = _arr.unique().sort(desc)
      })
      _arr.forEach((e, i) => {
        that.memberDetailInfo.certHistory.push({year: e, data: []})
      })
      list.forEach((e, i) => {
        that.memberDetailInfo.certHistory.forEach((ele, ind) => {
          if (ele.year === e.create_time.substring(0, 4)) {
            ele.data.push({
              game: e.event_title,
              grade: e.ranking,
              reward: e.level_name,
              org: e.apply_org_name,
              date: e.create_time.substring(0, 10)
            })
          }
        })
      })
    },
    confirmAllot () {
      const that = this
      const __url = that.$store.state.total.mem.child.memAllot[1]
      if (__url === '') return false
      let idArr = []
      that.multipleSelection.forEach((e, i) => {
        idArr.push(e.id)
      })
      if (idArr.length === 0 || that.allotOrgId === '') return false
      that.$ajax({
        url: __url,
        params: {
          ids: idArr.join(','),
          org_id: that.allotOrgId
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.$message.success(_data.msg)
          that.getData()
          that.allotVis = false
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    confirmEdit (formName) {
      const that = this
      const __url = that.$store.state.total.mem.child.memEdit[1]
      if (__url === '') return false
      that.$refs[formName].validate((valid) => {
        if (valid) {
          that.$ajax({
            method: 'post',
            url: __url,
            data: {
              id: that.memberInfo.id,
              level: that.memberInfo.rank,
              idcard_img: that.memberInfo.pic,
              name: that.memberInfo.name
            }
          }).then(res => {
            const _data = res.data
            if ((_data.errcode >> 0) === 0) {
              that.getData()
              that.$message.success('修改成功')
              that.dialogFormVisible = false
            } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
              that.$parent.overTime()
            } else {
              that.$message.warning(_data.msg)
            }
          }, res => {
            that.$message.warning('网络似乎出了点问题')
          })
        }
      })
    },
    freeze (row, id, state) {
      const that = this
      that.currentFreeze = row
      that.freezeVisible = true
    },
    doFreeze (id, state) {
      const that = this
      const __url = that.$store.state.total.mem.child.memFreeze[1]
      if (__url === '') return false
      that.$ajax({
        url: __url,
        params: {
          id: id,
          state: state === 1 ? 2 : 1
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.$message.success('修改成功')
          that.dialogFormVisible = false
          that.currentFreeze.state = state === 1 ? 2 : 1
          that.freezeVisible = false
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    search () {
      const that = this
      that.currentPage = 1
      that.getData()
    },
    handleSizeChange (pageSize) {
      document.cookie = 'playerPageSize=' + pageSize
      let that = this
      that.currentData = []
      that.pageSize = pageSize
      that.currentPage = 1
      that.getData()
    },
    handleCurrentChange (page) {
      let that = this
      that.currentPage = page
      that.getData()
    },
    getCardTypeName (num) {
      switch (num >> 0) {
        case 1:
          return '身份证'
        case 2:
          return '护照'
        case 4:
          return '台胞证'
        case 8:
          return '港澳通行证'
      }
    },
    getData () {
      // check member state and return data shown in the table
      const checkState = (t) => {
        const that = this
        const yy = that.systemTime.substring(0, 4)
        const MM = that.systemTime.substring(5, 7) - 1
        const dd = that.systemTime.substring(8, 10)
        const hh = that.systemTime.substring(11, 13)
        const mm = that.systemTime.substring(14, 16)
        const ss = that.systemTime.substring(17, 19)
        if (t === null) {
          return ['danger', '未注册']
        } else if (new Date(yy, MM, dd, hh, mm, ss) <= new Date(t)) {
          return ['success', '已年检']
        } else if (new Date(yy, MM, dd, hh, mm, ss) > new Date(t)) {
          return ['warning', '未年检']
        }
      }
      // format date to "2017-08-08"
      const formatDate = (t) => {
        let _t = ''
        if (typeof t === 'string') {
          const yy = t.substring(0, 4)
          const MM = t.substring(5, 7) - 1
          const dd = t.substring(8, 10)
          const hh = t.substring(11, 13)
          const mm = t.substring(14, 16)
          const ss = t.substring(17, 19)
          _t = new Date(yy, MM, dd, hh, mm, ss)
        } else {
          _t = t
        }
        const f = (a) => {
          return a < 10 ? '0' + a : a
        }
        return _t.getFullYear() + '-' + f(_t.getMonth() + 1) + '-' + f(_t.getDate()) + ' ' + f(_t.getHours()) + ': ' + f(_t.getMinutes())
      }
      // get the first month of every quarter in one year
      const quarter = (t) => {
        const yy = t.substring(0, 4)
        const MM = t.substring(5, 7) - 1
        const dd = t.substring(8, 10)
        const hh = t.substring(11, 13)
        const mm = t.substring(14, 16)
        const ss = t.substring(17, 19)
        const _t = new Date(yy, MM, dd, hh, mm, ss)
        const M = _t.getMonth() + 1
        const f = (w) => {
          const y = _t.getFullYear()
          return new Date(y, w, 1, 0, 0, 0)
        }
        if (M >= 1 && M <= 3) {
          return f(0)
        } else if (M >= 4 && M <= 6) {
          return f(3)
        } else if (M >= 7 && M <= 9) {
          return f(6)
        } else if (M >= 10 && M <= 12) {
          return f(9)
        }
      }
      const that = this
      const __url = that.$store.state.total.mem.child.memList[1]
      if (__url === '') return false
      that.$store.state.tableLoad = true
      // params
      let par = {
        keywords: that.input,
        type: that.select,
        pageSize: that.pageSize,
        page: that.currentPage
      }
      if (that.$store.state.searchDay) {
        par.end_time = that.$store.state.sysTime.substring(0, 10) + ' 00:00:00'
        const _now = new Date(par.end_time)
        const _before = new Date(_now.setDate(_now.getDate() - that.$store.state.searchDay))
        par.start_time = _before.getFullYear() + '-' + (_before.getMonth() + 1) + '-' + (_before.getDate() < 10 ? '0' + _before.getDate() : _before.getDate()) + ' 00:00:00'
        that.$store.commit('setSearchDay', 0)
      } else {
        par = {
          keywords: that.input,
          type: that.select,
          pageSize: that.pageSize,
          page: that.currentPage
        }
      }
      that.$ajax({
        url: __url,
        method: 'get',
        params: par
      }).then(res => {
        const _data = res.data
        const dat = _data.data
        that.$store.state.tableLoad = false
        if ((_data.errcode >> 0) === 0) {
          if (dat !== null) {
            that.currentData = []
            dat.forEach((e, i) => {
              that.currentData.push({
                no: e.ROW_NUMBER,
                id: e.id,
                number: e.idCard,
                name: e.idCardName,
                gender: e.sex >> 0 === 0 ? '男' : '女',
                rank: that.getRank(e.level),
                org: e.org_name,
                signTime: e.fees_start === null ? '未缴费注册' : formatDate(e.fees_start).substring(0, 10),
                overTime: e.fees_end === null ? '从未缴费' : formatDate(quarter(e.fees_end)).substring(0, 10),
                isOver: checkState(quarter(e.fees_end)),
                state: e.state >> 0,
                cardType: that.getCardTypeName(e.cardType >> 0),
                certNo: e.book_no === null ? '暂无' : e.book_no
              })
            })
          }
          that.totalNum = (_data.total >> 0)
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$store.state.tableLoad = false
        that.$message({message: '网络开小差了', type: 'warning'})
      })
    },
    addNewPlayer () {
      let root = this.$parent
      root.addTab('棋手注册', root)
      this.$router.push('/Main/addPlayer')
    },
    getOrgList () {
      const that = this
      that.orgList = [{
        label: '一级',
        options: []
      }, {
        label: '二级',
        options: []
      }, {
        label: '三级',
        options: []
      }, {
        label: '四级',
        options: []
      }]
      that.$ajax({
        url: that.$store.state.total.org.child.parentOrgList[1],
        params: {
          type: 'all',
          fields: 'id,org_name,org_level'
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data
          dat.forEach((e, i) => {
            if (e.org_level === '1') {
              that.orgList[0].options.push({
                label: e.org_name,
                value: e.id
              })
            } else if (e.org_level === '2') {
              that.orgList[1].options.push({
                label: e.org_name,
                value: e.id
              })
            } else if (e.org_level === '3') {
              that.orgList[2].options.push({
                label: e.org_name,
                value: e.id
              })
            } else if (e.org_level === '4') {
              that.orgList[3].options.push({
                label: e.org_name,
                value: e.id
              })
            }
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    }
  },
  mounted () {
    const that = this
    that.systemTime = that.$store.state.sysTime
    const __url = that.$store.state.total.set.child.feeList[1]
    if (__url) {
      that.$ajax({
        url: __url
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          _data.data.forEach((e, i) => {
            if (e.type === '2') {
              that.fee = e.price - 0
            }
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    }
    that.getData()
    that.getOrgList()
    if (window.location.href.includes('?pay')) {
      that.$notify.success({
        title: '支付成功',
        message: '年费缴纳成功',
        offset: 80
      })
    }
  }
}
</script>
