        padding:0 4px;
    }
    #player .text-black {
        color:#2D2F33;
    }
    #player .mem-dialog .one-day-content{
        margin-bottom: 10px;
        font-size: 16px;
    }
    #player .mem-dialog .one-day-info .one-day-img-container{
        width: 120px;
        position:absolute;
        right:50px;
        top:60px;
    }
    #player .mem-dialog .one-day-info .one-day-img{
        width: 100%;
        height: 168px;
        background:#e5e9f2;
        margin-bottom:22px;
    }
    #player .mem-dialog .one-day-info .one-day-img img{
        width: 100%;
    }
    #player .mem-dialog .mem-form .form-input{
        width:300px;
    }
    #player .mem-dialog .mem-form .form-input.age{
        width: 100px;
    }
    #player .no-cert{
        margin-top:20px;
        text-align:center;
        font-size: 16px;
        color:#8492a6
    }
    #player .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #player .fa-refresh:hover{
        opacity:0.7;
    }
    #player .type-area{
        text-align:center;
    }
    #player .type-area .wechat,#player .type-area .ali,#player .type-area .wallet{
        margin:0 20px 20px 20px;
        cursor:pointer;
        display:inline-block;
    }
    #player .allot-item{
        margin: 10px 0;
    }
    #player .selected-name{
        color:#20a0ff;
    }
    #player .el-table-column--selection{
        text-align: center;
    }
    #player .el-tag{
        margin:0;
    }

</style>



