<template>
    <main id="orgCreate"
          class="container">
        <header class="head-title">
            {{headName}}
        </header>
        <el-form ref="info"
                 label-position="right"
                 :model="orgData"
                 size="small"
                 label-width="110px"
                 :rules="rules" class="">
            <el-form-item label="机构名称"
                          prop="orgName"
                          class="item">
                <el-input v-model="orgData.orgName"
                          @change="delSpace"
                          required></el-input>
            </el-form-item>
            <el-form-item label="机构类型"
                          prop="orgType"
                          class="item">
                <el-select v-model="orgData.orgType"
                           placeholder="请选择机构级别"
                           :disabled="!state"
                           @change="changeType">
                    <el-option
                            v-for="item in orgOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="地址">
                <template slot-scope=" scope">
                    <el-select v-model="orgData.province"
                               disabled
                               required>
                        <el-option v-for="item in proArr"
                                   :key="item[0]"
                                   :label="item[1]"
                                   :value="item[0]"></el-option>
                    </el-select>
                    <el-select v-model="orgData.city"
                               :disabled="!$store.state.total.org.child.orgEdit[1]"
                               @change="getArea('city')"
                               required>
                        <el-option v-for="item in citArr"
                                   :disabled="!$store.state.total.org.child.orgEdit[1]"
                                   :key="item[0]"
                                   :label="item[1]"
                                   :value="item[0]"></el-option>
                    </el-select>
                    <el-select v-model="orgData.county"
                               :disabled="!$store.state.total.org.child.orgEdit[1]"
                               required>
                        <el-option v-for="item in couArr"
                                   :key="item[0]"
                                   :label="item[1]"
                                   :value="item[0]"></el-option>
                    </el-select>
                </template>
            </el-form-item>
            <el-form-item v-if="!(($store.state.orgId) >> 0 === editId >> 0)"
                          label="上属机构"
                          prop="parent"
                          class="item">
                <el-select v-model="orgData.parent"
                           placeholder="请选择上属机构"
                           filterable>
                    <el-option-group v-for="group in parentFilterOptions" :key="group.label" :label="group.label">
                        <el-option v-for="item in group.options" :label="item.label" :value="item.value" :key="item.value"></el-option>
                    </el-option-group>
                </el-select>
            </el-form-item>
            <el-form-item prop="address" label="街道门牌号" v-if="state===0">
                <el-input v-model="orgData.address" required :disabled="!$store.state.total.org.child.orgEdit[1]" :maxlength="100" type="textarea" :rows="3"></el-input><span style="font-size:12px;color:#999">请详细填写街道门牌号，该地址将作为证书寄送的地址</span>
            </el-form-item>
            <el-form-item
                    prop="power"
                    label="允许新增机构"
                    required
                    v-if="($store.state.orgId>>0) !== (editId>>0) && orgData.orgType !== '4' && orgData.orgType !== '四级（培训学校）'">
                <el-radio-group v-model="orgData.power">
                    <el-radio class="radio" label="1" :disabled="!$store.state.total.org.child.orgEdit[1]">是</el-radio>
                    <el-radio class="radio" label="0" :disabled="!$store.state.total.org.child.orgEdit[1]">否</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item
                    prop="print"
                    label="允许打印证书"
                    required
                    v-if="$store.state.orgLevel === 1">
                <el-radio-group v-model="orgData.print">
                    <el-radio class="radio" label="1">是</el-radio>
                    <el-radio class="radio" label="0">否</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="法人姓名" prop="person">
                <el-input v-model="orgData.person" :disabled="!$store.state.total.org.child.orgEdit[1]" required :maxlength="50"></el-input>
            </el-form-item>
            <el-form-item label="联系人姓名" prop="linkMan">
                <el-input v-model="orgData.linkMan" :disabled="!$store.state.total.org.child.orgEdit[1]" required :maxlength="50"></el-input>
            </el-form-item>
            <el-form-item label="联系人手机" prop="linkMob">
                <el-input v-model="orgData.linkMob" :disabled="!$store.state.total.org.child.orgEdit[1]" required :maxlength="11"></el-input>
            </el-form-item>
            <el-form-item label="上传租赁合同"  prop="contractURL">
                <el-upload
                        :action="this.$store.state.baseURL+this.$store.state.uploadImg"
                        :headers="{token:$parent.getToken()}"
                        list-type="picture-card"
                        :file-list="orgData.contractURL"
                        :on-success="saveContractURL"
                        :on-preview="handlePictureCardPreview"
                        :disabled="!$store.state.total.org.child.orgEdit[1]"
                        :on-remove="removeContractURL">
                    <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog  width="30%"
                            :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>
            </el-form-item>
            <el-form-item label="上传场地照片"  prop="fieldURL">
                <el-upload
                        :action="this.$store.state.baseURL+this.$store.state.uploadImg"
                        :headers="{token:$parent.getToken()}"
                        list-type="picture-card"
                        :file-list="orgData.fieldURL"
                        :disabled="!$store.state.total.org.child.orgEdit[1]"
                        :on-success="saveFieldURL"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="removeFieldURL">
                    <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog  width="30%"
                            :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>
            </el-form-item>
            <el-form-item label="上传执照"  prop="licenseURL">
                <el-upload
                        :action="this.$store.state.baseURL+this.$store.state.uploadImg"
                        :headers="{token:$parent.getToken()}"
                        list-type="picture-card"
                        :file-list="orgData.licenseURL"
                        :disabled="!$store.state.total.org.child.orgEdit[1]"
                        :on-success="saveLicenseURL"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="removeLicenseURL">
                    <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog  width="30%"
                            :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>
            </el-form-item>
            <div class="text-center">
                <el-button type="primary"
                           @click="addOrg('info')"
                           v-if="$store.state.total.org.child.orgEdit[1]">提 交</el-button>
                <el-button @click="resetForm('info')"
                           v-if="state===1">重 置</el-button>
                <el-button @click="goBack"
                           v-if="state===0">返 回 </el-button>
            </div>
        </el-form>
        <el-dialog :title="titleName"
                   width="30%"
                   :visible.sync="signUpDialogVisible"
                   :close-on-click-modal="false"
                   :close-on-press-escape="false"
                   :show-close="false">
            <p class="inner-name"
               v-if="titleName==='注册成功'">账号名称：{{orgData.orgName}}</p>
            <p class="inner-pwd"
               v-if="titleName==='注册成功'">初始密码：123456</p>
            <div class="inner-info">
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;机构名称：{{orgData.orgName}}</p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;机构类型：{{getType(orgData.orgType)}}</p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;上属机构：{{getParentName(orgData.parent)}}</p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;法人姓名：{{orgData.person}}</p>
                <p>&nbsp;联系人姓名：{{orgData.linkMan}}</p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;联系电话：{{orgData.linkMob}}</p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;地址：{{$parent.cityQuery(orgData.county,'county','get').toString().replace(/,/g,'')+orgData.address===null?'':orgData.address}}</p>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="returnOrgList">确 定</el-button>
            </span>
        </el-dialog>
    </main>
</template>
<script>
export default {
  data () {
    const that = this
    let $root = that.$parent
    let _proArr = $root.cityQuery('', 'province', 'get')
    let _citArr = that.formatArea($root.cityQuery(that.$store.state.pro, 'province', 'linkage'))
    let _couArr = that.formatArea($root.cityQuery(that.$store.state.pro + '01', 'city', 'linkage'))
    const checkPhone = (rule, value, callback) => {
      value = value.replace(/\s/g, '')
      that.orgData.linkMob = value
      if (!(/^1[0-9][0-9]\d{4,8}$/.test(value))) {
        callback(new Error('手机号格式不正确'))
      } else {
        callback()
      }
    }
    return {
      state: 1, // 1新增，0修改
      editId: '',
      headName: '填写注册信息',
      titleName: '注册成功',
      dialogVisible: false,
      signUpDialogVisible: false,
      dialogImageUrl: '',
      ajaxUrl: '',
      orgData: {
        orgName: '',
        orgType: '',
        parent: '',
        contractURL: [],
        fieldURL: [],
        licenseURL: [],
        img: {
          contract: '',
          field: '',
          license: ''
        },
        power: '1',
        print: '0',
        person: '',
        linkMan: '',
        linkMob: '',
        province: that.$store.state.pro,
        city: _citArr[0][0],
        county: _couArr[0][0],
        address: ''
      },
      orgOptions: [],
      orgInitialOptions: [],
      parentFilterOptions: [],
      parentOptions: [],
      parentInitialOptions: [],
      rules: {
        orgName: [
          {required: true, message: '请输入机构名称', trigger: 'blur'},
          {min: 3, max: 30, message: '长度为3-30个字符', trigger: 'blur'}
        ],
        orgType: [
          {required: true, message: '请选择机构类型', trigger: 'change'}
        ],
        parent: [
          {required: true, message: '请选择上属机构', trigger: 'change'}
        ],
        person: [
          {required: true, message: '请输入法人姓名', trigger: 'blur'},
          {min: 2, max: 18, message: '长度为2-50个字符', trigger: 'blur'}
        ],
        linkMan: [
          {required: true, message: '请输入联系人姓名', trigger: 'blur'},
          {min: 2, max: 18, message: '长度为2-50个字符', trigger: 'blur'}
        ],
        linkMob: [
          {required: true, validator: checkPhone, trigger: 'blur'}
        ],
        address: [
          {required: false, message: '请输入街道门牌号', trigger: 'blur'}
        ],
        contractURL: [
          {type: 'array', required: false, message: '请上传完整的租赁合同', trigger: 'change'}
        ],
        fieldURL: [
          {type: 'array', required: false, message: '请上传至少一张场地照片', trigger: 'change'}
        ],
        licenseURL: [
          {type: 'array', required: false, message: '请上传清晰的执照', trigger: 'change'}
        ]
      },
      proArr: _proArr,
      citArr: _citArr,
      couArr: _couArr
    }
  },
  methods: {
    formatArea (arr) {
      arr.forEach((e, i) => {
        if (e[0].includes(',')) {
          e[0] = e[0].substring(0, e[0].indexOf(','))
        }
      })
      return arr
    },
    delSpace () {
      this.orgData.orgName = this.orgData.orgName.replace(/\s/g, '')
    },
    changeType () {
      // 改变类型时联动修改可选父机构
      const that = this
      let arr = []
      let label = ''
      switch (+that.orgData.orgType) {
        case 2:
        case '二级（地市级）':
          arr = ['一级'] // 0
          label = '二级'
          break
        case 3:
        case '三级（县区级）':
          arr = ['一级', '二级'] // 0 1
          label = '三级'
          break
        case 4:
        case '四级（培训学校）':
          arr = ['一级', '二级', '三级'] // 0 1 2
          label = 'si级'
          break
      }
      that.parentOptions = []
      that.parentFilterOptions = []
      try {
        that.parentInitialOptions.forEach((e, i) => {
          if (arr.length - 1 >= i && e.label !== label) {
            that.parentOptions.push(e)
            that.parentFilterOptions.push(e)
          }
        })

        that.filterOrg(that.orgData.county)
      } catch (error) {
        console.log(error)
      }
    },
    goBack () {
      this.$parent.addTab('机构管理')
      this.$router.push('/Main/org')
    },
    getType (num) {
      if (num === '1') return '一级（省级）'
      else if (num === '2') return '二级（地市级）'
      else if (num === '3') return '三级（县区级）'
      else if (num === '4') return '四级（培训学校）'
    },
    getParentName (id) {
      const that = this
      let res = ''
      id = id >> 0
      that.parentOptions.forEach((e, i) => {
        e.options.forEach((element, index) => {
          if ((element.value >> 0) === id) {
            res = element.label
            return false
          }
        })
      })
      return res
    },
    removeURL (arr) {
      let _url = ''
      arr.forEach((e, i) => {
        if (e.url.includes('blob')) {
          if (i) _url += ',' + e.response.data
          else _url += e.response.data
        } else {
          const ee = e.url.substring(e.url.indexOf('uploads'))
          if (i) _url += ',' + ee
          else _url += ee
        }
      })
      return _url
    },
    removeContractURL (file, fileList) {
      this.orgData.img.contract = this.removeURL(fileList)
    },
    removeFieldURL (file, fileList) {
      this.orgData.img.field = this.removeURL(fileList)
    },
    removeLicenseURL (file, fileList) {
      this.orgData.img.license = this.removeURL(fileList)
    },
    saveContractURL (res, file, fileList) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        that.orgData.contractURL = fileList
        if (that.orgData.img.contract === '') {
          that.orgData.img.contract = res.data
        } else {
          that.orgData.img.contract += ',' + res.data
        }
      } else if ((res.errcode >> 0) === 1000 || (res.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(res.msg)
      }
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    saveFieldURL (res, file, fileList) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        that.orgData.fieldURL = fileList
        if (that.orgData.img.field === '') {
          that.orgData.img.field = res.data
        } else {
          that.orgData.img.field += ',' + res.data
        }
      } else if ((res.errcode >> 0) === 1000 || (res.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(res.msg)
      }
    },
    saveLicenseURL (res, file, fileList) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        that.orgData.licenseURL = fileList
        if (that.orgData.img.license === '') {
          that.orgData.img.license = res.data
        } else {
          that.orgData.img.license += ',' + res.data
        }
      } else if ((res.errcode >> 0) === 1000 || (res.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(res.msg)
      }
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    filterOrg (area) {
      const that = this
      if (typeof area === 'string') area = area.substring(0, 4)
      let recommend = []
      try {
        that.parentOptions.forEach((e, i) => {
          e.options.forEach((element, index) => {
            if (typeof element.area !== 'undefined') {
              if (element.area.includes(area)) {
                recommend.push(element)
              }
            }
          })
        })
        if (that.parentFilterOptions[0].label !== '推荐') {
          that.parentFilterOptions.unshift({
            label: '推荐',
            options: recommend
          })
        } else {
          that.parentFilterOptions[0].options = recommend
        }
      } catch (error) {
        console.log(error)
      }
    },
    getArea (type) {
      const $root = this.$parent
      const that = this
      let area = that.orgData.county
      if (type === 'province') {
        this.citArr = that.formatArea($root.cityQuery(this.orgData.province, 'province', 'linkage'))
        this.orgData.city = this.orgData.city === '' ? that.citArr[0][0] : this.orgData.city
        this.orgData.county = this.orgData.county === '' ? that.couArr[0][0] : this.orgData.county
        //  this.couArr = $root.cityQuery(this.citArr[0][0],'city','linkage');
      } else if (type === 'city') {
        that.couArr = that.formatArea($root.cityQuery(that.orgData.city, 'city', 'linkage'))
        that.orgData.county = that.orgData.county === '' ? that.couArr[0][0] : that.orgData.county
        area = that.orgData.city
      }
      if (that.orgData.city.includes(',')) {
        that.orgData.city = that.orgData.city.substring(0, that.orgData.city.indexOf(','))
      }
      if (that.orgData.county.includes(',')) {
        that.orgData.county = that.orgData.county.substring(0, that.orgData.county.indexOf(','))
      }
      that.filterOrg(area)
    },
    addOrg (formName) {
      const that = this
      if (that.ajaxUrl === '') return false
      if (that.orgData.linkMob !== '' && !(/^1[0-9][0-9]\d{4,8}$/.test(that.orgData.linkMob))) {
        that.$message.warning('手机号格式不正确')
        return false
      }
      that.$refs[formName].validate((valid) => {
        if (valid) {
          let par = {
            power: that.orgData.power,
            lease_img: that.orgData.img.contract,
            place_img: that.orgData.img.field,
            license_img: that.orgData.img.license,
            legal_name: that.orgData.person.replace(/\s/g, ''),
            link_name: that.orgData.linkMan.replace(/\s/g, ''),
            link_phone: that.orgData.linkMob,
            province: that.orgData.province,
            city: that.orgData.city,
            county: that.orgData.county,
            print_pre: that.orgData.print
          }
          if (that.state === 1) {
            // 新增
            par.level = that.orgData.orgType
            par.name = that.orgData.orgName.replace(/\s/g, '')
          }
          if (that.state === 0) {
            // 修改
            par.org_name = that.orgData.orgName.replace(/\s/g, '')
            par.id = that.editId
            par.address = that.orgData.address
          }
          // 改的不是自己
          if ((that.$store.state.orgId) >> 0 !== that.editId >> 0) {
            par.p_id = that.orgData.parent
          }
          // 改了四级
          if (that.orgData.orgType === '4') {
            par.power = '0'
          }
          // 省级可以控制 打印 功能
          if (that.$store.state.orgLevel === 1) {
            par.print = that.orgData.print
          }
          that.$ajax({
            method: 'post',
            url: that.ajaxUrl,
            data: par
          }).then((res) => {
            if ((res.data.errcode >> 0) === 1000) {
              that.$message.warning('未登录或登录已过期')
              that.$router.push('/login')
            } else if ((res.data.errcode >> 0) === 0) {
              that.signUpDialogVisible = true
              that.$message.success(that.titleName)
            } else if ((res.data.errcode >> 0) === 1000) {
              that.$parent.overTime()
            } else {
              that.$message.warning(res.data.msg)
            }
            that.$parent.refreshToken()
          }, res => {
            that.$message.warning('网络似乎出了点问题')
          })
        }
      })
    },
    returnOrgList () {
      const that = this
      that.signUpDialogVisible = false
      that.$parent.addTab('机构管理')
      that.$router.push('/Main/org')
    }
  },
  mounted () {
    const that = this
    // 生成上属机构选项（外壳）
    const _type = that.$store.state.orgLevel.toString()
    if (_type === '1') {
      that.orgInitialOptions = [
        {value: '2', label: '二级（地市级）'},
        {value: '3', label: '三级（县区级）'},
        {value: '4', label: '四级（培训学校）'}
      ]
    } else if (_type === '2') {
      that.orgInitialOptions = [
        {value: '3', label: '三级（县区级）'},
        {value: '4', label: '四级（培训学校）'}
      ]
    } else if (_type === '3') {
      that.orgInitialOptions = [
        {value: '4', label: '四级（培训学校）'}
      ]
    }
    that.orgOptions = []
    that.orgInitialOptions.forEach((e, i) => {
      that.orgOptions.push(e)
    })
    // 获取所有机构列表
    that.$ajax({
      url: that.$store.state.total.org.child.parentOrgList[1],
      params: {
        type: 'branch',
        fields: 'id,org_name,org_level,county'
      }
    }).then(res => {
      const _data = res.data
      if ((_data.errcode >> 0) === 0) {
        if (that.$store.state.orgLevel === 1) {
          that.parentInitialOptions = [{
            label: '直属',
            options: [{
              value: that.$store.state.orgId + '',
              label: that.$store.state.orgName,
              county: _data.county
            }]
          }, {
            label: '二级',
            options: []
          }, {
            label: '三级',
            options: []
          }]
        } else if (that.$store.state.orgLevel === 2) {
          that.parentInitialOptions = [{
            label: '直属',
            options: [{
              value: that.$store.state.orgId + '',
              label: that.$store.state.orgName,
              county: _data.county
            }]
          }, {
            label: '三级',
            options: []
          }]
        } else if (that.$store.state.orgLevel === 3) {
          that.parentInitialOptions = [{
            label: '直属',
            options: [{
              value: that.$store.state.orgId + '',
              label: that.$store.state.orgName,
              county: _data.county
            }]
          }]
        }
        const dat = _data.data
        dat.forEach((e, i) => {
          if (that.$store.state.orgLevel === 1) {
            if (e.org_level === '2') {
              that.parentInitialOptions[1].options.push({
                label: e.org_name,
                value: e.id,
                area: e.county
              })
            } else if (e.org_level === '3') {
              that.parentInitialOptions[2].options.push({
                label: e.org_name,
                value: e.id,
                area: e.county
              })
            }
          } else if (that.$store.state.orgLevel === 2) {
            if (e.org_level === '3') {
              that.parentInitialOptions[1].options.push({
                label: e.org_name,
                value: e.id,
                area: e.county
              })
            }
          }
        })
        that.changeType()
      } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(_data.msg)
      }
    }, res => {
      that.parentInitialOptions = [{
        label: '直属',
        options: [{
          value: that.$store.state.orgId + '',
          label: that.$store.state.orgName,
          county: '330101'
        }]
      }]
      that.$message.warning('网络似乎出了点问题')
    })
    // 获取修改机构数据
    let watch = (id) => {
      const fileList = (source) => {
        if (source !== '') {
          let arr = []
          source.forEach((e, i) => {
            arr.push({
              name: i,
              url: that.$store.state.baseURL + e
            })
          })
          return arr
        } else {
          return []
        }
      }
      const __url = that.$store.state.total.org.child.orgDetail[1]
      if (__url === '') return false
      that.$ajax({
        url: __url,
        params: {
          orgID: id
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data
          const _lease = (dat.lease_img === null || dat.lease_img === '') ? '' : dat.lease_img.split(',')
          const _place = (dat.place_img === null || dat.place_img === '') ? '' : dat.place_img.split(',')
          const _license = (dat.license_img === null || dat.license_img === '') ? '' : dat.license_img.split(',')
          that.orgData = {
            orgName: dat.org_name,
            orgType: dat.org_level.toString(),
            parent: dat.parent_id,
            person: dat.legal_name,
            linkMan: dat.link_name,
            linkMob: dat.link_phone,
            province: dat.province,
            city: dat.city,
            county: dat.county,
            address: dat.address,
            memNum: dat.count_mem,
            childNum: dat.count_org,
            print: dat.print_pre,
            img: {
              contract: dat.lease_img,
              field: dat.place_img,
              license: dat.license_img
            },
            power: dat.power + '',
            contractURL: fileList(_lease),
            fieldURL: fileList(_place),
            licenseURL: fileList(_license)
          }
          that.$store.commit('saveOrgEditData', id)
          that.getArea('city')
          that.changeType()
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    }
    const href = window.location.href
    const index = href.indexOf('?')
    let id = parseInt(href.substring(index + 1))
    if (href.includes('OrgEdit') && index === -1) {
      id = that.$store.state.orgEditData
      history.replaceState(null, '', location.hash + '?' + id)
    }
    if (isNaN(id)) {
      // 新增
      that.ajaxUrl = that.$store.state.total.org.child.orgAdd[1]
      that.rules.contractURL[0].required = false
      that.rules.fieldURL[0].required = false
      that.rules.licenseURL[0].required = false
      that.rules.linkMan[0] = false
      that.rules.linkMob[0] = []
      that.rules.person[0].required = false
      that.orgData.orgType = ((that.$store.state.orgLevel >> 0) + 1).toString()
      that.orgData.parent = that.$store.state.orgId + ''
    } else {
      // 修改
      that.ajaxUrl = that.$store.state.total.org.child.orgEdit[1]
      that.rules.address[0].required = true
      that.state = 0
      that.editId = id
      that.headName = '机构信息'
      that.titleName = '修改成功'
      watch(id)
    }
  }
}
</script>
<style>
    #orgCreate{
        background:#fff;
        padding-left:20px;
    }
    #orgCreate .el-dialog__header .el-dialog__title,.inner-name,.inner-pwd{
        color:#20a0ff;
    }
    #orgCreate .inner-name,.inner-pwd{
        margin-left:30px;
    }
    #orgCreate .el-dialog__body p{
        margin-bottom:10px;
    }
    #orgCreate .el-dialog__header{
        text-align:center;
    }
    #orgCreate .el-dialog__footer{
        text-align:center;
    }
    #orgCreate .inner-info{
        border:1px solid #ddd;
        padding:10px 8px;
    }
    #orgCreate .el-form{
        width:570px;
        margin-bottom:50px;
    }
    #orgCreate .el-select{
        width:150px;
    }
</style>



