<template>
    <main id="w_create" class="container">
        <header class="head-title" v-if="!bindState">绑定</header>
        <header class="head-title" v-if="bindState">修改</header>
        <div class="bank-info" v-if="bindState">
            <span>已绑定银行卡信息：</span>
            <span>开户人：{{info.name}}</span>
            <span>银行卡号：{{info.card | hide(info.card)}}</span>
            <span>预留手机号：{{info.mobile | hide(info.mobile)}}</span>
        </div>
        <div class="form-container">
            <el-form label-positon="left" ref="bindCard" :model="bindCard" :rules="rules" label-width="100px">
                <el-form-item prop="name" label="姓名">
                    <el-input v-model="bindCard.name" placeholder="请输入开户行预留姓名" :maxlength="30"></el-input>
                </el-form-item>
                <el-form-item prop="card" label="银行卡号">
                    <el-input v-model="bindCard.card" placeholder="请输入银行卡号" :maxlength="19"></el-input>
                </el-form-item>
                <el-form-item prop="place" label="开户行">
                    <el-input v-model="bindCard.place" placeholder="请输入开户行名称（如：中国银行杭州市分行庆春支行）"></el-input>
                </el-form-item>
                <el-form-item prop="mob" label="预留手机">
                    <el-input v-model="bindCard.mob" placeholder="请输入银行预留手机" :maxlength="11"></el-input>
                </el-form-item>
                <el-form-item prop="code" label="验证码">
                    <el-input v-model="bindCard.code" placeholder="请输入收到的验证码" :maxlength="6">
                            <el-button :style="{cursor:(codeBtn.isAjax || codeBtn.text.includes('重新') ? 'not-allowed' : 'pointer')}" slot="append" @click="getCode()" :disabled="codeBtn.isAjax || codeBtn.text.includes('重新')" :loading="codeBtn.isAjax">{{codeBtn.text}}</el-button>
                    </el-input>
                </el-form-item>
                <el-form-item class="text-center">
                    <el-button type="primary" @click="bind('bindCard')">绑 定</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="tip text-center" v-if="bindState">提示：绑定新卡将会覆盖原来绑定的旧卡，请慎重更换。</div>
    </main>
</template>
<script>export default{
  data () {
    let checkBank = (rule, value, callback) => {
      value = value.replace(/\s/g, '')
      if (value === '') {
        callback(new Error('请输入银行卡号'))
      } else if (value.length !== 16 && value.length !== 19) {
        callback(new Error('银行卡位数不正确，请检查'))
      } else {
        callback()
      }
    }
    let checkPhone = (rule, value, callback) => {
      value = value.replace(/\s/g, '')
      if (!/^1[34578]\d{9}$/.test(value)) {
        callback(new Error('手机号格式不正确'))
      } else {
        callback()
      }
    }
    return {
      bindState: false,
      bindCard: {
        name: '',
        card: '',
        place: '',
        mob: '',
        code: ''
      },
      info: {
        name: '',
        card: '',
        mobile: ''
      },
      codeBtn: {
        isAjax: false,
        text: '获取验证码'
      },
      rules: {
        name: [
          {required: true, message: '请输入开户人姓名', trigger: 'blur'},
          {min: 2, max: 30, message: '开户人姓名长度在2-30个字符', trigger: 'blur'}
        ],
        card: [
          {required: true, validator: checkBank, trigger: 'blur'}
        ],
        place: [
          {required: true, message: '请输入开户行名称', trigger: 'blur'},
          {min: 6, message: '开户行不少于6个字符', trigger: 'blur'}
        ],
        mob: [
          {required: true, validator: checkPhone, trigger: 'blur'}
        ],
        code: [
          {required: true, message: '请输入收到的验证码', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    getCode () {
      const that = this
      const __url = that.$store.state.code.bindCardCode
      if (__url === '') return false
      if (!/^1[34578]\d{9}$/.test(that.bindCard.mob)) {
        that.$message.warning('手机号码不正确')
        return false
      }
      that.codeBtn.isAjax = true
      that.$ajax({
        url: __url,
        params: {
          mobi: that.bindCard.mob
        }
      }).then(res => {
        const _data = res.data
        that.codeBtn.isAjax = false
        let _t = 60
        const timer = setInterval(() => {
          that.codeBtn.text = '重新获取 ' + _t + 's'
          _t--
          if (_t <= 0) {
            that.codeBtn.text = '获取验证码'
            clearInterval(timer)
          }
        }, 1000)
        if ((_data.errcode >> 0) === 0) {
          that.$notify.success({
            title: '验证码发送成功',
            message: '已发送至手机：' + that.bindCard.mob
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.codeBtn.isAjax = true
        that.$message.warning('网络似乎出了点问题')
      })
    },
    bind (formName) {
      const that = this
      const __url = that.$store.state.total.wallet.child.update[1]
      if (__url === '') return false
      that.$refs[formName].validate((valid) => {
        if (valid) {
          that.bindCard.name = that.bindCard.name.replace(/\s/g, '')
          that.bindCard.card = that.bindCard.card.replace(/\s/g, '')
          that.bindCard.place = that.bindCard.place.replace(/\s/g, '')
          that.bindCard.mob = that.bindCard.mob.replace(/\s/g, '')
          that.$ajax({
            url: __url,
            params: {
              name: that.bindCard.name.replace(/\s/g, ''),
              bank_name: that.bindCard.name.replace(/\s/g, ''),
              bank_card: that.bindCard.card.replace(/\s/g, ''),
              phone: that.bindCard.mob.replace(/\s/g, ''),
              code: that.bindCard.code.replace(/\s/g, '')
            }
          }).then(res => {
            const _data = res.data
            if ((_data.errcode >> 0) === 0) {
              that.$message.success('绑定成功')
              that.$parent.addTab('提现')
              that.$router.push('/Main/WCash')
            } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
              that.$parent.overTime()
            } else {
              that.$message.warning(_data.msg)
            }
          }, res => {
            that.$message.warning('网络似乎出了点问题')
          })
        }
      })
    },
    unbind (forName) {
      /*
              that.$refs[formName].validate((valid) => {
                  if(valid){
                      that.bindCard.name = that.bindCard.name.replace(/\s/g,'');
                      that.bindCard.card = that.bindCard.card.replace(/\s/g,'');
                      that.bindCard.place = that.bindCard.place.replace(/\s/g,'');
                      that.bindCard.mob = that.bindCard.mob.replace(/\s/g,'');
                      that.$ajax({
                          url:that.$store.state.total.wallet.update[1],
                          params:{
                              name:that.bindCard.name.replace(/\s/g,''),
                              bank_name:that.bindCard.name.replace(/\s/g,''),
                              bank_card:that.bindCard.card.replace(/\s/g,''),
                              phone:that.bindCard.mob.replace(/\s/g,'')
                          }
                      }).then(res=>{
                          const _data = res.data;
                          if((_data.errcode>>0)===0){
                              that.$message.success('绑定成功');
                          }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                              that.$parent.overTime();
                          } else {
                              that.$message.warning(_data.msg);
                          }
                      },res=>{
                          that.$message.warning('网络似乎出了点问题');
                      });
                  }
              });
              */
    }
  },
  filters: {
    hide (value) {
      return value.substring(0, 3) + '******' + value.substring(value.length - 2, value.length)
    }
  },
  mounted () {
    const that = this
    const __url = that.$store.state.total.wallet.child.info[1]
    if (__url === '') return false
    that.$ajax({
      url: __url
    }).then(res => {
      const _data = res.data
      if ((_data.errcode >> 0) === 0) {
        const dat = _data.data
        that.amount = dat.balance === null ? 0 : (dat.balance >> 0)
        if (dat.bank_card === null || dat.bank_name === null || dat.phone === null) {
          that.bindState = false
        } else {
          that.bindState = true
          that.info = {
            card: dat.bank_card,
            name: dat.bank_name,
            mobile: dat.phone
          }
        }
      } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(_data.msg)
      }
    }, res => {
      that.$message.warning('网络似乎出了点问题')
    })
  }
}
</script>
<style>
    #w_create{
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #w_create .form-container{
        width:40%;
        min-width:468px;
        /*margin:0 auto;*/
    }
    #w_create .tip{
        font-size:12px;
        color:#8391a5;
    }
    #w_create .bank-info{
        padding:10px 0 10px 40px;
        color:#99a9bf;
        font-size:14px;
    }
    #w_create .bank-info span{
        margin-left:10px;
        line-height:24px;
    }
</style>



