<template>
    <main id="login" class="container">
        <div class="bg_head">
            <img src="./../../src/assets/img/pro_name/河北围棋协会管理系统.png"
                 v-if="SystemName==='河北围棋协会管理系统'"
                 :alt="SystemName">
            <img src="./../../src/assets/img/pro_name/浙江围棋协会管理系统.png"
                 v-if="SystemName==='浙江围棋协会管理系统'"
                 :alt="SystemName">
        </div>
        <!--<div class="bg_chess"><img src="./../../src/assets/img/bg_chess.png" alt="chess"></div>-->
        <!--<div class="bg_login"><img src="./../../src/assets/img/bg_login.png" alt="chess"></div>-->
        <div id="loginArea" @keyup.enter="onSubmit('info')">
            <p id="version">{{ENV}} —— {{VERSION}}</p>
            <el-form ref="info"
                     :model="info"
                     :rules="rules"
                     class="login-form"
                     label-position="left"
                     label-width="60px">
                <el-form-item prop="account"
                              label="账号"
                              class="item">
                    <el-input v-model="info.account"
                              placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item  prop="password"
                               label="密码"
                               class="item">
                    <el-input type="password"
                              v-model="info.password"
                              placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item class="item">
                    <el-button class="btn"
                               type="primary"
                               @click="onSubmit('info')"
                               :loading="submitBtn">登 录</el-button>
                </el-form-item>
            </el-form>
        </div>
    </main>
</template>
<script>
export default{
  data: function () {
    return {
      SystemName: '',
      SystemImg: '',
      info: {
        account: '',
        password: ''
      },
      rules: {
        account: [
          {required: true, message: '请输入账号', trigger: 'blur'},
          {min: 3, max: 30, message: '长度为3-30个字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 18, message: '长度为6-18位', trigger: 'blur'}
        ]
      },
      submitBtn: false,
      ENV: '',
      VERSION: '1.0.21'
    }
  },
  methods: {
    onSubmit: function (formName) {
      let that = this
      that.submitBtn = !that.submitBtn
      that.$refs[formName].validate((valid) => {
        if (valid) {
          that.$ajax({
            method: 'post',
            url: 'admin/login',
            data: {
              name: that.info.account.replace(/\s/g, ''),
              pwd: that.info.password
            }
          }).then(res => {
            let _data = res.data
            if ((_data.errcode >> 0) === 0) {
              const dat = _data.data
              that.$message.success('欢迎登录')
              that.submitBtn = !that.submitBtn
              document.cookie = 'token=' + _data.data.token
              document.cookie = 'state=' + _data.data.state
              that.$ajax.defaults.headers = {token: dat.token}
              that.$router.push('/Main')
            } else {
              that.$message.warning(_data.msg)
              that.submitBtn = !that.submitBtn
            }
          }, res => {
            that.submitBtn = !that.submitBtn
            that.$message.warning('网络似乎出了点问题')
          })
        } else {
          that.submitBtn = !that.submitBtn
        }
      })
    }
  },
  mounted () {
    const that = this
    if (window.location.href.includes('out_trade_no')) {
      const url = window.location.href
      let type = 0
      type = url.substring(url.indexOf('out_trade_no=') + 27, url.indexOf('out_trade_no=') + 29)
      const returnRouter = {
        '04': '/Main/addPlayer?pay',
        '05': '/Main/myEvent?pay',
        '06': '/Main/apply?pay',
        '07': '/Main/player?pay'
      }
      that.$router.push(returnRouter[type])
      history.replaceState(null, '', '/#' + returnRouter[type])
    }
    that.SystemName = document.getElementsByTagName('title')[0].innerText.trim()
    that.SystemImg = that.SystemName
    if (window.location.href.includes('test')) {
      that.ENV = '测试版'
    } else {
      that.ENV = '正式版'
    }
  }
}
</script>
<style>
    #version{
        font-size:12px;
        color:#888;
        float:right;
    }
    #login{
        background:#fbf8fb;
        background-size:100% 100%;
        min-width:998px;
        height:100%;
        overflow: hidden;
        position:fixed;
        width:100%;
    }
    #login #loginArea{
        position: relative;
        z-index:1;
        overflow: hidden;
        margin:300px auto;
        /*background:rgba(60,60,60,0.7);*/
        width:400px;
        height:240px;
        /*-webkit-box-shadow:-2px -2px 8px 0 rgba(255,255,255,0.45);*/
        /*-moz-box-shadow:-2px -2px 8px 0 rgba(255,255,255,0.45);*/
        /*box-shadow:-2px -2px 8px 0 rgba(255,255,255,0.45);*/
    }
    #login .bg_head{
        width:1002px;
        margin:0 auto;
        position: absolute;
        top:200px;
        left:0;
        right:0;
        text-align: center;
    }
    #login .bg_chess{
        width:1002px;
        position: absolute;
        left:0;
        right:0;
        top:270px;
        margin:0 auto;
        text-align: center;
    }
    #login .bg_login{
        width:100%;
        height:100%;
        position:absolute;
        z-index:-1;
    }
    #login .bg_login img{
        width:100%;
        height:100%;
    }
    #login .bg_chess img{
        width:800px;
    }
    #login .login-form{
        width:100%;
        margin:40px auto;
    }
    #login .login-form .item{
        margin-bottom: 28px;
    }
    #login .login-form .btn{
        width:100%;
    }
    #login .el-form-item__label,#login .el-button{
        font-size:16px;
    }
    #login .title{
        text-align: center;
        font-size:18px;
        color:#fff;
        margin-top:16px;
    }
    @media screen and (-webkit-min-device-pixel-ratio:1.01){
        #login #loginArea{
            margin:200px auto;
        }
        #login .bg_chess{
            top:200px;
        }
        #login .bg_chess img{
            width:700px;
        }
        #login .bg_head{
            top:60px;
        }
    }
    @media screen and (min-device-pixel-ratio:1.01){
        #login #loginArea{
            margin:200px auto;
        }
        #login .bg_chess{
            top:200px;
        }
        #login .bg_chess img{
            width:700px;
        }
        #login .bg_head{
            top:60px;
        }
    }
</style>



