<template>
    <main id="authority" class="container">
        <el-tabs type="border-card"
                 @tab-click="getData($event)">
            <el-tab-pane label="账户权限设置">
                <div class="text-center"
                     v-if="!$store.state.total.set.child.childAdd[1]&&!$store.state.total.set.child.childList[1]">
                    <p>无权限操作该项</p>
                </div>
                <div class="auth-set"
                     v-if="$store.state.total.set.child.childAdd[1]">
                    <el-form label-positon="top"
                             ref="info"
                             :model="info"
                             label-width="68px"
                             :rules="rules"
                             class="login-form">
                        <el-form-item label="子账号"
                                      prop="account"
                                      class="item">
                            <el-input v-model="info.account"
                                      required
                                      placeholder="请输入账号"
                                      :maxlength="30"></el-input>
                        </el-form-item>
                        <el-form-item label="密码"
                                      prop="pwd"
                                      class="item">
                            <el-input auto-complete="new-password"
                                      type="password"
                                      :maxlength="18"
                                      v-model="info.pwd"
                                      required
                                      placeholder="请输入密码"></el-input>
                        </el-form-item>
                        <el-form-item label="账号权限">
                            <el-checkbox-group v-model="checked"
                                               @change="getAuth">
                                <el-checkbox v-for="item in authList"
                                             :label="item.name"
                                             :key="item.name"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary"
                                       @click="addAccount('info')"
                                       :disabled="isAjax"
                                       size="small"
                                       :loading="isAjax">新 增</el-button>
                            <el-button v-if="0" @click="info={account:'',pwd:'',auth:''}">重 置</el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="auth-table"
                     v-if="$store.state.total.set.child.childList[1]">
                    <el-table :data="accountInfo"
                              size="mini"
                              stripe
                              style="width:100%"
                              @expand-change="getDetail">
                        <el-table-column type="expand"
                                         label="详情">
                            <template slot-scope="scope">
                                <div class="detail-container"
                                     :loading="!scope.row.isAjax">
                                    <el-form label-position="left"
                                             label-width="100px"
                                             :inline="true"
                                             class="table-expand"
                                             v-if="scope.row.isAjax">
                                        <!--<el-form-item label="账号名">-->
                                            <!--<span>{{ scope.row.name }}</span>-->
                                        <!--</el-form-item>-->
                                        <!--<el-form-item label="角色">-->
                                            <!--<span>{{ scope.row.auth }}</span>-->
                                        <!--</el-form-item>-->
                                        <el-form-item label="权限">
                                            <span v-html="scope.row.allAuth">{{ scope.row.allAuth }}</span>
                                        </el-form-item>
                                        <el-form-item label="创建时间">
                                            <span>{{ scope.row.createTime }}</span>
                                        </el-form-item>
                                        <el-form-item label="最后修改时间">
                                            <span>{{ scope.row.updateTime }}</span>
                                        </el-form-item>
                                    </el-form>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="no"
                                         label="编号"
                                         min-width="80">
                        </el-table-column>
                        <el-table-column prop="name"
                                         label="账号"
                                         min-width="180">
                        </el-table-column>
                        <el-table-column prop="auth"
                                         label="角色"
                                         min-width="200">
                        </el-table-column>
                        <el-table-column label="操作"
                                         min-width="220">
                            <template slot-scope="scope">
                                <el-button type="text"
                                           @click="edit(scope.row.id)"
                                           v-if="$store.state.total.set.child.childChangePwd[1]">修改密码</el-button>
                                <el-popover
                                        placement="top"
                                        title="提示"
                                        width="200"
                                        v-model="scope.row.forbid"
                                        trigger="click"
                                        v-if="$store.state.total.set.child.childForbid[1]">
                                    <p>是否确认{{scope.row.state === '0'?'启用':'禁用'}}该账号？</p>
                                    <div style="text-align: right; margin: 0">
                                        <el-button size="mini"
                                                   type="text"
                                                   @click="scope.row.forbid = false">取消</el-button>
                                        <el-button type="primary"
                                                   size="mini"
                                                   :disabled="tinyAjax"
                                                   :loading="tinyAjax"
                                                   @click="scope.row.forbid = forbid(scope.row,scope.row.id,scope.row.state)">确定</el-button>
                                    </div>
                                    <el-button v-if="scope.row.state === '0'&&!scope.row.isAdmin"
                                               type="text"
                                               class="text-success"
                                               slot="reference">启用</el-button>
                                    <el-button v-if="scope.row.state === '1'&&!scope.row.isAdmin"
                                               type="text"
                                               class="text-warning"
                                               slot="reference">禁用</el-button>
                                </el-popover>
                                <el-popover placement="top"
                                            title="警告"
                                            width="200"
                                            v-model="scope.row.del"
                                            v-if="$store.state.total.set.child.childDel[1]">
                                    <p>子账号删除后将无法恢复！</p>
                                    <div style="text-align: right; margin: 0">
                                        <el-button size="mini"
                                                   type="text"
                                                   @click="scope.row.del = false">取消</el-button>
                                        <el-button type="primary"
                                                   size="mini"
                                                   :disabled="tinyAjax"
                                                   :loading="tinyAjax"
                                                   @click="scope.row.del = del(scope.row.id)">确定</el-button>
                                    </div>
                                    <el-button v-if="!scope.row.isAdmin"
                                               type="text"
                                               class="text-red"
                                               size="mini"
                                               slot="reference">删除</el-button>
                                </el-popover>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </el-tab-pane>
            <el-tab-pane label="账号动态">
                <el-table :data="dynamic"
                          size="mini"
                          stripe
                          style="width:100%"
                          v-if="$store.state.total.set.child.log[1]">
                    <el-table-column prop="no"
                                     label="编号"
                                     min-width="80">
                    </el-table-column>
                    <el-table-column prop="date"
                                     label="日期"
                                     min-width="200">
                    </el-table-column>
                    <el-table-column prop="name"
                                     label="账号"
                                     min-width="200">
                    </el-table-column>
                    <el-table-column prop="operate"
                                     label="动态"
                                     min-width="300">
                    </el-table-column>
                </el-table>
                <div class="text-center"
                     v-if="!$store.state.total.set.child.log[1]">
                    <p>无权限操作该项</p>
                </div>
                <el-pagination @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[20, 50, 100]"
                               :page-size="pageSize"
                               layout="total,sizes, prev, pager, next, jumper"
                               :total="totalNum"
                               v-if="$store.state.total.set.child.log[1]">
                </el-pagination>
            </el-tab-pane>
        </el-tabs>
        <el-dialog title="修改账号密码"
                   width="30%"
                   :visible.sync="dialogVisible"
                   :model="change"
                   ref="change"
                   :rules="changeRules">
            <el-form label-position="left"
                     label-width="60px">
                <el-form-item prop="oldPwd"
                              label="原密码">
                    <el-input placeholder="请输入原密码"
                              type="password"
                              v-model="change.oldPwd"
                              :maxlength="18"></el-input>
                </el-form-item>
                <el-form-item prop="newPwd" label="新密码">
                    <el-input placeholder="请输入新密码"
                              type="password"
                              v-model="change.newPwd"
                              :maxlength="18"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button size="small" @click="dialogVisible=false">取 消</el-button>
                    <el-button size="small" type="primary" @click="changePwd">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </main>
</template>
<script>export default{
  data () {
    return {
      isAjax: false,
      tinyAjax: false,
      dialogVisible: false,
      visible2: false,
      authList: [],
      info: {
        account: '',
        pwd: '',
        auth: '',
        id: ''
      },
      change: {
        oldPwd: '',
        newPwd: ''
      },
      editID: '',
      checked: [],
      rules: {
        account: [
          {required: true, message: '请输入账号', trigger: 'blur'},
          {min: 3, max: 18, message: '长度在3-18个字符', trigger: 'blur'}
        ],
        pwd: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 18, message: '长度在6-18个字符', trigger: 'blur'}
        ]
      },
      changeRules: {
        oldPwd: [
          {required: true, message: '请输入原密码', trigger: 'blur'},
          {min: 3, max: 18, message: '长度位6-18位', trigger: 'blur'}
        ],
        newPwd: [
          {required: true, message: '请输入新密码', trigger: 'blur'},
          {min: 6, max: 18, message: '长度位6-18位', trigger: 'blur'}
        ]
      },
      accountInfo: [],
      currentPage: 1,
      pageSize: 20,
      totalNum: 1000,
      dynamic: []
    }
  },
  methods: {
    getDetail (row) {
      row.isExpand = !row.isExpand
      // 只在没有请求过时发送请求
      if (row.isAjax) {
        return false
      }
      const that = this
      const __url = that.$store.state.total.set.child.roleDetail[1]
      if (__url === '') return false
      that.$ajax({
        url: __url,
        method: 'post',
        data: {
          u_id: row.id
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          row.isAjax = true
          const dat = _data.data
          if (dat.length === 0) {
            row.allAuth = ['主管理员']
          } else {
            row.allAuth = []
            dat.forEach((e, i) => {
              row.allAuth.push(e.role_name)
            })
          }
          row.allAuth = row.allAuth.join('&nbsp;&nbsp;&nbsp;')
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    edit (id) {
      const that = this
      that.dialogVisible = true
      that.editID = id
      that.change = {
        oldPwd: '',
        newPwd: ''
      }
    },
    getAuth (target) {
      const that = this
      let authArr = []
      let idArr = []
      // 选中的权限转为数组
      target.forEach((e, i) => {
        that.authList.forEach((element, index) => {
          if (e === element.name) {
            element.value.split(',').forEach((e, i) => {
              authArr.push(e)
            })
            idArr.push(element.id)
          }
        })
      })
      Array.prototype.unique = function () {
        this.sort() // 先排序
        let res = [this[0]]
        for (let i = 1; i < this.length; i++) {
          if (this[i] !== res[res.length - 1]) {
            res.push(this[i])
          }
        }
        return res
      }
      that.info.id = idArr.join(',')
      that.info.auth = authArr.unique().join(',')
    },
    changePwd () {
      const that = this
      const __url = that.$store.state.total.set.child.childChangePwd[1]
      if (that.change.oldPwd === '' || that.change.newPwd === '') {
        that.$message.warning('密码不能为空')
        return false
      }
      if (that.change.oldPwd.length > 18 || that.change.newPwd.length > 18 || that.change.oldPwd.length < 6 || that.change.newPwd.length < 6) {
        that.$message.warning('密码长度为 6-18 位')
        return false
      }
      if (__url === '') return false
      that.tinyAjax = true
      that.$ajax({
        url: __url,
        params: {
          u_id: that.editID,
          oldpwd: that.change.oldPwd,
          newpwd: that.change.newPwd
        }
      }).then(res => {
        that.tinyAjax = false
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.dialogVisible = false
          that.$notify.success({
            title: '修改成功',
            message: '新密码为：' + that.change.newPwd + '，请妥善保管',
            duration: 0,
            offset: 80
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.tinyAjax = false
        that.dialogVisible = false
        that.$message.warning('网络似乎出了点问题')
      })
    },
    forbid (row, id, state) {
      const that = this
      const __url = that.$store.state.total.set.child.childForbid[1]
      if (__url === '') return false
      that.tinyAjax = true
      that.$ajax({
        url: __url,
        params: {
          u_id: id,
          state: (state >> 0) === 1 ? 0 : 1
        }
      }).then(res => {
        that.tinyAjax = false
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.$message.success(_data.msg)
          row.state = (state >> 0) === 1 ? '0' : '1'
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.tinyAjax = false
        that.$message.warning('网络似乎出了点问题')
      })
      return false
    },
    del (id) {
      const that = this
      const __url = that.$store.state.total.set.child.childDel[1]
      if (__url === '') return false
      const delItem = (id) => {
        let a = ''
        that.accountInfo.forEach((e, i) => {
          if (e.id === id) {
            a = i
            return false
          }
        })
        return a
      }
      const index = delItem(id)
      that.tinyAjax = true
      that.$ajax({
        url: __url,
        params: {
          u_id: id
        }
      }).then(res => {
        that.tinyAjax = false
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.$message.success(_data.msg)
          that.accountInfo.splice(index, 1)
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.tinyAjax = false
        that.$message.warning('网络似乎出了点问题')
      })
      return false
    },
    handleSizeChange (pageSize) {
      const that = this
      that.dynamic = []
      that.pageSize = pageSize
      that.currentPage = 1
      that.getData(1)
    },
    handleCurrentChange (page) {
      const that = this
      that.currentPage = page
      that.getData(1)
    },
    getLog () {
      const that = this
      const __url = that.$store.state.total.set.child.log[1]
      if (__url === '') return false
      that.isAjax = true
      that.$ajax({
        url: __url,
        params: {
          page: that.currentPage,
          pageSize: that.pageSize
        }
      }).then(res => {
        that.isAjax = false
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data
          that.dynamic = []
          dat.forEach((e, i) => {
            that.dynamic.push({
              no: e.ROW_NUMBER,
              date: e.create_time === null || e.create_time === '' ? '无' : e.create_time.substring(0, 19),
              name: e.user_name,
              operate: e.mark
            })
          })
          that.totalNum = _data.total
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.isAjax = false
      })
    },
    getAccount () {
      const that = this
      const __url = that.$store.state.total.set.child.childList[1]
      if (__url === '') return false
      // that.isAjax = true;
      if (!that.accountInfo.length) {
        that.$ajax({
          url: __url
        }).then(res => {
          that.isAjax = false
          const _data = res.data
          if ((_data.errcode >> 0) === 0) {
            const dat = _data.data
            dat.forEach((e, i) => {
              that.accountInfo.push({
                no: i + 1,
                isAdmin: (e.is_admin >> 0) === 1,
                name: e.user_name,
                auth: (e.is_admin >> 0) === 1 ? '主管理员' : '子账号',
                id: e.id,
                state: e.state,
                del: false,
                forbid: false,
                isExpand: false,
                isAjax: false,
                allAuth: [],
                createTime: typeof e.create_time === 'string' ? e.create_time.substring(0, 19) : '无',
                updateTime: typeof e.update_time === 'string' ? e.update_time.substring(0, 19) : (typeof e.create_time === 'string' ? e.create_time.substring(0, 19) : '无')
              })
            })
          } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
            that.$parent.overTime()
          } else {
            that.$message.warning(_data.msg)
          }
        }, res => {
          that.isAjax = false
          that.$message.warning('网络似乎出了点问题')
        })
      }
    },
    getData (event) {
      const type = typeof event === 'number' ? event : (event.index >> 0)
      if (type === 1) {
        this.getLog()
      } else {
        this.getAccount()
      }
    },
    addAccount (form) {
      const that = this
      const __url = that.$store.state.total.set.child.childAdd[1]
      if (__url === '') return false
      if (that.info.auth === '') {
        that.$message.warning('至少选择一个权限')
        return false
      }
      that.$refs[form].validate((valid) => {
        if (valid) {
          that.$ajax({
            url: __url,
            method: 'post',
            data: {
              name: that.info.account,
              pwd: that.info.pwd,
              power: that.info.auth,
              role_id: that.info.id
            }
          }).then(res => {
            const _data = res.data
            if ((_data.errcode >> 0) === 0) {
              const dat = _data.data
              that.accountInfo.push({
                no: that.accountInfo.length + 1,
                name: dat.user_name,
                auth: dat.is_admin === '1' ? '主管理员' : '子账号',
                id: dat.id,
                state: '1',
                del: false,
                forbid: false,
                isExpand: false,
                isAjax: false,
                allAuth: []
              })
              that.$notify.success({
                title: '子账号创建成功',
                message: '请妥善保管信息',
                duration: 0,
                offset: 80
              })
            } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
              that.$parent.overTime()
            } else {
              that.$message.warning(_data.msg)
            }
          }, res => {
            that.$message.warning('网络似乎出了点问题')
          })
        }
      })
      that.$refs[form].resetFields()
    },
    getRoleList () {
      const that = this
      const __url = that.$store.state.total.set.child.roleList[1]
      if (__url === '') return false
      that.$ajax({
        url: __url
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data
          dat.forEach((e, i) => {
            that.authList.push({name: e.role_name, value: e.p_id, id: e.id})
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    }
  },
  mounted () {
    this.getData(0)
    this.getRoleList()
  }
}
</script>
<style>
    #authority .el-button--text{
        margin:0 8px;
    }
    #authority .el-checkbox+.el-checkbox{
        margin-left:10px;
    }
    #authority .text-red{
        color:#ff4949;
    }
    #authority .text-red:hover{
        opacity:0.7;
    }
    #authority .text-success{
        color:#13ce66;
    }
    #authority .text-success:hover{
        opacity:0.7;
    }
    #authority .text-warning{
        color:#f7ba2a;
    }
    #authority .text-warning:hover{
        opacity:0.7;
    }
    #authority .el-tabs__nav{
        width:100%;
    }
    #authority .el-tabs--border-card>.el-tabs__header .el-tabs__item{
        margin:0;
        width:50%;
        text-align:center;
        background:#eef1f6;
    }
    #authority .el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active{
        background: #20a1ff;
        color:#fff;
    }
    #authority .el-tabs{
        height:auto;
    }
    #authority .el-tabs .el-tabs__content{
        margin:0;
    }
    #authority .el-form{
        width:600px;
    }
    #authority .el-form .el-input{
        max-width:400px;
    }
    #authority .auth-set{
        padding:30px 0;
    }
    #authority .auth-set,#authority .auth-table{
        background:#fff;
    }
    #authority .auth-table{
        margin-top:30px;
    }
    #authority .el-table{
        margin:15px 0;
    }
    #authority .el-pagination{
        text-align:center;
    }
    #authority .table-expand {
        font-size: 0;
    }
    #authority .table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    #authority .table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 100%;
    }
    #authority .detail-container{
        min-height:30px;
    }
    #authority .el-button--small, .el-button--small.is-round{
        padding:4px 15px;
    }
</style>



