<template>
    <main id="more" class="container">
        <el-tabs type="border-card">
            <el-tab-pane label="待办事项">
                <el-table :data="backlog"
                          size="mini"
                          stripe
                          style="width:100%"
                          empty-text="功能开发中，敬请期待！">
                    <el-table-column prop="date" label="日期" min-width="180">
                    </el-table-column>
                    <el-table-column prop="content" label="内容" min-width="400">
                    </el-table-column>
                    <el-table-column label="操作" min-width="180">
                        <template slot-scope=" scope">
                            <label class="go" @click="solve()">前去处理</label>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane label="通知消息">
                <el-table :data="notice" stripe style="width:100%"  empty-text="功能开发中，敬请期待！">
                    <el-table-column prop="date" label="日期" min-width="180">
                    </el-table-column>
                    <el-table-column prop="content" label="内容" min-width="400">
                    </el-table-column>
                </el-table>
            </el-tab-pane>
        </el-tabs>
    </main>
</template>
<script>
export default {
  data () {
    return {
      backlog: [],
      notice: []
    }
  },
  methods: {
    solve () {
      this.$message('功能开发中，敬请期待')
    }
  },
  beforeCreate () {
  }
}
</script>
<style>
    #more .el-tabs__nav{
        float:inherit;
    }
    #more .el-tabs--border-card>.el-tabs__header .el-tabs__item {
        margin: 0;
        width: 50%;
        text-align: center;
        background: #eef1f6;
    }
    #more .el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active{
        background: #20a1ff;
        color:#fff;
    }
    #more .go{
        padding:10px 100px 10px 0;
        color: #20a0ff;
        cursor:pointer;
        opacity:1;
    }
    #more .go:hover{
        color:#20a0ff;
        opacity:0.8;
    }
    #more .container .el-tabs__nav{
        width:100%;
    }
    #more .container .el-tabs--border-card>.el-tabs__header .el-tabs__item{
        margin:0;
        width:50%;
        text-align:center;
    }
    #more .container .el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active{
        background: #20a1ff;
        color:#fff;
    }
   /* .el-table_1_column_1{
        color:#99a9bf;
    }*/
</style>



