      '收支明细': {auth: false, list: [40, 43, 47]},

      '图': {auth: false, list: [998]},

      '权限设置': {auth: false, list: [5, 6, 7, 8, 9, 39, 55]},
      '费用设置': {auth: false, list: [38]},
      '绑定云比赛': {auth: false, list: [17, 18, 19, 67]}
