<!-- 赛事审核 -->
<template>
    <main id="plan" class="container">
        <section class="search-area" @keyup.enter="search">
            <el-input class="search-group" v-model="key.keyword" placeholder="请输入赛事名称">
                <el-button slot="append" icon="search" @click="search">查询</el-button>
            </el-input>
            <el-button class="add-new" type="success" v-if="$store.state.orgLevel===1" @click="addEvent">预发布赛事</el-button>
            <div class="date-area">
                <span>比赛开始时间范围：</span>
                <el-date-picker
                        v-model="key.date"
                        type="daterange"
                        align="left"
                        placeholder="选择日期范围"
                        @change="formatTime"
                        :picker-options="pickerOptions">
                </el-date-picker>
            </div>
        </section>
        <section class="event-area">
            <el-tabs v-model="activeName" type="card" @tab-click="switchTab">
                <el-tab-pane class="plan" label="全部" name="first">
                    <p style="color:#999;margin-top:50px;" class="text-center" v-if="currentData.length===0">暂无数据</p>
                    <div v-for="item in currentData"
                         :key="item"
                         class="games">
                        <p class="title">
                            {{item.name}}
                            <span v-if="item.eventId">（云比赛赛事名：{{item.lswlName}}）</span></p>
                        <div class="game-info">
                            <i class="fa fa-building-o"></i>
                            <span>{{item.rank}}</span>
                            <i class="fa fa-bank"></i>
                            <span> 已申请机构数：{{item.isPlan===1?item.org:1}}</span>
                            <i class="fa fa-cny text-red" v-if="item.money"></i>
                            <span class="text-red" v-if="item.money">保证金：{{item.money}} 元</span>
                            <el-tag :type="item.isPlan===1?'primary':'warning'">{{item.isPlan===1?'省级计划':'非省级计划'}}</el-tag>
                            <el-tag v-if="item.checkState===2"
                                    type="success">已授权给：{{item.orgName}}</el-tag>
                            <el-tag v-if="item.checkState===1"
                                    type="danger">已拒绝</el-tag>
                            <el-tag v-if="item.checkState===0 && item.checkName !==''"
                                    type="info">当前审核机构：{{item.checkName}}</el-tag>
                            <el-button class="text-center GoGoGo"
                                       type="info"
                                       size="small"
                                       v-if="item.eventId"
                                       @click="getSSid(item.eventId)">进入赛事</el-button>
                            <el-button class="text-center"
                                       type="success"
                                       size="small"
                                       @click="solve(item.id)">{{$store.state.orgLevel === 1 ? '授 权' :'审 核'}}</el-button>
                            <el-button v-if="item.checkState!==2&&item.isPlan===1&&$store.state.orgLevel===1"
                                       class="text-center btn-edit"
                                       type="primary"
                                       size="small"
                                       @click="edit(item.id)">编 辑</el-button>
                            <el-button v-if="item.checkState!==2&&item.isPlan===1&&$store.state.orgLevel===1"
                                       class="text-center btn-del"
                                       type="danger"
                                       size="small"
                                       @click="del(item.id,item.name)">删 除</el-button>
                        </div>
                        <div class="game-info">
                            <i class="fa fa-bullseye" v-if="item.applyTime[0]"></i>
                            <span v-if="item.applyTime[0]">申请时间：{{showTime(item.applyTime[0])+' 至 '+showTime(item.applyTime[1])}}</span>
                            <i class="fa fa-clock-o" v-if="item.gameTime[0]"></i>
                            <span v-if="item.gameTime[0]">比赛时间：{{showTime(item.gameTime[0])+' 至 '+showTime(item.gameTime[1])}}</span>
                        </div>
                        <p class="intro">{{item.intro}}</p>
                    </div>
                </el-tab-pane>
                <el-tab-pane class="plan"
                             label="省级计划"
                             name="second"
                             v-if="$store.state.orgLevel === 1">
                    <p style="color:#999;margin-top:50px;" class="text-center"
                       v-if="currentData.length===0">暂无数据</p>
                    <div v-for="item in currentData"
                         :key="item"
                         class="games">
                        <p class="title">
                            {{item.name}}
                            <span v-if="item.eventId">（云比赛赛事名：{{item.lswlName}}）</span>
                        </p>
                        <div class="game-info">
                            <i class="fa fa-building-o"></i>
                            <span>{{item.rank}}</span>
                            <i class="fa fa-bank"></i>
                            <span> 已申请机构数：{{item.org}}</span>
                            <i class="fa fa-cny text-red" v-if="item.money"></i>
                            <span class="text-red" v-if="item.money">保证金：{{item.money}} 元</span>
                            <el-tag :type="item.isPlan===1?'primary':'warning'">{{item.isPlan===1?'省级计划':'非省级计划'}}</el-tag>
                            <el-tag v-if="item.checkState===2"
                                    type="success">已授权给：{{item.orgName}}</el-tag>
                            <el-tag v-if="item.checkState===1"
                                    type="danger">已拒绝</el-tag>
                            <el-tag v-if="item.checkState===0 && item.checkName !==''"
                                    type="info">当前审核机构：{{item.checkName}}</el-tag>
                            <el-button class="text-center GoGoGo"
                                       type="info"
                                       size="small"
                                       v-if="item.eventId"
                                       @click="getSSid(item.eventId)">进入赛事</el-button>
                            <el-button class="text-center"
                                       type="success"
                                       size="small"
                                       @click="solve(item.id)">{{$store.state.orgLevel === 1 ? '授 权' :'审 核'}}</el-button>
                            <el-button v-if="item.checkState!==2&&item.isPlan===1&&$store.state.orgLevel===1"
                                       class="text-center btn-edit"
                                       type="primary"
                                       size="small"
                                       @click="edit(item.id)">编 辑</el-button>
                            <el-button v-if="item.checkState!==2&&item.isPlan===1&&$store.state.orgLevel===1"
                                       class="text-center btn-del"
                                       type="danger"
                                       size="small"
                                       @click="del(item.id,item.name)">删 除</el-button>
                        </div>
                        <div class="game-info">
                            <i class="fa fa-bullseye" v-if="item.applyTime[0]"></i>
                            <span v-if="item.applyTime[0]">申请时间：{{showTime(item.applyTime[0])+' 至 '+showTime(item.applyTime[1])}}</span>
                            <i class="fa fa-clock-o" v-if="item.gameTime[0]"></i>
                            <span v-if="item.gameTime[0]">比赛时间：{{showTime(item.gameTime[0])+' 至 '+showTime(item.gameTime[1])}}</span>
                        </div>
                        <p class="intro">{{item.intro}}</p>
                    </div>
                </el-tab-pane>
                <el-tab-pane class="plan"
                             label="非省级计划"
                             v-if="$store.state.orgLevel === 1"
                             name="third">
                    <p style="color:#999;margin-top:50px;" class="text-center"
                       v-if="currentData.length===0">暂无数据</p>
                    <div v-for="item in currentData"
                         :key="item"
                         class="games">
                        <p class="title">
                            {{item.name}}
                            <span v-if="item.eventId">
                                （云比赛赛事名：{{item.lswlName}}）</span>
                        </p>
                        <div class="game-info">
                            <i class="fa fa-building-o"></i>
                            <span>{{item.rank}}</span>
                            <i class="fa fa-bank"></i>
                            <span> 已申请机构数：1</span>
                            <i class="fa fa-cny text-red"
                               v-if="item.money"></i>
                            <span class="text-red"
                                  v-if="item.money">保证金：{{item.money}} 元</span>
                            <el-tag :type="item.isPlan===1?'primary':'warning'">{{item.isPlan===1?'省级计划':'非省级计划'}}</el-tag>
                            <el-tag v-if="item.checkState===2" type="success">已授权给：{{item.orgName}}</el-tag>
                            <el-tag v-if="item.checkState===1" type="danger">已拒绝</el-tag>
                            <el-tag v-if="item.checkState===0 && item.checkName !==''" type="info">当前审核机构：{{item.checkName}}</el-tag>
                            <el-button class="text-center GoGoGo"
                                       type="info"
                                       size="small"
                                       v-if="item.eventId"
                                       @click="getSSid(item.eventId)">进入赛事</el-button>
                            <el-button class="text-center"
                                       type="success"
                                       size="small" @click="solve(item.id)">{{$store.state.orgLevel === 1 ? '授 权' :'审 核'}}</el-button>
                            <el-button v-if="item.checkState!==2&&item.isPlan===1&&$store.state.orgLevel===1"
                                       class="text-center btn-edit"
                                       type="primary"
                                       size="small"
                                       @click="edit(item.id)">编 辑</el-button>
                            <el-button v-if="item.checkState!==2&&item.isPlan===1&&$store.state.orgLevel===1"
                                       class="text-center btn-del"
                                       type="danger"
                                       size="small"
                                       @click="del(item.id,item.name)">删 除</el-button>
                        </div>
                        <div class="game-info">
                            <i class="fa fa-bullseye" v-if="item.applyTime[0]"></i>
                            <span v-if="item.applyTime[0]">申请时间：{{showTime(item.applyTime[0])+' 至 '+showTime(item.applyTime[1])}}</span>
                            <i class="fa fa-clock-o" v-if="item.gameTime[0]"></i>
                            <span v-if="item.gameTime[0]">比赛时间：{{showTime(item.gameTime[0])+' 至 '+showTime(item.gameTime[1])}}</span>
                        </div>
                        <p class="intro">{{item.intro}}</p>
                    </div>
                </el-tab-pane>
            </el-tabs>
            <div class="text-center pagination">
                <el-pagination @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[6, 10, 20, 50]"
                               :page-size="pageSize"
                               layout="total,sizes, prev, pager, next, jumper"
                               :total="totalNum">
                </el-pagination>
            </div>
            <el-dialog title="删除赛事"
                       width="30%"
                       :visible.sync="delVis">
                <p>是否确认删除赛事 <span class="text-blue"> {{delName}}</span> ？</p>
                <div style="height:10px"></div>
                <p class="tip">提示：一旦删除无法恢复！</p>
                <p class="tip">已授权或有未处理信息时无法删除。</p>
                <div style="height:10px"></div>
                <div class="text-center">
                    <el-button size="small" @click="delVis = false">取 消</el-button>
                    <el-button type="primary" size="small" @click="confirmDel">确 认</el-button>
                </div>
            </el-dialog>
            <el-dialog :title=" rules.name+' - 赛事规程'"
                       :visible.sync="rulesVis">
                <div class="text-right">
                    <el-button type="primary" size="small" @click="getSSid(rules.id)">进入赛事</el-button>
                </div>
                <div style="height:10px"></div>
                <div v-html="rulesContent" v-if="rulesContent">{{rulesContent}}</div>
                <div v-if="!rulesContent" style="font-size:14px" class="text-center">暂未发布赛事规程</div>
            </el-dialog>
            <el-dialog title="绑定云比赛账号"
                       width="30%"
                       :visible.sync="bindVis">
                <p>
                    暂未绑定云比赛账号，无法使用赛事服务。
                </p>
                <p class="tip" v-if="!$store.state.total.set.auth">提示：你的账号无此权限，请使用主管理员账号绑定。</p>
                <div style="height:10px"></div>
                <div class="text-center">
                    <el-button size="small" @click="bindVis = false">取 消</el-button>
                    <el-button type="primary" size="small" @click="goToBind" v-if="$store.state.total.set.auth">前往绑定</el-button>
                </div>
            </el-dialog>
        </section>
    </main>
</template>
<script>
export default{
  data(){
    const that = this;
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      key:{
        keyword:'',
        date:[],
        type:''
      },
      activeName:'first',
      opText:'',
                pageSize:that.$parent.getCookie('planPageSize') !== '' ? (that.$parent.getCookie('planPageSize')>>0) : 6,
                currentPage:1,
                currentData:[],
                totalNum:0,
                isBoss:'',
                rulesVis:false,
                delVis:false,
                bindVis:false,
                editId:'',
                delName:'',
                rulesContent:'',
                rules:{
                    id:'',
                    name:''
                }
            }
        },
        methods: {
            goToBind(){
                const that = this;
                if(!that.$store.state.total.set.auth) return false;
                that.$router.push('/Main/setting3');
                that.$parent.addTab('绑定云比赛');
            },
            showGameRules(eid,name){
                const that = this;
                const _url = that.$store.state.gameRuleURL + eid;// 2028 有内网数据
                that.rules.id = eid;
                that.rules.name = name;
                that.$ajax({
                    url:_url
                }).then( res => {
                    const _data = res.data;
                    if( _data.error === 0 ){
                        that.rulesVis = true;
                        that.rulesContent = _data.datArr.rules;
                    }
                }, res => {
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            getSSid(eid){
                const that = this;
                const __url = that.$store.state.getSSid;
                let ssid;
                if( __url ==='' ) return false;
                that.$ajax({
                    url:__url
                }).then(res=>{
                    const _data = res.data;
                    if( (_data.errcode>>0) === 0 ){
                        ssid = _data.data.ssid;
                        that.toYunbisai(ssid,eid);
                    } else {
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            toYunbisai(ssid,eid){
                const that = this;
                const __url = that.$store.state.toYunbisai;
                if( __url ==='' ) return false;
                that.$parent.openNewTag();
                window.open(that.$store.state.baseURL + __url + '?ssid=' + ssid + '&event_id=' + eid + '&type=1');
            },
            edit(id){
                const that = this;
                that.$parent.addTab('编辑赛事');
                that.$router.push('/Main/planEdit?'+id);
            },
            del(id,name){
                const that = this;
                that.delVis = true;
                that.delName = name;
                that.editId = id;
                that.delVis = true;
            },
            confirmDel(){
                const that = this;
                const __url = that.$store.state.total.event.child.eventDel[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    params:{
                        id:that.editId
                    }
                }).then(res=>{
                    const _data = res.data;
                    that.delVis = false;
                    if((_data.errcode>>0)===0){
                        that.$notify.success('删除成功');
                        that.getData();
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    } else {
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.delVis = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            showTime(e){
                if( e!==null){
                    return e.substring(0,10);
                } else {
                    return false;
                }
            },
            getOpText(isPlan,level,state){
                const str = isPlan + '' + level + state;
                switch(str){
                    case '010':
                    case '110':
                    case '111':
                        return '授 权';
                    case '121':
                    case '120':
                        return '立即申请';
                }
                return '查看详情';
            },
            formatTime(e){
                const ttt = (t,type)=>{
                    return t.getFullYear() + '-' + (t.getMonth() + 1) + '-' + t.getDate() + ' ' +( type === 'start' ? '00:00:00' : '23:59:59');
                };
                if( e !== '' && e !== null && typeof e !== 'undefined' && e !== undefined ){
                    this.key.date[0] = ttt(e[0],'start');
                    this.key.date[1] = ttt(e[1],'end');
                } else {
                    this.key.date = ['',''];
                }
                this.getData();
            },
            getRange(num){
                if(num===58) return '全国';
                if(num===59) return '省级';
                if(num===60) return '县市级';
                if(num===61) return '地市级';
            },
            handleSizeChange(pageSize){
                document.cookie = 'planPageSize='+pageSize;
                let that = this;
                that.currentData=[];
                that.pageSize = pageSize;
                that.currentPage = 1;
                that.getData();
            },
            handleCurrentChange(page){
                let that = this;
                that.currentPage = page;
                that.getData();
            },
            addEvent(){
                let root = this.$parent;
                root.addTab('预发布赛事',root);
                this.$router.push('/Main/planAdd');
            },
            solve(id){
                let that = this;
                that.$parent.addTab('授权赛事');
                that.$router.push('/Main/planApply?'+id);
            },
            getData(){
                const that = this;
                const __url =that.$store.state.total.event.child.planList[1];
                if(__url==='') return false;
                if(that.key.date === null || typeof that.key.date === 'undefined'){
                    that.key.date = ['',''];
                }

                let par = {
                    type:'wait',
                    is_plan:that.key.type,
                    page:that.currentPage,
                    pageSize:that.pageSize,
                    keywords:that.key.keyword,
                    start:that.key.date[0],
                    end:that.key.date[1]
                };
                for(let i in par){
                    if( typeof par[i] === 'undefined' || par[i] === ''){
                        delete par[i];
                    }
                }
                that.$ajax({
                    url:__url,
                    params:par
                }).then(res=>{
                   const _data = res.data;
                   if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.currentData = [];
                        dat.forEach((e,i)=>{
                            that.currentData.push({
                                id:e.id,
                                name:e.title,
                                rank:that.getRange(e.scale>>0),
                                money:e.bail===null?0:(e.bail-0).toFixed(2),
                                isPlan:(e.is_plan>>0),
                                checkState:(e.check_state>>0),
                                gameTime:[e.game_start,e.game_end],
                                applyTime:[e.apply_start,e.apply_end],
                                intro:e.condition,
                                org:e.num===null?0:e.num,
                                lswlName:e.lswl_event_title,
                                eventId:e.event_id,
                                op:that.getOpText(e.is_plan>>0,that.$store.state.orgLevel,e.check_state>>0),
                                orgName:e.org_name,
                                checkName:e.check_org_name === null ? '' : e.check_org_name,
                            });
                        });
                       that.totalNum = _data.total;
                   }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                   }else if( (_data.errcode>>0) === 1700 ){
                       that.bindVis = true;
                   } else {
                        that.$message.warning(_data.msg);
                   }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            switchTab(e){
                const that =this;
                if(e.index==='0'){
                    that.key.type = '';
                }else if(e.index==='1'){
                    that.key.type = '1';
                }else if(e.index==='2'){
                    that.key.type = '0';
                }
                that.getData();
            },
            search(){
                this.getData();
            }
        },
        mounted(){
            const that = this;
            that.isBoss = that.$store.state.orgLevel===1;

            if(!that.isBoss) that.opText = '前往申请';

            if(that.$store.state.orgLevel){
                that.getData();
            } else {
                setTimeout(()=>{
                    that.getData();
                },1000);
            }
        }
    }
</script>
<style>
    #plan .text-right{
        text-align:right;
    }
    #plan .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #plan .games .game-info button.GoGoGo{
        top:90px;
    }
    #plan .tip{
        color:#8492A6;
        font-size:12px;
    }
    #plan .text-blue{
        color:#20a0ff;
        font-size:16px;
    }
    #plan .date-area{
        margin-top:20px;
        color:#8492A6;
        font-size:14px;
    }
    #plan .plan{
        width:100%;
        min-height:550px;
    }
    #plan .text-red{
        color:#ff4949;
    }
    #plan .pagination{
        padding-bottom:20px;
    }
    #plan .games{
        padding:6px 20px;
        margin-bottom:10px;
        position: relative;
    }
    #plan .games .intro{
        font-size:14px;
        color:#999;
        width: 90%;
        max-width: 1034px;
        margin:10px 0 15px 0;
    }
    #plan .games .game-info{
        font-size:14px;
        margin:12px 0;
    }
    #plan .games .game-info .fa{
        margin-right:8px;
    }
    #plan .games .game-info button{
        position: absolute;
        right:40px;
        top:50px;
    }
    #plan .games .game-info .btn-edit{
        top:90px;
    }
    #plan .games .game-info .btn-del{
        top:130px;
    }
    #plan .games .game-info span{
        margin-right:30px;
    }
    #plan .games .game-info .el-button span{
        margin-right:0;
    }
    #plan .games .title{
        color:#20a0ff;
        margin:10px 0;
        text-align:left;
    }
    #plan .games .title .fa{
        font-size:22px;
        margin-left:8px;
    }
    #plan .el-icon-more {
        margin-left:14px;
    }
    #plan .search-area .add-new{
        margin-left:20px;
    }
    #plan .search-area .search-group{
        width:80%;
    }
    #plan .event-area{
        margin-top:20px;
        background:#fff;
    }
    #plan .games{
        border:1px solid #D3DCE6;
    }
    #plan .el-tabs--card>.el-tabs__header .el-tabs__item.is-active{
        -webkit-border-radius:0;
        -moz-border-radius:0;
        border-radius:0;
        border-top:2px solid #20a0ff;
    }
    #plan .el-tabs__item{
        box-sizing:content-box;
    }
    #plan .el-tabs__nav-scroll{
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        background:transparent;
    }
    #plan .games .game-info .el-tag{
        margin-right:10px;
    }
</style>


