<!-- 转会列表 -->
<template>
    <main id="player"
          class="container">
        <section class="search-area"
                 @keyup.enter="search">
            <el-input class="search-group"
                      v-model="input"
                      placeholder="请输入棋手证件号">
                <el-button slot="append"
                           icon="search"
                           @click="search"
                           :disabled="isSearch"
                           :loading="isSearch">查询</el-button>
            </el-input>
            <el-button class="add-new"
                       type="success"
                       @click="addNewPlayer($event)"
                       v-if="$store.state.total.mem.child.memTransfer[1]">转会申请</el-button>
        </section>
        <section class="player-list">
            <el-tabs v-model="select"
                     type="card"
                     @tab-click="switchTab">
                <el-tab-pane class="plan"
                             label="转出会员"
                             name="first">
                    <el-table :data="currentData_0"
                              size="mini"
                              stripe>
                        <el-table-column prop="no"
                                         label="会员信息"
                                         min-width="170">
                            <template slot-scope=" scope">
                                <p>{{scope.row.name}}（{{scope.row.rank}}）</p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="no"
                                         label="证件号"
                                         min-width="170">
                            <template slot-scope=" scope">
                                <p>{{scope.row.number}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="no"
                                         label="转入机构"
                                         min-width="170">
                            <template slot-scope=" scope">
                                <p>{{scope.row.orgIn}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="no"
                                         label="申请时间"
                                         min-width="130">
                            <template slot-scope=" scope">
                                <p>{{scope.row.time}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="isOver"
                                         label="状态"
                                         min-width="80">
                            <template slot-scope=" scope">
                                <el-tag v-if="scope.row.state===1"
                                        size="small"
                                        type="primary">待处理</el-tag>
                                <el-tag v-if="scope.row.state===2"
                                        size="small"
                                        type="success">已同意</el-tag>
                                <el-tag v-if="scope.row.state===4"
                                        size="small"
                                        type="danger">已拒绝</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作"
                                         min-width="90">
                            <template slot-scope=" scope">
                                <el-button type="text"
                                           size="mini"
                                           class="text-blue"
                                           @click="solve(scope.row.state,scope.row.id)">处理</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane class="plan"
                             label="转入会员"
                             name="second">
                    <el-table :data="currentData_1"
                              size="mini"
                              stripe>
                        <el-table-column label="会员信息"
                                         min-width="170">
                            <template slot-scope=" scope">
                                <p>{{scope.row.name}}（{{scope.row.rank}}）</p>
                            </template>
                        </el-table-column>
                        <el-table-column label="证件号"
                                         min-width="170">
                            <template slot-scope=" scope">
                                <p>{{scope.row.number}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column label="转出机构"
                                         min-width="170">
                            <template slot-scope=" scope">
                                <p>{{scope.row.orgOut}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column label="申请时间"
                                         min-width="130">
                            <template slot-scope=" scope">
                                <p>{{scope.row.time}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="isOver"
                                         label="状态"
                                         min-width="80">
                            <template slot-scope=" scope">
                                <el-tag v-if="scope.row.state===1"
                                        size="small"
                                        type="primary">待处理</el-tag>
                                <el-tag v-if="scope.row.state===2"
                                        size="small"
                                        type="success">已同意</el-tag>
                                <el-tag v-if="scope.row.state===4"
                                        size="small"
                                        type="danger">已拒绝</el-tag>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentPage"
                           :page-sizes="[20, 50, 100]"
                           :page-size="pageSize"
                           layout="total,sizes, prev, pager, next, jumper"
                           :total="totalNum">
            </el-pagination>
        </section>
        <!--支付-->
        <el-dialog title="选择支付方式"
                   width="30%"
                   :visible.sync="payVis">
            <div class="type-area">
                <div class="ali"
                     @click="$parent.Pay('ali',orderID,'/Main/player?pay')">
                    <img src="./../../assets/img/alipay.png"
                         alt="ali">
                    <p>支付宝</p>
                </div>
                <div class="wechat"
                     @click="$parent.Pay('wx',orderID,'/Main/player?pay')">
                    <img src="./../../assets/img/wechat.png"
                         alt="wechat">
                    <p>微信</p>
                </div>
                <div class="wallet"
                     v-if="0">
                    <img src="./../../assets/img/walletpay.png"
                         alt="wallet">
                    <p>钱包余额</p>
                </div>
            </div>
            <p class="tip">提示：支付注册费后，即可完成会员的注册。</p>
        </el-dialog>
        <el-dialog title="申请会员转入"
                   width="30%"
                   :visible.sync="transferVis">
            <p>
                申请将会员
                <span style="color:#20a0ff;font-size: 16px">{{transferInfo.name}}</span>
                <span class="tip">（注册证号：{{transferInfo.ID}}）</span>
                转入
            </p>
            <el-form size="middle"
                     ref="formInfo"
                     :model="formInfo"
                     label-width="100px"
                     :rules="rules"
                     class="transfer-form">
                <el-form-item prop="reason"
                              label="申请理由">
                    <el-input v-model="formInfo.reason"></el-input>
                </el-form-item>
                <el-form-item prop="imgUrl"
                              label="相关照片">
                    <el-upload
                            :action="this.$store.state.baseURL+this.$store.state.uploadImg"
                            :headers="{token:$parent.getToken()}"
                            list-type="picture-card"
                            :file-list="formInfo.imgUrl"
                            :on-success="saveLicenseURL"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="removeLicenseURL">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                </el-form-item>
            </el-form>
            <div class="text-center"
                 style="margin-top: 10px">
                <el-button size="small"
                           @click="transferVis = false">取消</el-button>
                <el-button size="small"
                           type="primary">提交</el-button>
            </div>
        </el-dialog>
        <el-dialog  width="30%"
                    :visible.sync="dialogVisible">
            <img width="100%"
                 :src="dialogImageUrl"
                 alt="">
        </el-dialog>
        <el-dialog :visible.sync="solveVis"
                   width="30%"
                   title="处理转会请求">
            <p style="margin-bottom: 10px">理由（同意时可不填）：</p>
            <el-input v-model="reason"></el-input>
            <div class="text-center"
                 style="margin-top:20px">
                <el-button size="small"
                           type="danger"
                           @click="confirmSolve(4)">拒绝</el-button>
                <el-button size="small"
                           type="primary"
                           @click="confirmSolve(2)">同意</el-button>
            </div>
        </el-dialog>
    </main>
</template>
<script>
export default{
  data () {
    const that = this
    let hei = 0
    let win = window.outerWidth
    if (win > 1366) {
      hei = 610
    } else if (win <= 1366 && win >= 1024) {
      hei = 450
    } else {
      hei = 300
    }
    let checkID = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入注册证号'))
      } else {
        let res = this.$parent.checkID(value)
        if (!res[0]) {
          callback(new Error(res[1]))
        } else {
          that.getAge(this.memberInfo.ID)
          callback()
        }
      }
    }
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      formInfo: {
        memId: '',
        orgId: '',
        reason: '',
        imgUrl: [],
        img: ''
      },
      rules: {
        reason: [
          {required: true, message: '请输入原因', trigger: 'blur'}
        ]
      },
      isSearch: false,
      dialogFormVisible: false,
      freezeVisible: false,
      input: '',
      select: 'first',
      selectVal: '0',
      currentPage: 1,
      pageSize: that.$parent.getCookie('playerPageSize') !== '' ? (that.$parent.getCookie('playerPageSize') >> 0) : 20,
      totalNum: 0,
      money: 0,
      fee: 0,
      orderID: '',
      transferVis: false,
      payVis: false,
      solveVis: false,
      reason: '',
      solveId: '',
      transferInfo: {
        name: '',
        id: '',
        ID: ''
      },
      multipleSelection: [],
      systemTime: '',
      currentFreeze: {},
      currentData_0: [],
      currentData_1: [],
      tableHei: hei,
      memberInfo: {
        name: '',
        ID: '',
        rank: '',
        isFee: '',
        pic: ''
      },
      gotAge: '',
      gotGender: 1,
      checkRules: {
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'}
        ],
        ID: [
          {required: true, validator: checkID, trigger: 'blur'}
        ]
      },
      optionGroups: [
        {
          label: '级别',
          options: [
            {value: 143, label: '28级'},
            {value: 142, label: '27级'},
            {value: 141, label: '26级'},
            {value: 99, label: '25级'},
            {value: 103, label: '24级'},
            {value: 104, label: '23级'},
            {value: 106, label: '22级'},
            {value: 107, label: '21级'},
            {value: 108, label: '20级'},
            {value: 109, label: '19级'},
            {value: 110, label: '18级'},
            {value: 111, label: '17级'},
            {value: 112, label: '16级'},
            {value: 113, label: '15级'},
            {value: 114, label: '14级'},
            {value: 115, label: '13级'},
            {value: 116, label: '12级'},
            {value: 117, label: '11级'},
            {value: 118, label: '10级'},
            {value: 119, label: '9级'},
            {value: 120, label: '8级'},
            {value: 121, label: '7级'},
            {value: 122, label: '6级'},
            {value: 123, label: '5级'},
            {value: 124, label: '4级'},
            {value: 125, label: '3级'},
            {value: 126, label: '2级'},
            {value: 127, label: '1级'}
          ]
        },
        {
          label: '业余段位',
          options: [
            {value: 128, label: '1段'},
            {value: 129, label: '2段'},
            {value: 130, label: '3段'},
            {value: 131, label: '4段'},
            {value: 132, label: '5段'},
            {value: 133, label: '6段'}
          ]
        }
      ]
    }
  },
  filters: {
    showNum (value) {
      return value.toString().split('').reverse().join('').replace(/(\d{3})/g, '$1,').replace(/,$/, '').split('').reverse().join('')
    }
  },
  methods: {
    getCardTypeName (num) {
      switch (num >> 0) {
        case 1:
          return '身份证'
        case 2:
          return '护照'
        case 4:
          return '台胞证'
        case 8:
          return '港澳通行证'
      }
    },
    switchTab (e) {
      const that = this
      that.selectVal = e.index
      that.getData()
    },
    solve (state, id) {
      if ((state >> 0) !== 1) {
        this.$message.warning('已处理')
        return false
      }
      this.solveVis = true
      this.solveId = id
    },
    confirmSolve (param) {
      const that = this
      const __url = that.$store.state.total.mem.child.updateTransfer[1]
      if (__url === '') return false
      if (param === 4 && that.reason === '') {
        that.$message.warning('请填写拒绝理由')
        return false
      }
      that.$ajax({
        url: __url,
        method: 'post',
        data: {
          guid: that.solveId,
          transfer_state: param,
          reason: that.reason
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.$message.success(_data.msg)
          that.solveVis = false
          that.reason = ''
          that.getData()
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
          that.solveVis = false
          that.reason = ''
          that.getData()
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    saveLicenseURL (res, file, fileList) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        that.formInfo.imgUrl = fileList
        if (that.formInfo.img === '') {
          that.formInfo.img = res.data
        } else {
          that.formInfo.img += ',' + res.data
        }
      } else if ((res.errcode >> 0) === 1000 || (res.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(res.msg)
      }
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    removeLicenseURL (file, fileList) {
      this.formInfo.img = this.removeURL(fileList)
    },
    removeURL (arr) {
      let _url = ''
      arr.forEach((e, i) => {
        if (e.url.includes('blob')) {
          if (i) _url += ',' + e.response.data
          else _url += e.response.data
        } else {
          const ee = e.url.substring(e.url.indexOf('uploads'))
          if (i) _url += ','+ ee
          else _url += ee
        }
      })
      return _url
    },
    getRank (rank) {
      rank = rank  >> 0
      this.optionGroups.forEach((e, i) => {
        e.options.forEach((e, i) => {
          if (e.value === rank) {
            rank = e.label
            return false
          }
        })
      })
      return rank
    },
    watch (id, name, ID, orgId) {
      const that = this
      that.transferInfo.name = name
      that.transferInfo.ID = ID
      that.transferInfo.orgId = ID
      that.transferInfo.id = id
      that.transferVis = true
      that.formInfo.memId = id
      that.formInfo.orgId = orgId
    },
    search () {
      const that = this
      that.currentPage = 1
      that.getData()
    },
    handleSizeChange (pageSize) {
      document.cookie = 'playerPageSize=' + pageSize
      let that = this
      that.currentData_0 = []
      that.pageSize = pageSize
      that.currentPage = 1
      that.getData()
    },
    handleCurrentChange (page) {
      let that = this
      that.currentPage = page
      that.getData()
    },
    getData () {
      const that = this
      const __url = that.$store.state.total.mem.child.transferList[1]
      if (__url === '') return false
      that.$ajax({
        url: __url,
        method: 'get',
        params: {
          type: that.selectVal,
          pageSize: that.pageSize,
          page: that.currentPage,
          keywords: that.input
        }
      }).then(res => {
        const _data = res.data
        const dat = _data.data
        if ((_data.errcode >> 0) === 0) {
          if (dat !== null) {
            if (that.selectVal === '0') {
              that.currentData_0 = []
              dat.forEach((e, i) => {
                that.currentData_0.push({
                  id: e.transfer_guid,
                  no: e.ROW_NUMBER,
                  number: e.idCard,
                  cardType: that.getCardTypeName(e.cardType >> 0),
                  name: e.mem_name,
                  rank: that.getRank(e.level),
                  orgIn: e.transfer_in_org_name,
                  orgOut: e.transfer_out_org_name,
                  time: e.create_time.substring(0,16),
                  state: e.transfer_state >> 0
                });
              });
            }
            if (that.selectVal==='1') {
              that.currentData_1 = []
              dat.forEach((e, i) => {
                that.currentData_1.push({
                  id: e.transfer_guid,
                  no: e.ROW_NUMBER,
                  cardType: that.getCardTypeName(e.cardType >> 0),
                  number: e.idCard,
                  name: e.mem_name,
                  rank: that.getRank(e.level),
                  orgIn: e.transfer_in_org_name,
                  orgOut: e.transfer_out_org_name,
                  time: e.create_time.substring(0,16),
                  state: e.transfer_state >> 0
                })
              })
            }
          }
          that.totalNum = (_data.total >> 0)
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message({message: '网络开小差了', type: 'warning'})
      })
    },
    addNewPlayer () {
      let root = this.$parent
      root.addTab('转会申请', root)
      this.$router.push('/Main/transferPlayer')
    }
  },
  mounted () {
    const that = this
    const href = window.location.href;
    if (href.includes('?out')) {
      that.selectVal = '1'
      that.select = 'second'
    }
    that.getData()
  }
}
</script>
<style>
    #player .text-success{
        color:#13ce66;
    }
    #player .money{
        margin-left:24px;
        font-size: 18px;
    }
    #player .text-success:hover{
        opacity: 0.7;
    }
    #player .text-warning{
        color:#f7ba2a;
    }
    #player .text-red{
        color:#ff4949;
    }
    #player .upload-img{
        width: 100%;
    }
    #player .player-disabled{
        color:#999;
    }
    #player .tip{
        color:#8492A6;
        font-size: 12px;
        margin:4px 0;
        text-align:right;
    }
    #player .text-warning:hover{
        opacity: 0.7;
    }
    #player .el-button--text.text-red span{
        color:#ff4949;
    }
    #player .el-button--text.text-red:hover span{
        opacity: 0.7;
    }
    #player .transfer-form{
        margin-top:20px;
    }
    #player .popper__arrow{
        left:43px !important;
    }
    #player .el-popover{
        min-width:70px;
        width:70px !important;
    }
    #player .watch{
        padding: 0;
        color: #20a0ff;
        cursor:pointer;
        opacity: 1;
    }
    #player .watch:hover{
        color:#20a0ff;
        opacity: 0.8;
    }
    #player .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #player .el-icon-more {
        margin-left: 14px;
    }
    /*.search-area .el-input-group__append button.el-button{
        color:#fff;
        background-color:#20a0ff;
        border-color:#20a0ff;
        border-radius: 0;
    }*/
    #player .search-area .add-new{
        margin-left:20px;
    }
    #player .search-area .search-group{
        width:80%;
    }
    #player .player-list{
        margin-top:20px;
        padding: 0;
    }
    #player .player-list .player-num{
        color:#13ce66;
        font-size: 18px;
    }
    #player .el-pagination{
        margin-top: 10px;
        text-align:center;
    }
    #player .search-group .el-input__inner{
        height:38px;
    }
    #player .mem-dialog{
        min-width:500px;
    }
    #player .mem-dialog .mem-form .form-input{
        width:300px;
    }
    #player .mem-dialog .mem-form .form-input.age{
        width: 100px;
    }
    #player .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #player .fa-refresh:hover{
        opacity: 0.7;
    }
    #player .type-area{
        text-align:center;
    }
    #player .type-area .wechat,#player .type-area .ali,#player .type-area .wallet{
        margin: 0 20px 20px 20px;
        cursor:pointer;
        display:inline-block;
    }
    #player .el-tabs--card>.el-tabs__header .el-tabs__item.is-active{
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        border-radius: 0;
        border-top:2px solid #20a0ff;
    }
    #player .el-tabs__item{
        box-sizing:content-box;
    }
    #player .el-tabs__header{
        margin: 0 0 2px;
    }
    #player .el-tabs__content{
        margin: 0;
    }
    #player .el-table th>.cell, #player .el-table .cell{
        padding: 0 0 0 12px !important;
    }
    #player .el-table.el-button--mini{
        padding:8px;
    }
</style>



