<!-- 授权赛事 -->
<template>
<main id="planApply" class="container">
    <header class="head-title">
        <span>授权赛事</span>
    </header>
    <div style="overflow:hidden">
        <p class="title">{{planTitle}}</p>
        <div class="plan-detail"
             :key="item"
             v-for="item in planData"
             v-if="!item.content.includes('false') && item.title!=='赛事保证金：'">
            <span>{{item.title}}</span>
            <span v-if="item.title!=='赛事保证金：'" :class="{'text-red':item.content.includes(' 元')}">{{item.content}}</span>
        </div>
        <div style="clear:both"></div>
        <p class="text-gray"
           style="text-align:left;"
           v-if="brief">简介：{{brief}}</p>
        <br>
    </div>
    <p style="margin:0 0 10px 10px">申请机构：</p>
    <div class="applyList">
        <p class="text-gray" v-if="applyList.length === 0 && !noData">暂无机构申请该赛事</p>
        <p class="text-gray"
           v-if="noData">请从 <span class="text-blue text-big" @click="toPlan">赛事计划</span> 入口进入</p>
        <div class="org-item"
             :key="item"
             v-for="item in applyList">
            <span><i class="fa fa-bank"></i>{{item.name}}</span>
            <span><i class="fa fa-map-marker"></i>{{$parent.getType(item.type)}}</span>
            <span><i class="fa fa-user"></i>{{item.linkMan}}</span>
            <span><i class="fa fa-phone"></i>{{item.linkMob}}</span>
            <span class="arrow-right"><i class="el-icon-arrow-right"></i></span>
            <span class="btn"
                  v-if="item.state === 0 && checkPower !=='no'">
                <el-button type="success"
                           size="small"
                           @click="operation(item.id,item.state, item.name, item.orderId,'apply')" >同意</el-button>
            </span>
            <span class="btn"
                  v-if="item.state === 0 && checkPower !=='no'">
                <el-button type="danger"
                           size="small"
                           @click="operation(item.id,item.state, item.name, item.orderId,'refuse')">不同意</el-button>
            </span>
            <span class="check-state text-gray" v-if="item.state === 0 && checkPower ==='no'">审核中</span>
            <span class="check-state text-green" v-if="item.state===2">已同意</span>
            <span class="check-state text-red" v-if="item.state===1">不同意</span>
            <p class="refuse-reason" v-if="item.remark!==null&&item.remark!==''">{{'回复：'+item.remark}}</p>
        </div>
    </div>
    <div class=" check-area" v-if="isPlan==='0'  && checkLogs.length !== 0 ">
        <span class="check-area-title">
            审核进度：
            <!--<el-button type="text" @click="checkDetailVis = !checkDetailVis">查看详情</el-button>-->
        </span>
        <div class="check-step">
            <el-steps :space="200"
                      :active="checkLogs.length + checkOrgInfo.length"
                      process-status="wait">
                <el-step v-for="(item,index) in checkLogs"
                         :title="item.result"
                         :description="item.orgName"
                         :key="index"
                         :status="item.status"
                         @click.native="showCheckDetail(index)"></el-step>
                <el-step v-if="checkOrgInfo.orgName"
                         :title="checkOrgInfo.result"
                         :description="checkOrgInfo.orgName"
                         :status="checkOrgInfo.status"></el-step>
                <el-step v-if="checkOrgInfo.orgName && checkOrgInfo.orgLevel !=='1'"
                         title="等待审核"
                         status="wait"></el-step>
            </el-steps>
        </div>
    </div>
    <el-dialog title="确认授权"
               width="30%"
               :visible.sync="applyVis">
        <p v-if="checkPower === 'grade'">
            当前赛事审核机构为 <span class="text-blue">{{checkOrgInfo.orgName}}</span> ，您属于越级操作。
        </p>
        <p>
            是否确认
            <span v-if="checkPower === 'grade'">继续</span>
            授权该赛事给
            <span class="text-blue">{{thisName}}</span>
            ？
        </p>
        <div class="text-center">
            <el-button size="small" @click="applyVis = false">取 消</el-button>
            <el-button size="small" type="success" @click="applyConfirm">授 权</el-button>
        </div>
    </el-dialog>
    <el-dialog title="确认拒绝"
               width="30%"
               :visible.sync="refuseVis"
               @keyup.enter="refuseConfirm">
        <p v-if="checkPower === 'grade'">
            当前赛事授权机构为
            <span class="text-blue">{{checkOrgInfo.orgName}}</span>
            ，您属于越级操作。
        </p>
        <p>
            是否
            <span v-if="checkPower === 'grade'">继续</span>
            确认拒绝
            <span class="text-blue">{{thisName}}</span>
            举办该赛事？
        </p>
        <p style="margin-top:20px"><el-input placeholder="请简要说明拒绝理由" v-model="thisReason"></el-input></p>
        <div class="text-center">
            <el-button size="small" @click="refuseVis = false">取 消</el-button>
            <el-button size="small" type="danger" @click="refuseConfirm">拒 绝</el-button>
        </div>
    </el-dialog>
    <el-dialog title="审核详情"
               width="30%"
               v-if="checkLogs.length"
               :visible.sync="checkDetailVis">
        <p class="show-item">
            <span>审核机构:</span>
            {{checkLogs[showIndex].orgName}}
        </p>
        <!--<p class="show-item">
            <span>机构联系人:</span>
            {{checkLogs[showIndex].linkMan}}
        </p>
        <p class="show-item">
            <span>联系电话:</span>
            {{checkLogs[showIndex].linkPhone}}
        </p>-->
        <p class="show-item">
            <span>审核结果:</span>
            <label :class="{
                   'text-green':checkLogs[showIndex].result === '审核通过',
                   'text-red':checkLogs[showIndex].result === '审核未通过',
                   'text-gray':checkLogs[showIndex].result === '审核中'
               }">{{checkLogs[showIndex].result}}</label>
        </p>
        <p class="show-item">
            <span>审核时间:</span>
            {{checkLogs[showIndex].time}}
        </p>
        <p class="show-item" v-if="checkLogs[showIndex].remark !=='暂无' ">
            <span>回复：</span>
            {{checkLogs[showIndex].remark}}
        </p>
        <!--<el-table :data="checkLogs">
            <el-table-column prop="orgName" label="审核机构"></el-table-column>
            <el-table-column prop="linkMan" label="机构联系人"></el-table-column>
            <el-table-column prop="linkPhone" label="联系电话"></el-table-column>
            <el-table-column prop="result" label="审核结果"></el-table-column>
            <el-table-column prop="time" label="审核时间"></el-table-column>
        </el-table>-->
    </el-dialog>
</main>
</template>
<script>
export default{
  data () {
    return {
      noData: false,
      applyVis: false,
      refuseVis: false,
      checkDetailVis: false,
      id: '',
      thisID: '',
      thisName: '',
      thisOrder: '',
      thisReason: '',
      planTitle: '',
      isPlan: '',
      planData: [],
      applyList: [],
      checkLogs: [],
      checkPower: '',
      checkOrgInfo: {},
      brief: '',
      showIndex: 0
    }
  },
  methods: {
    showCheckDetail (index) {
      this.showIndex = index
      this.checkDetailVis = true
    },
    showTime (e) {
      if (e !== null) {
        return e.substring(0, 10)
      } else {
        return false
      }
    },
    getRange (num) {
      if (num === 58) return '全国'
      if (num === 59) return '省级'
      if (num === 60) return '县市级'
      if (num === 61) return '地市级'
    },
    getData () {
      const that = this
      const __url = that.$store.state.total.event.child.planDetail[1]
      if (__url === '') return false
      that.$ajax({
        url: __url,
        params: {
          id: that.id
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          const dat = _data.data
          const event = dat.eventinfo
          const org = dat.orginfo
          // 审核 logs
          const log = dat.check_logs
          const checkOrgInfo = dat.check_org_info
          that.checkLogs = []
          that.applyList = []
          that.checkOrgInfo = {}
          // 基本信息
          that.planTitle = event.title
          that.isPlan = event.is_plan
          that.planData = [
            {title: '申请时间：', content: that.showTime(event.apply_start) + ' 至 ' + that.showTime(event.apply_end)},
            {title: '比赛时间：', content: that.showTime(event.game_start) + ' 至 ' + that.showTime(event.game_end)},
            {title: '赛事规模：', content: that.getRange(event.scale >> 0)},
            {title: '赛事保证金：', content: (event.bail === null ? 0 : (event.bail - 0).toFixed(2)) + ' 元'},
            {title: '已申请机构数: ', content: org.length.toString()}
          ]
          that.checkPower = dat.check_power
          that.brief = event.condition
          org.forEach((e, i) => {
            that.applyList.push({
              id: e.org_id,
              orderId: e.order_id,
              state: (e.check_state >> 0),
              name: e.org_name,
              type: e.org_level,
              linkMan: e.link_name,
              linkMob: e.link_phone,
              remark: e.remark
            })
          })
          try {
            log.forEach((e, i) => {
              let result
              let status
              switch (+e.check_state) {
                case 0:
                  result = '审核未通过'
                  status = 'error'
                  break
                case 1:
                  result = '审核通过'
                  status = 'success'
                  break
                case 2:
                  result = '审核中'
                  status = ''
                  break
              }
              that.checkLogs.push({
                result: result,
                status: status,
                time: typeof e.create_time === 'string' ? e.create_time.substring(0, 16) : '暂无',
                linkMan: e.link_name,
                linkPhone: e.link_phone,
                orgLevel: e.org_level,
                orgName: e.org_name,
                remark: (e.remark === null || e.remark.trim() === '' || typeof e.remark === 'undefined') ? '暂无' : e.remark
              })
            })
            // 当前审核机构
            that.checkOrgInfo = {
              result: '审核中',
              status: 'finish',
              linkMan: checkOrgInfo.link_name,
              linkPhone: checkOrgInfo.link_phone,
              orgLevel: checkOrgInfo.org_level,
              orgName: checkOrgInfo.org_name
            }
            that.$store.commit('savePlanApplyData', [
              that.id,
              that.planTitle,
              that.brief,
              that.isPlan,
              that.planData,
              that.applyList,
              that.checkOrgInfo,
              that.checkLogs,
              that.checkPower
            ])
          } catch (error) {}
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    toPlan () {
      this.$parent.addTab('赛事计划')
      this.$router.push('/Main/plan')
    },
    operation (id, state, name, order, type) {
      const that = this
      if (state !== 0) {
        that.$message.info('已完成处理')
        return false
      }
      that.thisID = id
      that.thisName = name
      that.thisOrder = order
      if (type === 'apply') {
        that.applyVis = true
      } else {
        that.refuseVis = true
      }
      that.getData()
    },
    findIndex (id) {
      let index = ''
      this.applyList.forEach((e, i) => {
        if (id === e.id) {
          index = i
          return false
        }
      })
      return index
    },
    applyConfirm () {
      const that = this
      if (that.isPlan === '1') {
        // 省级计划同意
        const __url = that.$store.state.total.event.child.solvePlan[1]
        if (__url === '') return false
        that.$ajax({
          url: __url,
          method: 'post',
          data: {
            id: that.id,
            order_id: that.thisOrder,
            check_state: 2
          }
        }).then(res => {
          const _data = res.data
          if ((_data.errcode >> 0) === 0) {
            const index = that.findIndex(that.thisID)
            that.applyList.forEach((e, i) => {
              e.state = 1
            })
            that.applyList[index].state = 2
            that.$notify.success('成功将该赛事授权给 ' + that.thisName)
            that.applyVis = false
          } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
            that.$parent.overTime()
          } else {
            that.$message.warning(_data.msg)
          }
        }, res => {
          that.$message.warning('网络似乎出了点问题')
        })
      } else {
        // 非省级计划同意
        const __url = that.$store.state.total.event.child.solveMy[1]
        if (__url === '') return false
        that.$ajax({
          url: __url,
          params: {
            id: that.id,
            check_state: 2
          }
        }).then(res => {
          const _data = res.data
          if ((_data.errcode >> 0) === 0) {
            const index = that.findIndex(that.thisID)
            that.applyList.forEach((e, i) => {
              e.state = 1
            })
            that.applyList[index].state = 2
            that.$notify.success('成功将该赛事授权给 ' + that.thisName)
            that.applyVis = false
            that.getData()
          } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
            that.$parent.overTime()
          } else {
            that.$message.warning(_data.msg)
          }
        }, res => {
          that.$message.warning('网络似乎出了点问题')
        })
      }
    },
    refuseConfirm () {
      const that = this
      if (that.isPlan === '1') {
        // 省级计划拒绝
        const __url = that.$store.state.total.event.child.solvePlan[1]
        if (__url === '') return false
        that.$ajax({
          url: __url,
          method: 'post',
          data: {
            id: that.id,
            order_id: that.thisOrder,
            check_state: 1,
            remark: that.thisReason
          }
        }).then(res => {
          const _data = res.data
          if ((_data.errcode >> 0) === 0) {
            const index = that.findIndex(that.thisID)
            that.applyList[index].state = 1
            that.$notify.success('已拒绝 ' + that.thisName + ' 承办该赛事')
            that.refuseVis = false
            that.applyList.forEach((e, i) => {
              if (e.orderId === that.thisOrder) {
                e.remark = that.thisReason
              }
            })
            that.refuseVis = false
          } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
            that.$parent.overTime()
          } else {
            that.$message.warning(_data.msg)
          }
        }, res => {
          that.$message.warning('网络似乎出了点问题')
        })
      } else {
        // 非省级计划拒绝
        const __url = that.$store.state.total.event.child.solveMy[1]
        if (__url === '') return false
        that.$ajax({
          url: __url,
          method: 'post',
          data: {
            id: that.id,
            check_state: 1,
            remark: that.thisReason
          }
        }).then(res => {
          const _data = res.data
          if ((_data.errcode >> 0) === 0) {
            const index = that.findIndex(that.thisID)
            that.applyList[index].state = 1
            that.$notify.success('已拒绝 ' + that.thisName + ' 承办该赛事')
            that.refuseVis = false
            that.applyList.forEach((e, i) => {
              if (e.orderId === that.thisOrder) {
                e.remark = that.thisReason
              }
            })
            that.getData()
          } else if ((_data.errcode >> 0) === 1000) {
            that.$parent.overTime()
          } else {
            that.$message.warning(_data.msg)
          }
        }, res => {
          that.$message.warning('网络似乎出了点问题')
        })
      }
    }
  },
  mounted () {
    const that = this
    const href = window.location.href
    const index = href.indexOf('?')
    that.id = parseInt(href.substring(index + 1, index + 5))
    if (that.id) {
      if (that.id !== that.$store.state.planApplyData.id || typeof that.$store.state.planApplyData === 'string') {
        that.getData()
      } else if (typeof that.$store.state.planApplyData === 'object' && that.$store.state.planApplyData.id === that.id) {
        that.id = that.$store.state.planApplyData.id
        that.planTitle = that.$store.state.planApplyData.planTitle
        that.brief = that.$store.state.planApplyData.brief
        that.isPlan = that.$store.state.planApplyData.isPlan
        that.planData = that.$store.state.planApplyData.planData
        that.applyList = that.$store.state.planApplyData.applyList
        that.checkOrgInfo = that.$store.state.planApplyData.checkOrgInfo
        that.checkLogs = that.$store.state.planApplyData.checkLogs
        that.checkPower = that.$store.state.planApplyData.checkPower
      }
    } else {
      that.id = that.$store.state.planApplyData.id
      that.planTitle = that.$store.state.planApplyData.planTitle
      that.brief = that.$store.state.planApplyData.brief
      that.isPlan = that.$store.state.planApplyData.isPlan
      that.planData = that.$store.state.planApplyData.planData
      that.applyList = that.$store.state.planApplyData.applyList
      that.checkOrgInfo = that.$store.state.planApplyData.checkOrgInfo
      that.checkLogs = that.$store.state.planApplyData.checkLogs
      that.checkPower = that.$store.state.planApplyData.checkPower
      history.replaceState(null, '', location.hash + '?' + that.$store.state.planApplyData.id)
    }
  }
}
</script>
<style>
    #planApply{
        background:#fff;
        padding:0 20px;
    }
    #planApply .check-state{
        font-size:14px;
    }
    #planApply .text-gray{
        margin-top:10px;
        color:#8492A6;
        font-size:14px;
        text-align:center;
    }
    #planApply .text-blue{
        color:#20a0ff;
        font-size:14px;
    }
    #planApply .text-big{
        font-size:18px;
    }
    #planApply .text-big:hover{
        opacity:0.7;
    }
    #planApply .text-center{
        margin-top:30px;
    }
    #planApply .title{
        color:#20a0ff;
        margin-bottom:14px;
    }
    #planApply .fa{
        margin-right:4px;
    }
    #planApply .plan-detail{
        font-size:14px;
        float:left;
        width:50%;
        margin-bottom:14px;
    }
    #planApply .plan-detail span:first-child{
        width:104px;
        display: inline-block;
    }
    #planApply .text-red {
        color: #ff4949;
    }
    #planApply .text-green {
        color: #13ce66;
    }
    #planApply .applyList .org-item{
        border-top:1px solid #ddd;
        min-height:80px;
    }
    #planApply .applyList .org-item:last-child{
        border-bottom:1px solid #ddd;
    }
    #planApply .applyList .org-item>span{
        display: inline-block;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height:80px;
    }
    #planApply .applyList .org-item>span:first-child{
        max-width:200px;
        width:20%;
    }
    #planApply .applyList .org-item>span:nth-child(2) {
        max-width:130px;
        width:14%;
    }
    #planApply .applyList .org-item>span:nth-child(3) {
        max-width:150px;
        width:12%;
    }
    #planApply .applyList .org-item>span:nth-child(4) {
        max-width:120px;
        width:18%;
    }
    #planApply .applyList .org-item>span:nth-child(5) {
        max-width:85px;
        width:7%;
        text-align: center;
    }
    #planApply .applyList .org-item>span:nth-child(6),#planApply .applyList .org-item>span:nth-child(7) {
        text-align: center;
    }
    #planApply .btn{
        margin:0 10px;
    }
    #planApply .arrow-right{
        color:#20a0ff;
    }
    #planApply .el-button{
        width:68px;
    }
    #planApply .refuse-reason{
        color:#999;
        margin-bottom:10px;
    }
    #planApply .check-area{
        margin:10px 0 0 0;
        overflow:hidden;
    }
    #planApply .check-area .check-area-title{
        display:inline-block;
        margin-bottom:10px;
        /*width:88px;*/
    }
    #planApply .check-area .check-step{
        margin-left:20px;
        cursor:pointer;
    }
    #planApply .el-step__description{
        white-space:nowrap;
    }
    #planApply .show-item{
        font-size:16px;
        margin:4px 0;
        color:#8492A6;
    }
    #planApply .show-item span{
        width:90px;
        text-align:right;
        padding-right:10px;
        display:inline-block;
    }
</style>



