<template>
    <main class="container content">
        图
        <div id="pie" style="width:500px;height:500px;"></div>
    </main>
</template>
<script>
    import echarts from 'echarts'
    export default {
        data() {
            return {
                // 初始化空对象
                chart: null,
                // 初始化图表配置
                head: ['直接访问','邮件营销','联盟广告','视频广告','搜索引擎'],
                headData: [ {value:335, name:'直接访问'},
                    {value:310, name:'邮件营销'},
                    {value:234, name:'联盟广告'},
                    {value:135, name:'视频广告'},
                    {value:1548, name:'搜索引擎'}
                ]
            }
        },
        methods: {
            // 绘图
            drawPie(id) {
                // 绘图方法
                this.chart = echarts.init(document.getElementById(id));
                // 皮肤添加同一般使用方式
                this.chart.showLoading();
                // 返回到data中
                let that = this;
                // set
                this.chart.setOption({
                    title : {
                        text: '某站点用户访问来源',
                        subtext: '纯属虚构',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: that.head
                    },
                    series : [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:that.headData,
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
                this.chart.hideLoading()
            }
        },
        beforeCreate(){

        },
        mounted(){
            //this.headData[4].value=10000;
            this.drawPie('pie');
        }
    }
</script>


