<!-- 转会操作页面 -->
<template>
    <main id="player"
          class="container">
        <section class="search-area"
                 @keyup.enter="search">
            <el-input class="search-group"
                      v-model="input"
                      placeholder="请输入棋手证件号">
                <el-button slot="append"
                           icon="search"
                           @click="search"
                           :disabled="isSearch"
                           :loading="isSearch">查询</el-button>
            </el-input>
        </section>
        <section class="player-list">
            <div>
                <div style="float:right;display:inline-block">
                    <i class="fa fa-refresh"
                       @click="getData"></i>
                </div>
            </div>
            <el-table :data="currentData"
                      stripe
                      size="mini"
                      :loading="$store.state.tableLoad">
                <el-table-column label="基本信息"
                                 min-width="170">
                    <template slot-scope=" scope">
                        <p>{{scope.row.name}}（{{scope.row.gender + ' ' + scope.row.rank}}）</p>
                    </template>
                </el-table-column>
                <el-table-column label="证件号"
                                 min-width="170">
                    <template slot-scope=" scope">
                        <p>{{scope.row.number}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="隶属机构"
                                 min-width="120">
                    <template slot-scope=" scope">
                        <p>{{scope.row.org}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="联系人"
                                 min-width="120">
                    <template slot-scope=" scope">
                        <p>{{scope.row.linkName}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="联系电话"
                                 min-width="120">
                    <template slot-scope=" scope">
                        <p>{{scope.row.linkPhone}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="注册时间"
                                 min-width="100">
                    <template slot-scope=" scope">
                        <p>{{scope.row.signTime}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="到期时间"
                                 min-width="100">
                    <template slot-scope=" scope">
                        <p>{{scope.row.overTime}}</p>
                    </template>
                </el-table-column>
                <el-table-column prop="isOver"
                                 label="状态"
                                 min-width="80">
                    <template slot-scope=" scope">
                        <el-tag :type="scope.row.isOver[0]"
                                size="small">{{scope.row.isOver[1]}}</el-tag>
                        <el-tag type="danger"
                                size="small"
                                v-show="scope.row.state===1">已禁赛</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作"
                                 min-width="80">
                    <template slot-scope=" scope">
                        <el-button type="text"
                                   @click="watch(scope.row.id,scope.row.name,scope.row.number,scope.row.orgId)"
                                   v-if="$store.state.total.mem.child.memEdit[1]">申请转入</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentPage"
                           :page-sizes="[20, 50, 100]"
                           :page-size="pageSize"
                           layout="total,sizes, prev, pager, next, jumper"
                           :total="totalNum">
            </el-pagination>
        </section>
        <!--支付-->
        <el-dialog title="选择支付方式"
                   width="30%"
                   :visible.sync="payVis">
            <div class="type-area">
                <div class="ali"
                     @click="$parent.Pay('ali',orderID,'/Main/player?pay')">
                    <img src="./../../assets/img/alipay.png" alt="ali">
                    <p>支付宝</p>
                </div>
                <div class="wechat"
                     @click="$parent.Pay('wx',orderID,'/Main/player?pay')">
                    <img src="./../../assets/img/wechat.png" alt="wechat">
                    <p>微信</p>
                </div>
                <div class="wallet"
                     v-if="0">
                    <img src="./../../assets/img/walletpay.png" alt="wallet">
                    <p>钱包余额</p>
                </div>
            </div>
            <p class="tip">提示：支付注册费后，即可完成会员的注册。</p>
        </el-dialog>

        <el-dialog title="申请会员转入"
                   width="30%"
                   :visible.sync="transferVis">
            <p>
                申请将会员
                <span style="color:#20a0ff;font-size: 16px">{{transferInfo.name}}</span>
                <span class="tip">（注册证号：{{transferInfo.ID}}）</span>
                转入
            </p>
            <el-form size="middle"
                     ref="formInfo"
                     :model="formInfo"
                     label-width="100px"
                     :rules="rules"
                     class="transfer-form">
                <el-form-item prop="reason"
                              label="申请理由">
                    <el-input v-model="formInfo.reason"></el-input>
                </el-form-item>
                <el-form-item prop="imgUrl"
                              label="相关照片">
                    <el-upload
                            :action="this.$store.state.baseURL+this.$store.state.uploadImg"
                            :headers="{token:$parent.getToken()}"
                            list-type="picture-card"
                            :file-list="formInfo.imgUrl"
                            :on-success="saveLicenseURL"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="removeLicenseURL">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                </el-form-item>
            </el-form>
            <div class="text-center"
                 style="margin-top: 10px">
                <el-button size="small"
                           @click="transferVis = false">取消</el-button>
                <el-button size="small"
                           @click="confirmTransfer" type="primary">提交</el-button>
            </div>
        </el-dialog>
        <el-dialog  width="30%"
                    :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
    </main>
</template>
<script>
export default{
  data () {
    const that = this
    let hei = 0
    let win = window.outerWidth
    if (win > 1366) {
      hei = 640
    } else if (win <= 1366 && win >= 1024) {
      hei = 450
    } else {
      hei = 300
    }
    let checkID = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入注册证号'))
      } else {
        let res = this.$parent.checkID(value)
        if (!res[0]) {
          callback(new Error(res[1]))
        } else {
          that.getAge(this.memberInfo.ID)
          callback()
        }
      }
    }
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      formInfo: {
        memId: '',
        orgId: '',
        reason: '',
        imgUrl: [],
        img: ''
      },
      rules: {
        reason: [
          {required: true, message: '请输入原因', trigger: 'blur'}
        ]
      },
      isSearch: false,
      dialogFormVisible: false,
      freezeVisible: false,
      input: '',
      select: 'all',
      currentPage: 1,
      pageSize: that.$parent.getCookie('playerPageSize') !== '' ? (that.$parent.getCookie('playerPageSize') >> 0) : 20,
      totalNum: 0,
      money: 0,
      fee: 0,
      orderID: '',
      transferVis: false,
      payVis: false,
      transferInfo: {
        name: '',
        id: '',
        ID: ''
      },
      multipleSelection: [],
      systemTime: '',
      currentFreeze: {},
      currentData: [],
      tableHei: hei,
      memberInfo: {
        name: '',
        ID: '',
        rank: '',
        isFee: '',
        pic: ''
      },
      gotAge: '',
      gotGender: 1,
      checkRules: {
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'}
        ],
        ID: [
          {required: true, validator: checkID, trigger: 'blur'}
        ]
      },
      optionGroups: [
        {
          label: '级别',
          options: [
            {value: 143, label: '28级'},
            {value: 142, label: '27级'},
            {value: 141, label: '26级'},
            {value: 99, label: '25级'},
            {value: 103, label: '24级'},
            {value: 104, label: '23级'},
            {value: 106, label: '22级'},
            {value: 107, label: '21级'},
            {value: 108, label: '20级'},
            {value: 109, label: '19级'},
            {value: 110, label: '18级'},
            {value: 111, label: '17级'},
            {value: 112, label: '16级'},
            {value: 113, label: '15级'},
            {value: 114, label: '14级'},
            {value: 115, label: '13级'},
            {value: 116, label: '12级'},
            {value: 117, label: '11级'},
            {value: 118, label: '10级'},
            {value: 119, label: '9级'},
            {value: 120, label: '8级'},
            {value: 121, label: '7级'},
            {value: 122, label: '6级'},
            {value: 123, label: '5级'},
            {value: 124, label: '4级'},
            {value: 125, label: '3级'},
            {value: 126, label: '2级'},
            {value: 127, label: '1级'}
          ]
        },
        {
          label: '业余段位',
          options: [
            {value: 128, label: '1段'},
            {value: 129, label: '2段'},
            {value: 130, label: '3段'},
            {value: 131, label: '4段'},
            {value: 132, label: '5段'},
            {value: 133, label: '6段'}
          ]
        }
      ]
    }
  },
  filters: {
    showNum (value) {
      return value.toString().split('').reverse().join('').replace(/(\d{3})/g, '$1,').replace(/,$/, '').split('').reverse().join('')
    }
  },
  methods: {
    getCardTypeName (num) {
      switch (num >> 0) {
        case 1:
          return '身份证'
        case 2:
          return '护照'
        case 4:
          return '台胞证'
        case 8:
          return '港澳通行证'
      }
    },
    confirmTransfer () {
      const that = this
      const __url = that.$store.state.total.mem.child.memTransfer[1]
      if (__url === '') return false
      that.$ajax({
        url: __url,
        params: {
          mem_id: that.formInfo.memId,
          reason: that.formInfo.reason,
          img_files: that.formInfo.img,
          out_org_id: that.formInfo.orgId
        }
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.$message.success(_data.msg)
          that.transferVis = false
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.transferVis = false
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    },
    saveLicenseURL (res, file, fileList) {
      const that = this
      if ((res.errcode >> 0) === 0) {
        that.formInfo.imgUrl = fileList
        if (that.formInfo.img === '') {
          that.formInfo.img = res.data
        } else {
          that.formInfo.img += ',' + res.data
        }
      } else if ((res.errcode >> 0) === 1000 || (res.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(res.msg)
      }
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    removeLicenseURL (file, fileList) {
      this.formInfo.img = this.removeURL(fileList)
    },
    removeURL (arr) {
      let _url = ''
      arr.forEach((e, i) => {
        if (e.url.includes('blob')) {
          if (i) _url += ',' + e.response.data
          else _url += e.response.data
        } else {
          const ee = e.url.substring(e.url.indexOf('uploads'))
          if (i) _url += ',' + ee
          else _url += ee
        }
      })
      return _url
    },
    getRank (rank) {
      rank = rank >> 0
      this.optionGroups.forEach((e, i) => {
        e.options.forEach((e, i) => {
          if (e.value === rank) {
            rank = e.label
            return false
          }
        })
      })
      return rank
    },
    watch (id, name, ID, orgId) {
      const that = this
      that.transferInfo.name = name
      that.transferInfo.ID = ID
      that.transferInfo.orgId = ID
      that.transferInfo.id = id
      that.formInfo.memId = id
      that.formInfo.orgId = orgId
      that.formInfo.reason = ''
      that.formInfo.img = ''
      that.formInfo.imgUrl = []
      that.transferVis = true
    },
    search () {
      const that = this
      that.currentPage = 1
      that.getData()
    },
    handleSizeChange (pageSize) {
      document.cookie = 'playerPageSize=' + pageSize
      let that = this
      that.currentData = []
      that.pageSize = pageSize
      that.currentPage = 1
      that.getData()
    },
    handleCurrentChange (page) {
      let that = this
      that.currentPage = page
      that.getData()
    },
    getData () {
      const checkState = (t) => {
        const that = this
        const yy = that.systemTime.substring(0, 4)
        const MM = that.systemTime.substring(5, 7) - 1
        const dd = that.systemTime.substring(8, 10)
        const hh = that.systemTime.substring(11, 13)
        const mm = that.systemTime.substring(14, 16)
        const ss = that.systemTime.substring(17, 19)
        if (t === null) {
          return ['danger', '未注册']
        } else if (new Date(yy, MM, dd, hh, mm, ss) <= new Date(t)) {
          return ['success', '已年检']
        } else if (new Date(yy, MM, dd, hh, mm, ss) > new Date(t)) {
          return ['warning', '未年检']
        }
      }
      const formatDate = (t) => {
        let _t = ''
        if (typeof t === 'string') {
          const yy = t.substring(0, 4)
          const MM = t.substring(5, 7) - 1
          const dd = t.substring(8, 10)
          const hh = t.substring(11, 13)
          const mm = t.substring(14, 16)
          const ss = t.substring(17, 19)
          _t = new Date(yy, MM, dd, hh, mm, ss)
        } else {
          _t = t
        }
        const f = (a) => {
          return a < 10 ? '0' + a : a
        }
        return _t.getFullYear() + '-' + f(_t.getMonth() + 1) + '-' + f(_t.getDate()) + ' ' + f(_t.getHours()) + ': ' + f(_t.getMinutes())
      }
      const quarter = (t) => {
        const yy = t.substring(0, 4)
        const MM = t.substring(5, 7)
        const dd = t.substring(8, 10)
        const hh = t.substring(11, 13)
        const mm = t.substring(14, 16)
        const ss = t.substring(17, 19)
        const _t = new Date(yy, MM, dd, hh, mm, ss)
        const M = _t.getMonth() + 1
        const f = (w) => {
          const y = _t.getFullYear()
          return new Date(y, w, 1, 0, 0, 0)
        }
        if (M >= 1 && M <= 3) {
          return f(0)
        } else if (M >= 4 && M <= 6) {
          return f(3)
        } else if (M >= 7 && M <= 9) {
          return f(6)
        } else if (M >= 10 && M <= 12) {
          return f(9)
        }
      }
      const that = this
      const __url = that.$store.state.total.mem.child.memFind[1]
      if (__url === '') return false
      that.$store.state.tableLoad = true
      that.$ajax({
        url: __url,
        method: 'get',
        params: {
          idcard: that.input
        }
      }).then(res => {
        const _data = res.data
        const dat = _data.data
        that.$store.state.tableLoad = false
        if ((_data.errcode >> 0) === 0) {
          if (dat !== null) {
            that.currentData = []
            let _gender = ''
            if (dat.idCard.length === 15) {
              _gender = dat.idCard[14] % 2 === 0 ? '女' : '男'
            } else if (dat.idCard.length === 18) {
              _gender = dat.idCard[16] % 2 === 0 ? '女' : '男'
            }
            that.currentData.push({
              id: dat.id,
              number: dat.idCard,
              cardType: that.getCardTypeName(dat.cardType >> 0),
              name: dat.idCardName,
              gender: _gender,
              rank: that.getRank(dat.level),
              org: dat.org_name,
              orgId: dat.org_id,
              linkName: dat.link_name,
              linkPhone: dat.link_phone,
              signTime: dat.fees_start === null ? '未缴费注册' : formatDate(dat.fees_start).substring(0, 10),
              overTime: dat.fees_end === null ? '从未缴费' : formatDate(quarter(dat.fees_end)).substring(0, 10),
              isOver: checkState(quarter(dat.fees_end)),
              state: dat.state >> 0
            })
            const href = window.location.href
            if (href.includes('?')) {
              that.input = (href.substring(href.indexOf('?') + 1))
              that.watch(that.currentData[0].id, that.currentData[0].name, that.currentData[0].number, that.currentData[0].orgId)
            }
          }
          that.totalNum = (_data.total >> 0)
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$store.state.tableLoad = false
        that.$message({message: '网络开小差了', type: 'warning'})
      })
    },
    addNewPlayer () {
      let root = this.$parent
      root.addTab('棋手注册', root)
      this.$router.push('/Main/addPlayer')
    }
  },
  mounted () {
    const that = this
    that.systemTime = that.$store.state.sysTime
    const __url = that.$store.state.total.set.child.feeList[1]
    const href = window.location.href
    if (__url) {
      that.$ajax({
        url: __url
      }).then(res => {
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          _data.data.forEach((e, i) => {
            if (e.type === '2') {
              that.fee = e.price - 0
            }
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.$message.warning('网络似乎出了点问题')
      })
    }
    if (href.includes('?')) {
      that.input = (href.substring(href.indexOf('?') + 1))
      that.getData()
    }
  }
}
</script>
<style>
    #player .text-success{
        color:#13ce66;
    }
    #player .money{
        margin-left:24px;
        font-size: 18px;
    }
    #player .text-success:hover{
        opacity:0.7;
    }
    #player .text-warning{
        color:#f7ba2a;
    }
    #player .text-red{
        color:#ff4949;
    }
    #player .upload-img{
        width: 100%;
    }
    #player .player-disabled{
        color:#999;
    }
    #player .tip{
        color:#8492A6;
        font-size: 12px;
        margin:4px 0;
        text-align:right;
    }
    #player .text-warning:hover{
        opacity:0.7;
    }
    #player .el-button--text.text-red span{
        color:#ff4949;
    }
    #player .el-button--text.text-red:hover span{
        opacity:0.7;
    }
    #player .transfer-form{
        margin-top:20px;
    }
    #player .popper__arrow{
        left:43px !important;
    }
    #player .el-popover{
        min-width:70px;
        width:70px !important;
    }
    #player .watch{
        padding:0;
        color: #20a0ff;
        cursor:pointer;
        opacity: 1;
    }
    #player .watch:hover{
        color:#20a0ff;
        opacity:0.8;
    }
    #player .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #player .el-icon-more {
        margin-left: 14px;
    }
    /*.search-area .el-input-group__append button.el-button{
        color:#fff;
        background-color:#20a0ff;
        border-color:#20a0ff;
        border-radius:0;
    }*/
    #player .search-area .add-new{
        margin-left:20px;
    }
    #player .search-area .search-group{
        width:80%;
    }
    #player .player-list{
        margin-top:20px;
        padding:0;
    }
    #player .player-list .player-num{
        color:#13ce66;
        font-size: 18px;
    }
    #player .el-pagination{
        margin-top: 10px;
        text-align:center;
    }
    #player .search-group .el-input__inner{
        height:38px;
    }
    #player .mem-dialog{
        min-width:500px;
    }
    #player .mem-dialog .mem-form .form-input{
        width:300px;
    }
    #player .mem-dialog .mem-form .form-input.age{
        width: 100px;
    }
    #player .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #player .fa-refresh:hover{
        opacity:0.7;
    }
    #player .type-area{
        text-align:center;
    }
    #player .type-area .wechat,#player .type-area .ali,#player .type-area .wallet{
        margin:0 20px 20px 20px;
        cursor:pointer;
        display:inline-block;
    }
</style>



