<template>
    <main id="main"
          :loading.body="bodyLoading">
        <!-- 头部导航 -->
        <header class="header">
            <div>
                {{SystemName}}
                <span class="serve-content">技术服务热线：0571-85220313，0571-88131530</span>
            </div>
            <!--放出可显示当前时间-->
            <!--<span class="system-time">{{showTime($store.state.sysTime)}}</span>-->
            <div>
                <el-button type="text"
                           v-if="$store.state.bindState==='0'"
                           @click="toBindYunBiSai">绑定云比赛</el-button>
                <span @click="more()"
                      style="cursor: pointer;margin-right:12px">
                        <el-badge :value="msgNum"
                                  :max="99"
                                  class="item">
                        <span class="fa fa-bell"></span>
                    </el-badge>
                    </span>
                <!--<span class="fa fa-user-circle headImg"></span>-->
                <span class="userName"
                      @click="showUserInfo">
                    {{orgName}} - {{userName}}
                </span>
                <span class="exit"
                      @click="exit">退出</span>
            </div>
        </header>
        <main>
            <!-- 左侧导航 -->
            <div class="main-left">
                <!--<i class="fa fa-th-list collapse"-->
                   <!--@click="changeMenu"></i>-->
                <el-row>
                    <el-menu default-active="/Organization"
                             class="el-menu-vertical"
                             background-color="#324057"
                             text-color="#bfcbd9"
                             :router="true"
                             :collapse="isCollapse"
                             :unique-opened="true">
                        <el-menu-item class="slide-menu"
                                      :index="homePath"
                                      @click="addTab($event)"
                                      :key="homePath"
                                      v-if="$store.state.total.home.auth">
                            <router-link to="/Main/home">
                                <div class="inner-img icon-home"></div>
                                <span>首页</span>
                            </router-link>
                        </el-menu-item>
                        <el-menu-item class="slide-menu"
                                      :index="orgPath"
                                      :key="orgPath"
                                      @click="addTab($event)"
                                      v-if="$store.state.total.org.auth">
                            <router-link to="/Main/org">
                                <div class="inner-img icon-org"></div>
                                <span>机构管理</span>
                            </router-link>
                        </el-menu-item>
                        <el-submenu  :index="index"
                                     :key="item.icon"
                                     v-for="(item,index) in Modules"
                                     v-if="$store.state.total[item.icon].auth">
                            <template slot="title">
                                <div class="inner-img"
                                     :class="'icon-'+item.icon"></div>
                                <span slot="title">{{item.name}}</span>
                            </template>
                            <el-menu-item-group>
                                <el-menu-item :key="child.path"
                                              @click="addTab($event)"
                                              :index="child.path"
                                              v-for="child in item.children"
                                              v-if="!(!$store.state.subAuth[child.name].auth||($store.state.orgLevel===4&&child.name==='会员分配') || ($store.state.orgLevel===4&&child.name==='赛事审核'))">{{child.name}}</el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                </el-row>
            </div>
            <!-- 右侧主内容区 -->
            <div class="main-right">
                <div class="tab">
                    <div class="tab-item"
                         v-model="item.name"
                         :class="{'tab-active':item.active}"
                         @click="routeGo($event,item.name)"
                         v-for="item in editableTabs">
                        {{item.name}}
                        <span class="tab-close"
                              v-if="item.closeable"
                              @click.stop="removeTab($event)">×</span>
                    </div>
                </div>
                <div class="right-wrap">
                    <transition name="el-fade-in-linear">
                        <router-view class="view"></router-view>
                    </transition>
                </div>
            </div>
        </main>
        <el-dialog title="机构信息"
                   :visible.sync="infoVisible">
            <div class="info"
                 v-for="item in userInfo">
                <span class="title">{{item.title}}</span>
                <span class="detail"
                      v-html="item.detail">{{item.detail}}</span>
            </div>
            <div class="text-center">
                <el-button type="info"
                           @click="toPassword">修改密码</el-button>
                <el-button type="primary"
                           @click="toOrgEdit">修改信息</el-button>
            </div>
        </el-dialog>
        <el-dialog width="30%"
                   title="为确保您的账号安全，使用前请先修改账号密码"
                   :visible.sync="pwdState"
                   :model="change"
                   ref="change"
                   :rules="changeRules"
                   :close-on-click-modal="false"
                   :close-on-press-escape="false"
                   :show-close="false">
            <el-form label-position="left"
                     label-width="100px">
                <el-form-item prop="newPwd" label="新密码">
                    <el-input placeholder="请输入新密码"
                              :minlength="6"
                              :maxlength="18"
                              type="password"
                              v-model="change.newPwd"></el-input>
                </el-form-item>
                <el-form-item prop="repeatPwd"
                              label="重复新密码">
                    <el-input placeholder="再次输入新密码"
                              :minlength="6"
                              :maxlength="18"
                              type="password"
                              v-model="change.repeatPwd"></el-input>
                </el-form-item>
                <el-form-item class="text-center">
                    <el-button size="small"
                               type="primary"
                               @click="changePwd">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog title="请上传认证照片以确保机构信息完整"
                   width="30%"
                   :visible.sync="authState"
                   :model="change"
                   ref="change"
                   :rules="changeRules"
                   :close-on-click-modal="false"
                   :close-on-press-escape="false"
                   :show-close="false">
            <p>如不完善机构信息将可能影响到今后的使用。</p>
            <div class="text-center"
                 style="margin-top:10px">
                <el-button @click="authState = false">下次再说</el-button>
                <el-button type="primary"
                           @click="completePic">马上完善</el-button>
            </div>
        </el-dialog>
    </main>
</template>
<script>
    export default {
        name: 'app',
        data(){
            return {
                SystemName:'',
                isCollapse:false,
                change:{
                    oldPwd:'123456',
                    newPwd:'',
                    repeatPwd:''
                },
                changeRules:{
                    oldPwd:[
                        {required:true,message:'请输入原密码',trigger:'blur'},
                        {min:3,max:18,message:'长度位6-18位',trigger:'blur'}
                    ],
                    newPwd:[
                        {required:true,message:'请输入新密码',trigger:'blur'},
                        {min:6,max:18,message:'长度位6-18位',trigger:'blur'},
                    ]
                },
                pwdState:false,
                authState:false,
                bodyLoading:true,
                infoVisible:false,
                homeInfo:{},
                orgName:'',
                userName:'',
                msgNum: 5,
                userInfo:[],
                Modules: {
                    member: {
                        name: '会员管理',
                        icon: 'mem',
                        children: [
                            {name: '棋手管理', path: '/Main/player'},
                            {name: '会员转会', path: '/Main/transferList'},
                            {name: '会员分配', path: '/Main/allotPlayer'},
                            {name: '裁判管理', path: '/Main/judge'},
                            {name: '教练管理', path: '/Main/coach'},
                        ]
                    },
                    event: {
                        name: '赛事管理',
                        icon: 'event',
                        children: [
                            {name: '赛事计划', path: '/Main/plan'},
                            {name: '我的赛事', path: '/Main/myEvent'},
                            {name: '赛事审核', path: '/Main/eventAllot'},
                        ]
                    },
                    credential: {
                        name: '段级位管理',
                        icon: 'cre',
                        children: [
                            {name: '待确认名单', path: '/Main/wait'},
                            {name: '段级位名单', path: '/Main/rankingList'},
                            {name: '证书列表', path: '/Main/creList'},
                        ]
                    },
                    wallet: {
                        name: '钱包管理',
                        icon: 'wallet',
                        children: [
                            {name: '提现', path: '/Main/WCash'},
                            {name: '系统内转账', path: '/Main/WTransfer'},
                            {name: '转账至云比赛', path: '/Main/WToYunbisai'},
                            {name: '绑定/修改', path: '/Main/WCreate'},
                            {name: '收支明细', path: '/Main/WList'}
                        ]
                    },
                    statistics: {
                        name: '报表管理',
                        icon: 'sta',
                        children: [
                            {name: '图', path: '/Main/charts'}
                        ]
                    },
                    authority: {
                        name: '系统设置',
                        icon: 'set',
                        children: [
                            {name: '权限设置', path: '/Main/setting1'},
                            {name: '费用设置', path: '/Main/setting2'},
                            {name: '绑定云比赛', path: '/Main/setting3'}
                        ]
                    }
                },
                allPath:[
                    ['/Main','首页'],
                    ['/Main/more','消息'],
                    ['/Main/org','机构管理'],
                    ['/Main/OrgAdd','添加机构'],
                    ['/Main/OrgEdit','机构信息'],
                    ['/Main/player','棋手管理'],
                    ['/Main/transferPlayer','转会申请'],
                    ['/Main/transferList','会员转会'],
                    ['/Main/allotPlayer','会员分配'],
                    ['/Main/addPlayer','棋手注册'],
                    ['/Main/importPlayer','批量导入棋手'],
                    ['/Main/judge','裁判管理'],
                    ['/Main/addJudge','添加裁判'],
                    ['/Main/coach','教练管理'],
                    ['/Main/addCoach','添加教练'],
                    ['/Main/plan','赛事计划'],
                    ['/Main/planApply','授权赛事'],
                    ['/Main/planAdd','预发布赛事'],
                    ['/Main/planEdit','编辑赛事'],
                    ['/Main/applyFor','申请赛事'],
                    ['/Main/myEvent','我的赛事'],
                    ['/Main/wait','待确认名单'],
                    ['/Main/rankingList','段级位名单'],
                    ['/Main/creList','证书列表'],
                    ['/Main/WCash','提现'],
                    ['/Main/WTransfer','系统内转账'],
                    ['/Main/WCreate','绑定/修改'],
                    ['/Main/WList','收支明细'],
                    ['/Main/charts','图'],
                    ['/Main/setting1','权限设置'],
                    ['/Main/setting2','费用设置'],
                    ['/Main/setting3','绑定云比赛']
                ],
                homePath: '/Main/home',
                orgPath: '/Main/org',
                systemTime:'',
                editableTabs: [{
                    name: '首页',
                    active:true,
                    closeable:false
                }],
                tabIndex: 1
            }
        },
        methods: {
            changeMenu(){
                // 需要做样式处理
                this.isCollapse = !this.isCollapse;
            },
            openNewTag(){
                this.$notify.info({title:'提示',message:'如未跳转，请尝试解除浏览器的自动拦截。如成功跳转，请忽略该消息。'});
            },
            completePic(){
                this.$router.push('/Main/orgEdit?'+this.$store.state.orgId);
                this.authState = false;
                this.addTab('机构信息');
            },
            changePwd(){
                const that = this;
                const __url = that.$store.state.total.set.child.childChangePwd[1];
                if(that.change.newPwd === ''){
                    that.$message.warning('输入密码不能为空');
                    return false;
                }
                if(that.change.repeatPwd === ''){
                    that.$message.warning('请再次输入密码');
                    return false;
                }
                if( that.change.repeatPwd.length > 18 || that.change.newPwd.length > 18 || that.change.repeatPwd.length < 6 || that.change.newPwd.length < 6 ){
                    that.$message.warning('密码长度为 6-18 位');
                    return false;
                }
                if( that.change.newPwd !== that.change.repeatPwd ){
                    that.$message.warning('两次密码输入不一致');
                    return false;
                }
                if(__url==='') return false;
                that.tinyAjax = true;
                that.$ajax({
                    url:__url,
                    params:{
                        u_id:that.$store.state.userId,
                        oldpwd:that.change.oldPwd,
                        newpwd:that.change.newPwd,
                    }
                }).then(res=>{
                    that.tinyAjax = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.pwdState =false;
                        that.$notify.success({
                            title: '修改成功',
                            message:'请妥善保管你的密码',
                            duration: 0,
                            offset:80
                        });
                        document.cookie = 'state=1';
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    } else {
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.tinyAjax = false;
                    that.pwdState =false;
                    that.$message.warning('网络似乎出了点问题');
                });

            },
            toBindYunBiSai(){
                this.addTab('绑定云比赛');
                this.$router.push('/Main/setting3');
            },
            getLocation(){
              this.$ajax({
                  url:'http://int.dpool.sina.com.cn/iplookup/iplookup.php'
              }).then();
            },
            Pay(type,orderID,url){
                const that = this;
                let pay_url = '';
                if(type==='ali') pay_url = that.$store.state.pay.ali;
                else if(type==='wx') pay_url = that.$store.state.pay.wx;
                window.location.href =  that.$store.state.baseURL + pay_url + '?orderid=' + orderID + '&type=1&return_url=http://' + window.location.host + '/#' + url ;
            },
            toPassword(){
                this.$router.push('/Main/setting1');
                this.addTab('权限设置');
                this.infoVisible = false;
            },
            toOrgEdit(){
                this.$router.push('/Main/orgEdit?'+this.$store.state.orgId);
                this.addTab('机构信息');
                this.infoVisible = false;
            },
            showTime(time){
                const f = (a)=>{return a<10?'0'+a:a};
                time = new Date(time);
                let y = time.getFullYear(),
                    M = time.getMonth()+1,
                    d = time.getDate(),
                    h = time.getHours(),
                    m = time.getMinutes(),
                    s = time.getSeconds();
                return y+'年'+f(M)+'月'+f(d)+'日 '+f(h)+':'+f(m)+':'+f(s);
            },
            overTime(){
                this.$message.warning('未登录或登录已过期');
                this.$router.push('/login');
            },
            getType(num){
                if(num==='1') return '一级（省级）';
                else if(num==='2') return '二级（地市级）';
                else if(num==='3') return '三级（县区级）';
                else if(num==='4') return '四级（培训学校）';
            },
            showPic(src){
                if(src===null) return '';
                const that = this;
                const arr = src.split(',');
                let content = '';
                arr.forEach((e,i)=>{
                     content += '<img src="'+that.$store.state.imgURL+e+'" alt="'+i+'"/>';
                });
                return content;
            },
            showUserInfo(){
                const that = this;
                const __url = that.$store.state.total.org.child.orgDetail[1];
                if(__url==='') return false;
                that.infoVisible = true;
                that.$ajax({
                    url:__url
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        const area = that.cityQuery(dat.county,'county','get');
                        that.userInfo = [
                            {title:'登录账号',detail:that.homeInfo.userName},
                            {title:'机构类型',detail:that.getType(dat.org_level)},
                            {title:'法人姓名',detail:dat.legal_name},
                            {title:'联系人姓名',detail:dat.link_name},
                            {title:'联系人手机号',detail:dat.link_phone},
                            {title:'地址',detail:area[0]+area[1]+area[2] + dat.address === null ? '' : dat.address},
//                            {title:'租赁合同',detail:that.showPic(dat.lease_img)},
//                            {title:'棋场照片',detail:that.showPic(dat.place_img)},
//                            {title:'工商执照',detail:that.showPic(dat.license_img)}
                        ];
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.overTime();
                    } else {
                        that.$message.warning(data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            setCookie(arr){
                arr.forEach((e,i)=>{
                    document.cookie = e.name +'='+escape(e.val);
                });
            },
            getCookie(name){
                let res='';
                document.cookie.split('; ').forEach((e,i)=>{
                    const arr = e.split('=');
                    if(arr[0]===name){
                        res = unescape(arr[1]);
                        return false;
                    }
                });
                return res;
            },
            delCookie(name){
                const date = new Date();
                date.setTime(date.getTime() - 10000);
                document.cookie = name + '=vvv;expires=' + date.toGMTString();
            },
            getHomeInfo(){
                const that = this;
                const __url = that.$store.state.total.home.child.homeInfo[1];
                if(__url==='') return false;
                that.bodyLoading = true;
                //获取用户信息
                that.$ajax({
                    method:'get',
                    url:__url
                }).then((res)=>{
                    that.bodyLoading = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.orgName = dat.org_name;
                        that.userName = dat.user_name === dat.org_name ? '主管理员' : dat.user_name;
                        that.msgNum = dat.notices_num;
                        that.homeInfo = {
                            orgName:dat.org_name,
                            userName:dat.user_name,
                            memberNum:dat.mem_num,
                            yesterdayMemberNum:dat.yes_mem_num,
                            weekMemberNum:dat.week_mem_num,
                            orgNum:dat.org_num,
                            yesterdayOrgNum:dat.yes_org_num,
                            weekOrgNum:dat.week_org_num,
                            eventNum:dat.event_num,
                            yesterdayEventNum:0,
                            yearEvent:dat.event_num_year,
                            monthEvent:dat.event_num_month,
                            weekEventNum:0,
                            sysInfo:dat.notices,
                            orgId:dat.org_id,
                            userId:dat.user_id
                        };
                        that.systemTime = dat.sys_time;

                        // 列表判断  级别
                        if(dat.org_level==='1'){
                            /*that.Modules.credential.children = [
                                {name: '段级位名单', path: '/Main/rankingList'},
                            ];*/
                            that.Modules.event.children.forEach((e,i)=>{
                                if(e.name==='我的赛事'){
                                    that.Modules.event.children.splice(i,1);
                                }
                            });
                        }else if(dat.org_level==='4'){
                            that.$store.state.total.org.auth = false;
                        }
                        // 非省级去除费用设置权限
                        if(dat.org_level!=='1'){
                            that.Modules.authority.children.forEach((e,i)=>{
                                if(e.name==='费用设置'){
                                    that.Modules.authority.children.splice(i,1);
                                }
                            });
                        }

                        // 数量统计
                        const __obj = {
                            all:[that.homeInfo.orgNum,that.homeInfo.memberNum,that.homeInfo.eventNum],
                            yesterday:[that.homeInfo.yesterdayOrgNum,that.homeInfo.yesterdayMemberNum,that.homeInfo.eventNum],
                            week:[that.homeInfo.weekOrgNum,that.homeInfo.weekMemberNum,that.homeInfo.eventNum],
                        };

                        // 存信息

                        that.$store.commit('setInfo',{
                            orgId:that.homeInfo.orgId,
                            bindState:dat.lswl_org_id===null?0:1,
                            orgLevel:(dat.org_level>>0),
                            orgName:that.homeInfo.orgName,
                            pro:dat.province,
                            sysTime:that.systemTime,
                            userId:that.homeInfo.userId,
                            printAuth:dat.auth_state.print_pre, // 打印权限
                        });
                        // 判断是否上传了图片
                        const __state = dat.auth_state;
                        if( __state.lease_img==='' || __state.lease_img === null || __state.place_img === '' || __state.place_img === null || __state.license_img === '' || __state.license_img === null){
                            that.authState = true;
                        }
                        // 刷时间
                        that.$store.commit('updateTime');
                        that.$store.commit('setNumber',__obj);
                        that.$store.commit('setSysInfo',that.homeInfo.sysInfo);
                        that.$store.commit('setYearEvent',that.homeInfo.yearEvent);
                        that.$store.commit('setMonthEvent',that.homeInfo.monthEvent);
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.overTime();
                    } else {
                        that.$message.warning(_data.msg);
                    }
                },(res)=>{
                    that.bodyLoading = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            addTab(target, app){
                let tabName;
                let that = app || this;
                let same =  false;
                typeof target === 'string' ? (tabName = target.replace(/[\s×]/g,'')) : (tabName = target.$el.innerText.replace(/[\s×]/g,''));
                let open = that.editableTabs;
                open.forEach((e,i)=>{
                    e.active = false;
                    if(e.name === tabName.replace(/[\s×]/g,'')){
                        e.active = true;
                        same = true;
                    }
                });
                if(same) return false;
                if (open.length > 5) {
                    that.editableTabs.splice(1,1);
                }
                that.editableTabs.push({
                    name:tabName.replace(/[\s×]/g,''),
                    active:true,
                    closeable:true
                });
            },
            removeTab(event){
                const _text = (event.target.parentNode.innerText.replace(/[\s×]/g,''));
                let that = this;
                let tabs = that.editableTabs;
                tabs.forEach((e,i)=>{
                    if(e.name===_text){
                        if(i===tabs.length-1||e.active){
                            tabs[i-1].active=true;
                            that.routeGo(tabs[i-1].name,tabs[i-1].name);
                        }
                        tabs.splice(i,1);
                    }
                });
            },
            searchRoute(event,name){
                const nameArr = ['首页','机构管理','棋手注册','添加机构','消息','添加机构','授权赛事','预发布赛事','申请赛事','赛事申请','编辑赛事','机构信息','批量导入棋手','转会申请','待确认名单','段级位名单','证书列表'],

                    pathArr = ['/Main','/Main/org','/Main/addPlayer','/Main/OrgAdd','/Main/more','/Main/OrgAdd','/Main/planApply','/Main/planAdd','/Main/applyFor','/Main/eventApply','/Main/planEdit','/Main/OrgEdit','/Main/importPlayer','/Main/transferPlayer','/Main/wait','/Main/rankingList','/Main/creList'],
                      eventName = typeof event === 'string' ? event.replace(/[\s×]/g,'') : event.target.innerText.replace(/[\s×]/g,'');
                let flag = false;
                this.editableTabs.forEach((e,i)=>{
                    e.active = eventName.trim()===this.editableTabs[i].name.trim();
                });
                nameArr.forEach((e,i)=>{
                   if(eventName === e){
                       // planEdit（全部） planApply（省级） applyFor（下级） 会有特殊处理
                       this.$router.push(pathArr[i]);
                       return false;
                   }
                   if(i>=nameArr.length-1){
                       flag = true;
                   }
                });
                if(flag){
                    let _list = this.Modules;
                    for (let i in _list) {
                        let _inner = _list[i].children;
                        for (let j in _inner) {
                            if (_inner[j].name === name) {
                                this.$router.push(_inner[j].path);
                            }
                        }
                    }
                }
            },
            routeGo(event,name){
                this.searchRoute(event,name.replace(/[\s×]/g,''));
            },
            more(){
                this.msgNum = 0;
                this.addTab('消息');
                this.$router.push('/Main/more');
            },
            exit(){
                const that =this;
                document.cookie.split('; ').forEach((e,i)=>{
                    const arr = e.split('=');
                    that.delCookie(arr[0]);
                });
                this.$router.push('');
                const host = window.location.host;
                window.location.href = 'http://' + host;
            },
            getToken(){
                let getCookie = (name)=>{
                    let arrCookie = document.cookie.split('; ');
                    for(let i = 0; i < arrCookie.length; i++){
                        let arr = arrCookie[i].split('=');
                        if( arr[0] === name ) return arr[1];
                    }
                    return '';
                };
                return getCookie('token');
            },
            refreshToken(){
                const that = this;
                const __url = that.$store.state.refreshToken[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url
                }).then( res => {
                    const _data = res.data.token;
                    document.cookie = 'token='+_data;
                    that.$ajax.defaults.headers={token:_data};
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            openTab(href){
                const that = this;
                that.allPath.forEach((e,i)=>{
                    if(href.includes(e[0])){
                        that.addTab(e[1]);
                        return false;
                    }
                });
            },
