                        console.log('位数不合规则');
                        return [];
                    }
                }
                //获得省市县
                function getArea(k,st){
                    let t;
                    if(k===''){
                        t=[];
                        switch (st){
                            case '11':case '31':
                            for(let i in obj){
                                t.push([i,format(obj[i].k)]);
                            }
                            break;
                            case '12':case '32':
                            for(let i in obj){
                                for(let j in obj[i].c){
                                    t.push([j,format(obj[i].c[j].k)]);
                                }
                            }
                            break;
                            case '13':case '33':
                            for(let i in obj){
                                for(let j in obj[i].c){
                                    for(let m in obj[i].c[j].c){
                                        t.push([m,format(obj[i].c[j].c[m].k)]);
                                    }
                                }
                            }
                            break;
                        }
                    }else if(k>>0){
                        t=[];
                        t=cut(k);
                    }else if(k!==''){
                        t=[];
                        switch(st){
                            case '11':case '31':
                            for(let i in obj){
                                if(match(k,obj[i].k)){
                                    t.push([i,format(obj[i].k)]);
                                }
                            }
                            break;
                            case '12':case '32':
                            for(let i in obj){
                                for(let j in obj[i].c){
                                    if(match(k,obj[i].c[j].k)){
                                        t.push([j,format(obj[i].c[j].k)]);
                                    }
                                }
                            }
                            for(let p=0;p<t.length;p++){
                                for(let q=0;q<change.length;q++){
                                    if((t[p][0]>>0)===change[q].n){
                                        t[p][0] = t[p][0]+','+change[q].o;
                                    }
                                }
                            }
                            break;
                            case '13':case '33':
                            for(let i in obj){
                                for(let j in obj[i].c){
                                    for(let m in obj[i].c[j].c){
                                        if(match(k,obj[i].c[j].c[m].k)){
                                            t.push([m,format(obj[i].c[j].c[m].k)]);
                                        }
                                    }
                                }
                            }
                            for(let p=0;p<t.length;p++){
                                for(let q=0;q<change.length;q++){
                                    if(((t[p][0].substring(0,4))>>0)===change[q].n){
                                        t[p][0] = t[p][0]+','+change[q].o+t[p][0].substring(4,6);
                                    }
                                }
                            }
                            break;
                        }
                    }
                    return t;
                }
                //获得下一级
                function searchArea(k,st){
                    if(k.indexOf(',')!==-1){
                        k=k.substring(0,4);
                    }
                    if(k===''||k===' '){
                        return getArea('',1+st.substring(0,1));
                    }
                    let t = [];
                    if(st==='21'||st==='41'){
                        for(let i in obj){
                            if(match(k,obj[i].k)||i===k){
                                if(st!=='41'){
                                    t.push([i,format(obj[i].k)]);
                                }
                                for(let j in obj[i].c){
                                    t.push([j,format(obj[i].c[j].k)]);
                                }
                            }
                        }
                        for(let p=0;p<t.length;p++){
                            for(let q=0;q<change.length;q++){
                                if((t[p][0]>>0)===change[q].n){
                                    t[p][0] = t[p][0]+','+change[q].o;
                                    break;
                                }
                            }
                        }
                    }
                    if(st==='22'||st==='42'){
                        for(let i in obj){
                            for(let j in obj[i].c){
                                if(match(k,obj[i].c[j].k)||(j)===k){
                                    if(st!=='42'){
                                        t.push([j,format(obj[i].c[j].k)]);
                                    }
                                    for(let m in obj[i].c[j].c){
                                        t.push([m,format(obj[i].c[j].c[m].k)]);
                                    }
                                }
                            }
                        }
                        for(let p=0;p<t.length;p++){
                            for(let q=0;q<change.length;q++){
                                if((t[p][0]>>0)===change[q].n){
                                    t[p][0] = t[p][0]+','+change[q].o;
                                    break;
                                }
                                if(((t[p][0].substring(0,4))>>0)===change[q].n){
                                    t[p][0] = t[p][0]+','+change[q].o+t[p][0].substring(4,6);
                                }
                            }
                        }
                    }
                    if(st==='23'||st==='43'){
                        for(let i in obj){
                            for(let j in obj[i].c){
                                for(let m in obj[i].c[j].c){
                                    if(match(k,obj[i].c[j].c[m].k)||(m)===k){
                                        t.push([m,format(obj[i].c[j].c[m].k)]);
                                    }
                                }
                            }
                        }
                    }
                    return t;
                }
            }
        },
        created(){
            const that = this;
            that.$parent.readAuth(()=>{
                that.getHomeInfo();
            });
            if(window.location.href.toLowerCase().includes('orgadd')||window.location.href.toLowerCase().includes('orgedit')){
                that.$router.push('/Main/org');
                that.addTab('机构管理');
            } else {
                that.openTab(window.location.href);
            }
            that.pwdState = that.getCookie('state')==='0';

            that.SystemName = document.getElementsByTagName('title')[0].innerText.trim();
        }
    }
</script>
